<template>

    <form class="GrantPage" @submit.prevent="submit">

        <file-catcher @catch="catchFiles" ref="fileCatcher" />

        <div class="ProfilePage__avatar-selector">
            <logo class="ProfilePage__avatar" :params="avatar" @click="avatarClick" size="large" />
            <secondary-button small @click="avatarClick" :loading="uploadingPhoto">Выбрать фотографию</secondary-button>
        </div>

        <div class="GrantPage__fields">
            <component v-for="fieldComponent in fieldComponents"
                       :is="fieldComponent.component"
                       :key="fieldComponent.data.field"
                       :field="fieldComponent.data.field"
                       :label="fieldComponent.data.label"
                       :description="fieldComponent.data.description"
                       :default="fieldComponent.data.default"
                       :optional="fieldComponent.data.optional"
                       :object="object"
                       @input="input(fieldComponent.data.field, $event)"
                       ref="components"
            />
        </div>

        <div class="GrantPage__actions">
            <primary-button class="GrantPage__actions-grant" type="submit" :loading="loading">Предоставить</primary-button>
            <secondary-button class="GrantPage__actions-cancel" type="button" @click="cancel">Отказаться</secondary-button>
        </div>
    </form>

</template>

<style>

    .GrantPage {
        max-width: 350px;
        flex-grow: 2;
    }

    .GrantPage__grant {
        display: flex;
        align-items: center;
        justify-content: center;

        margin-bottom: 40px;
    }

    .GrantPage__description {
        text-align: center;

        margin-bottom: 40px;
    }

    .GrantPage__fields {
        margin-bottom: 60px;
    }

    .GrantPage__actions {
        text-align: center;
    }

</style>

<script>

    import store from "@/store";
    import Logo from "@/modules/common/components/ui/Logo";
    import UserFieldRow from "@/modules/auth/pages/grantPage/UserFieldRow";
    import PrimaryButton from "@/modules/common/components/ui/PrimaryButton";
    import SecondaryButton from "@/modules/common/components/ui/SecondaryButton";
    import FileCatcher from "@/modules/common/components/ui/FileCatcher";
    import axios from "axios";
    import config from "@/config";
    import fileStore from "../../../fileStore";

    export default {

        components: {
            Logo,
            PrimaryButton,
            SecondaryButton,
            FileCatcher,
        },

        props: {
            fields: {
                type: Array,
                required: true,
            },
        },

        data() {
            return {
                avatarBgColor: '#CCC',
                avatarImage: store.requestedAuth.avatar
                    ? store.requestedAuth.avatar.image
                    : '',
                object: {},

                uploadingPhoto: false,

                loading: false,
            }
        },

        computed: {
            auth() {
                return store.requestedAuth
            },

            avatar() {
                return {
                    text: this.initials,
                    bgColor: this.avatarBgColor,
                    image: this.avatarImage,
                }
            },

            uploadUrl() {
                return config.fileStorage.url + '/fileservice/widget/upload?fullAnswer=true&accountId=chatium'
            },

            account() {
                return store.requestedAccount
            },

            accountFirstChar() {
                return this.account.name.slice(0, 1)
            },

            grantFromLogo() {
                const logo = { ...this.auth.avatar }

                if (!logo.image && !logo.text) {
                    if (this.auth.firstName || this.auth.lastName) {
                        const text =
                            (this.auth.firstName ? this.auth.firstName : '') +
                            (this.auth.lastName ? this.auth.lastName : '')

                        logo.text = text.slice(0, 1)
                    } else {
                        logo.text = '?'
                    }
                }

                return logo
            },

            grantToLogo() {
                return {
                    text: this.accountFirstChar.toUpperCase(),
                    bgColor: '#ec1b5d',
                    color: '#ffffff',
                }
            },

            fieldComponents() {
                return this.fields.map(data => {
                    let component = UserFieldRow

                    return {
                        component,
                        data,
                    }
                })
            },
        },

        methods: {
            input(field, value) {
                this.object = { ...this.object, [field]: value }
            },

            async catchFiles(files) {
                this.uploadingPhoto = true;
                try {
                    const file = files.shift()

                    if (!file.type.includes('image/')) {
                        return false
                    }

                    const formData = new FormData();
                    formData.append('Filedata', file);

                    const response = await axios.post(
                        this.uploadUrl,
                        formData,
                        { headers: { 'Content-Type': 'multipart/form-data' } }
                    );

                    this.avatarImage = fileStore.normalSquare(response.data.hash)
                } finally {
                    this.uploadingPhoto = false;
                }
            },

            avatarClick() {
                this.$refs.fileCatcher.selectFiles()
            },

            async submit() {
                this.loading = true
                try {
                    // Валидируем все строки
                    const validations = await Promise.all(
                        this.$refs.components.map(component => component.validate())
                    )

                    // Ищем, есть ли невалидные?
                    const invalid = validations.find(validation => validation === false)
                    if (invalid) {
                        return
                    }

                    // Подготавливаем словарь для сохранения
                    const data = this.$refs.components.reduce((result, component) => {
                        result[component.getField()] = component.getValue()
                        return result
                    }, {})

                    if (this.avatarImage) {
                        data.avatar = {
                            image: this.avatarImage
                        }
                    }

                    const response = await this.$api.post('user/profile', data)

                    if (response.success) {
                        this.$emit('granted')
                    }
                } finally {
                    this.loading = false
                }
            },

            cancel() {
                this.$emit('cancel')
            },
        },

        mounted() {
            this.$refs.components[0].focus()
        },

    }

</script>
