import Vue from "vue";
import { Api } from "@/api/console";
import router from "@/router/console";
import store from "@/store";

import VueKonva from "vue-konva";
import PortalVue from "portal-vue";

import "@fortawesome/fontawesome-free/css/all.css";

import PresenterLayout from "@/modules/common/components/presenter/PresenterLayout";
import BlockList from "@/modules/common/components/presenter/blocks/BlockList";

Vue.config.productionTip = false;

Vue.use(Api);
Vue.use(VueKonva);
Vue.use(PortalVue);

store.host = location.hostname

Vue.component("presenter-layout", PresenterLayout);
Vue.component("block-list", BlockList);

new Vue({
  router,
  render: h => h(Vue.component("RouterView"))
}).$mount("#application");
