<template>

    <div :id="message.id"
         class="ChatMessage"
         :class="{ ['__type-' + type]: true, '__bubble': bubble, '__same-author': message.sameAuthor, '__another-author': !message.sameAuthor, '__outgoing': message.isOutgoing }"
         :style="styles"
         @mouseleave="mouseleave"
         @contextmenu="$emit('contextMenu', $event)"
    >
        <div class="ChatMessage__wrapper">
            <div v-if="type === 'message'" class="ChatMessage__left">
                <message-avatar class="ChatMessage__author-photo" :avatar="message.author.avatar" :clickable="message.author && !!message.author.onClick" @click="authorClick" />
            </div>
            <div class="ChatMessage__right" @dblclick="reply">
                <div class="ChatMessage__right-wrapper">
                    <div v-if="message.author && type === 'message'" class="ChatMessage__author-name" :class="{ '__clickable': !!message.author.onClick }" @click="authorClick">{{ message.author.name }}</div>
                    <div v-if="message.replyTo" class="ChatMessage__reply" :class="{ '__with-image': !!replyMessageImage }" @click="replyClick">
                        <div v-if="replyMessageImage" class="ChatMessage__reply-image" :style="{ backgroundImage: 'url(' + replyMessageImage.thumbnail_url_400 + ')' }"></div>
                        <div class="ChatMessage__reply-author-name">{{ message.replyTo.author.name }}</div>
                        <div class="ChatMessage__reply-text" v-html="replyMessageText"></div>
                    </div>
                    <div v-if="message.textEscaped" class="ChatMessage__text" :class="{ __emoji: emoji }" v-html="message.textEscaped" @click="messageClick"></div>
                    <chat-message-files v-if="message.files && message.files.length > 0" class="ChatMessage__files" :files="message.files" />
                    <div v-if="reactions.length" class="ChatMessage__reactions">
                        <div v-for="reaction in reactions"
                             class="ChatMessage__reactions-reaction"
                             :class="{ __self: reaction.self }"
                             @click.stop="$emit('react', { reaction: reaction.char, message, action: reaction.self ? 'unset' : 'set' })"
                             @dblclick.stop
                        >
                            <span class="ChatMessage__reactions-reaction-char">{{ reaction.char }}</span>
                            <span class="ChatMessage__reactions-reaction-count">{{ reaction.count }}</span>
                        </div>
                    </div>
                    <block-list v-if="message.blocks"
                                :blocks="message.blocks"
                                @navigate="$emit('navigate', $event)"
                    />
                </div>
                <div v-if="type === 'message'" class="ChatMessage__date">
                    <div v-if="message.createdAt !== message.updatedAt" class="ChatMessage__date-edited">изменено</div>
                    <div v-if="message.__local === true">
                        <svg class="ChatMessage__date-saving" width="12" height="12" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd" viewBox="0 0 24 24" fill="gray">
                            <path d="M12 0c6.623 0 12 5.377 12 12s-5.377 12-12 12-12-5.377-12-12 5.377-12 12-12zm0 1c6.071 0 11 4.929 11 11s-4.929 11-11 11-11-4.929-11-11 4.929-11 11-11zm0 11h6v1h-7v-9h1v8z"/>
                        </svg>
                    </div>
                    <div v-else-if="message.isOutgoing" class="ChatMessage__date-readed" :class="{ __confirmed: message.readed }">
                        <svg class="ChatMessage__date-readed-one" width="12" height="12" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd" viewBox="0 0 24 24" fill="green">
                            <path d="M21 6.285l-11.16 12.733-6.84-6.018 1.319-1.49 5.341 4.686 9.865-11.196 1.475 1.285z"/>
                        </svg>
                        <svg class="ChatMessage__date-readed-two" width="12" height="12" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd" viewBox="0 0 24 24" fill="green">
                            <path d="M21 6.285l-11.16 12.733-6.84-6.018 1.319-1.49 5.341 4.686 9.865-11.196 1.475 1.285z"/>
                        </svg>
                    </div>
                    <div class="ChatMessage__date-date">{{ humanDate }}</div>
                </div>
            </div>
        </div>

        <div v-if="false && type !== 'system'" class="ChatMessage__actions">
            <div v-if="confirmation" class="ChatMessage__action-confirmation">Вы уверены?</div>
            <template v-if="!confirmation && showReactions">
                <div class="ChatMessage__action" @click="$emit('react', { reaction: '👍', message, action: 'set' })">👍</div>
                <div class="ChatMessage__action" @click="$emit('react', { reaction: '👎', message, action: 'set' })">👎</div>
                <div class="ChatMessage__action" @click="$emit('react', { reaction: '✅', message, action: 'set' })">✅</div>
                <div class="ChatMessage__action" @click="$emit('react', { reaction: '💩', message, action: 'set' })">💩</div>
                <div class="ChatMessage__action" @click="$emit('react', { reaction: '🍺', message, action: 'set' })">🍺</div>
                <div class="ChatMessage__action" @click="$emit('react', { reaction: '🍷', message, action: 'set' })">🍷</div>
                <div class="ChatMessage__action" @click="$emit('react', { reaction: '👿', message, action: 'set' })">👿</div>
                <div class="ChatMessage__action" @click="$emit('react', { reaction: '😧', message, action: 'set' })">😧</div>
                <div class="ChatMessage__action" @click="$emit('react', { reaction: '😂', message, action: 'set' })">😂</div>
            </template>

            <div class="ChatMessage__action" @click="toggleReactions">
                <svg width="14" height="14" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill-rule="evenodd" clip-rule="evenodd" fill="currentColor">
                    <path d="M12 2c5.514 0 10 4.486 10 10s-4.486 10-10 10-10-4.486-10-10 4.486-10 10-10zm0-2c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm6 14h-12c.331 1.465 2.827 4 6.001 4 3.134 0 5.666-2.521 5.999-4zm-9.5-6c-.828 0-1.5.671-1.5 1.5s.672 1.5 1.5 1.5 1.5-.671 1.5-1.5-.672-1.5-1.5-1.5zm7 0c-.828 0-1.5.671-1.5 1.5s.672 1.5 1.5 1.5 1.5-.671 1.5-1.5-.672-1.5-1.5-1.5z"/>
                </svg>
            </div>

            <div v-if="canPin && !confirmation" class="ChatMessage__action" @click="$emit('pin', message)">
                <svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" viewBox="0 0 24 24" fill="currentColor">
                    <path d="M11 17h2v5l-2 2v-7zm3.571-12c0-2.903 2.36-3.089 2.429-5h-10c.068 1.911 2.429 2.097 2.429 5 0 3.771-3.429 3.291-3.429 10h12c0-6.709-3.429-6.229-3.429-10z"/>
                </svg>
            </div>
            <div v-if="message.canEdit && !confirmation" class="ChatMessage__action" @click="$emit('edit', message)">
                <svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" viewBox="0 0 24 24" fill="currentColor">
                    <path d="M7.127 22.564l-7.126 1.436 1.438-7.125 5.688 5.689zm-4.274-7.104l5.688 5.689 15.46-15.46-5.689-5.689-15.459 15.46z"/>
                </svg>
            </div>
            <div v-if="message.canEdit" class="ChatMessage__action" :class="{ __confirmation: confirmation }" @click="() => { !confirmation ? confirmation = true : $emit('delete', message) }">
                <svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" viewBox="0 0 24 24" fill="currentColor">
                    <path d="M19 24h-14c-1.104 0-2-.896-2-2v-16h18v16c0 1.104-.896 2-2 2zm-7-10.414l3.293-3.293 1.414 1.414-3.293 3.293 3.293 3.293-1.414 1.414-3.293-3.293-3.293 3.293-1.414-1.414 3.293-3.293-3.293-3.293 1.414-1.414 3.293 3.293zm10-8.586h-20v-2h6v-1.5c0-.827.673-1.5 1.5-1.5h5c.825 0 1.5.671 1.5 1.5v1.5h6v2zm-8-3h-4v1h4v-1z"/>
                </svg>
            </div>
        </div>

        <div v-if="lastRead" class="ChatMessage__last-read">
            Новые сообщения
        </div>
    </div>

</template>

<style>

    .ChatMessage {
        position: relative;

        font-size: 14px;
        line-height: 20px;
    }

    .ChatMessage__wrapper {
        display: flex;
        padding: 7px 20px;
    }

    .ChatMessage.__another-author {
        margin-top: 15px;
    }

    .ChatMessage:hover {
        background-color: rgba(0, 0, 0, 0.025);
    }

    .ChatMessage__left {
        width: 50px;
        flex-shrink: 0;
    }

    .ChatMessage__right {
        position: relative;
        flex-grow: 2;

        padding-right: 90px;
        overflow: hidden;
    }

    .ChatMessage.__bubble {
        padding: 5px 20px;
    }

    .ChatMessage.__bubble.__another-author {
        margin-top: 10px;
    }

    .ChatMessage.__bubble .ChatMessage__right-wrapper {
        background-color: #f6f6f6;
        padding: 10px;
        border-radius: 10px;
        display: inline-block;
    }

    .ChatMessage.__bubble.__outgoing .ChatMessage__right-wrapper {
        background-color: #d5f2ff;
    }

    .ChatMessage__actions {
        display: none;
        user-select: none;

        position: absolute;
        top: 4px;
        right: 15px;
        height: 26px;
        border: 1px solid #eaeaea;
        background-color: white;
        border-radius: 5px;
    }

    .ChatMessage:hover .ChatMessage__actions {
        display: flex;
    }

    .ChatMessage__action {
        width: 30px;
        height: 24px;

        display: flex;
        align-items: center;
        justify-content: center;

        color: gray;
    }

    .ChatMessage__action:hover {
        color: black;
        background-color: #eaeaea;
        cursor: pointer;
    }

    .ChatMessage__action.__confirmation {
        color: red;
    }

    .ChatMessage__action-confirmation {
        display: flex;
        align-items: center;
        padding: 0 10px;
    }

    .ChatMessage__author-name {
        font-size: 13px;
        font-weight: bold;
        line-height: 18px;
        border-radius: 5px;
    }

    .ChatMessage__author-name.__clickable {
        cursor: pointer;
    }

    .ChatMessage__author-photo {
        height: 38px;
        width: 38px;
        border-radius: 38px;
        background-size: cover;
    }

    .ChatMessage__reply {
        color: gray;
        border-left: 2px solid silver;
        padding-left: 14px;

        margin-bottom: 4px;

        cursor: pointer;

        font-size: 13px;
        line-height: 18px;
    }

    .ChatMessage__reply.__with-image {
        padding-left: 50px;
    }

    .ChatMessage__reply-author-name {
        font-weight: bold;
    }

    .ChatMessage__reply-text {
        max-height: 18px;
        overflow: hidden;
        text-overflow: ellipsis;
        line-height: 18px;
    }

    .ChatMessage__reply-image {
        position: absolute;
        left: 10px;
        width: 35px;
        height: 35px;
        border: 1px solid silver;
        border-radius: 3px;
        background-size: cover;
        background-repeat: no-repeat;
    }

    .ChatMessage__text {
        word-break: break-word;
        max-width: 800px;
        white-space: pre-line;
    }

    .ChatMessage__text.__emoji {
        font-size: 72px;
        letter-spacing: 10px;
        line-height: 92px;
    }

    .ChatMessage__text a {
        color: #2988ff;
    }

    .ChatMessage__text code {
        font-family: Menlo,Monaco,'Consolas','Courier New',monospace;
    }

    .ChatMessage__text .multiline-code {
        padding: 10px;
        border-radius: 5px;
        margin: 3px 0;
        background-color: #eaeaea;
        font-size: 12px;
        overflow-x: auto;
    }

    .ChatMessage__text .inline-code {
        color: darkred;
        cursor: pointer;
    }

    .ChatMessage__date {
        position: absolute;
        right: 0;
        top: 0;

        color: #999999;
        font-size: 12px;
        -webkit-font-smoothing: antialiased;

        display: flex;
        flex-wrap: nowrap;
    }

    .ChatMessage__date-edited {
        margin-right: 8px;
    }

    .ChatMessage__edit {
        color: #b9b9b9;
        -webkit-font-smoothing: antialiased;

        cursor: pointer;

        display: none;
        padding-left: 5px;
    }

    .ChatMessage .ChatMessage__edit {
        display: inline-block;
    }

    .__another-author .ChatMessage__edit {
        top: 0;
    }

    .ChatMessage__files {
        margin-top: 5px;
    }

    .ChatMessage__reactions {
        margin-top: 5px;
        user-select: none;
        cursor: pointer;
    }

    .ChatMessage__reactions-reaction {
        display: inline-block;

        padding: 0 8px;
        border: 1px solid #eaeaea;
        border-radius: 3px;

        margin-right: 5px;
    }

    .ChatMessage__reactions-reaction-count {
        margin-left: 5px;
        font-weight: normal;
    }

    .ChatMessage__reactions-reaction.__self {
        border: 1px solid #cbedfd;
        background-color: #eefaff;
    }

    .ChatMessage__date-readed {
        margin-right: 5px;
        position: relative;
        top: 2px;
        width: 16px;
    }

    .ChatMessage__date-readed-one {
        position: absolute;
        top: 2px;
        left: 0;
    }

    .ChatMessage__date-readed-two {
        display: none;
        position: absolute;
        top: 2px;
        left: 3px;
    }

    .ChatMessage__date-saving {
        position: relative;
        top: 2px;
        margin-right: 4px;
    }

    .ChatMessage__date-readed.__confirmed .ChatMessage__date-readed-two {
        display: block;
    }

    .ChatMessage.__same-author .ChatMessage__author-name,
    .ChatMessage.__same-author .ChatMessage__author-photo {
        display: none;
        top: 2px;
    }

    .ChatMessage.__same-author.__outgoing .ChatMessage__date,
    .ChatMessage.__same-author:hover .ChatMessage__date {
        display: flex;
        padding: 5px;
        margin: -5px;
    }

    .ChatMessage.__type-system {
        color: gray;
        font-size: 13px;
        font-weight: bold;
        text-align: center;
    }

    .ChatMessage.__type-system .ChatMessage__right,
    .ChatMessage.__type-blocks .ChatMessage__right {
        padding-right: 0;
    }

    .ChatMessage.__type-blocks .ChatMessage__wrapper {
        padding: 0;
    }

    .ChatMessage.__type-blocks {
        margin-top: 10px;
    }

    .ChatMessage.__type-blocks .ChatMessage__right-wrapper > .BlockList > .BlockList {
        border-radius: 5px;
    }

    .ChatMessage:hover {
        background-color: transparent;
    }

    .ChatMessage.__highlighted {
        animation: ChatMessage__highlighted 1s;
    }

    @keyframes ChatMessage__highlighted {
        0% {
            background-color: transparent;
        }
        50% {
            background-color: #adddff;
        }
        100% {
            background-color: transparent;
        }
    }

    .ChatMessage__last-read {
        display: flex;
        height: 30px;
        align-items: center;
        justify-content: center;
        background-color: floralwhite;
        border-top: 1px solid #f0f0f0;
        border-bottom: 1px solid #f0f0f0;
    }

    @media screen and (max-width: 900px) {

        .ChatMessage__wrapper {
            padding-right: 5px;
        }

        .ChatMessage__right {
            padding-right: 25px;
        }

        .ChatMessage__date-date {
            display: none;
        }
    }

    @media
    only screen and (-webkit-min-device-pixel-ratio: 1.25),
    only screen and ( min--moz-device-pixel-ratio: 1.25),
    only screen and ( -o-min-device-pixel-ratio: 1.25/1),
    only screen and ( min-device-pixel-ratio: 1.25),
    only screen and ( min-resolution: 200dpi),
    only screen and ( min-resolution: 1.25dppx)
    {
        .ChatMessage {
            font-weight: normal;
        }
    }

</style>

<script>

    import datetime from '@/services/datetime';
    import ChatMessageFiles from './chatMessage/ChatMessageFiles';
    import MessageAvatar from '@/modules/chat/components/chatMessage/MessageAvatar';

    export default {

        components: {
            ChatMessageFiles,
            MessageAvatar,
        },

        props: {
            message: {
                type: Object,
                required: true,
            },

            lastRead: {
                type: Boolean,
                default: false,
            },

            user: {
                type: [Object, null],
            },

            canPin: {
                type: Boolean,
                default: false,
            },

            bubble: {
                type: Boolean,
                default: false,
            },
        },

        data() {
            return {
                confirmation: false,
                showReactions: false,
            };
        },

        computed: {
            humanDate() {
                return datetime.formatTime(this.message.createdAt);
            },

            emoji() {
                return /^([\uD800-\uDBFF][\uDC00-\uDFFF])+$/.test(this.message.text)
            },

            replyMessageImage() {
                return this.message.replyTo ? this.message.replyTo.files.find((file) => /image/.test(file.meta.mime)) : null;
            },

            replyMessageText() {
                if (this.message.replyTo) {
                    let text = this.message.replyTo.textEscaped;

                    if (text === '') {
                        text = this.replyMessageImage ? '(фотография)' : '(файл)';
                    }

                    return text;
                } else {
                    return '';
                }
            },

            styles() {
                if (this.message.bgColor) {
                    return {
                        backgroundColor: this.message.bgColor,
                    }
                } else {
                    return {};
                }
            },

            type() {
                return this.message.type ? this.message.type.toLowerCase() : 'message'
            },

            reactions() {
                return this.message.reactions
                    ? Object.keys(this.message.reactions).map(char => ({
                            char,
                            count: this.message.reactions[char].length,
                            self: !!this.message.reactions[char].find(item => this.user && item.user_id === this.user.id),
                      }))
                    : []
            },
        },

        methods: {
            async messageClick(event) {
                if (event.target.classList.contains('inline-code')) {
                    this.$emit('navigate', {
                        onClick: {
                            type: 'copyToClipboard',
                            value: event.target.innerText,
                        },
                    })
                }
            },

            authorClick() {
                if (this.message.author && this.message.author.onClick) {
                    this.$emit('navigate', {
                        onClick: this.message.author.onClick,
                    });
                }
            },

            replyClick() {
                this.$emit('scroll-to', this.message.replyTo);
            },

            reply() {
                if (this.type === 'message') {
                    this.$emit('reply', this.message)
                }
            },

            mouseleave() {
                this.confirmation = false
                this.showReactions = false
            },

            toggleReactions(event) {
                if (this.showReactions) {
                    this.showReactions = false
                } else {
                    if (event.altKey) {
                        this.customReaction()
                    } else {
                        this.showReactions = true
                    }
                }
            },

            customReaction() {
                const emoji = prompt('Эмоджи на ваш вкус')
                if (/^([\uD800-\uDBFF][\uDC00-\uDFFF])$/.test(emoji)) {
                    this.$emit('react', { reaction: emoji, message: this.message, action: 'set' })
                }
            },
        },

    };

</script>
