<template>

    <div class="BlockList" @contextmenu="$emit('contextmenu', $event)">
        <component v-for="(block, index) in blocks"
                   :key="block.key || block.id ||index"
                   :is="getComponent(block)"
                   :block="block"
                   :small="small"
                   :selected="block.onClick ? block.onClick.url === selectedUrl : false"
                   :style="getBlockContainerStyles(block)"
                   @navigate="navigate"
                   @refresh="refresh"
                   @back="back"
                   @change="change"
                   @loaded="$emit('loaded')"
                   @contextmenu="contextMenu(block, $event)"
        />
    </div>

</template>

<script>

    import getComponent from './getComponent'
    import style from './helper/style';

    export default {

        props: {
            blocks: {
                type: Array,
                required: true,
            },

            selectedUrl: {
                type: String,
                default: null,
            },

            small: {
                type: Boolean,
                default: false,
            },

            scope: {
                type: String,
                default: 'default',
            },
        },

        methods: {
            navigate(block) {
                this.$emit('navigate', block);
            },

            getComponent(block) {
                return getComponent(block);
            },

            refresh() {
                this.$emit('refresh');
            },

            back() {
                this.$emit('back');
            },

            change(block) {
                this.$emit('change', block);
            },

            getBlockContainerStyles(block) {
                const container = style.container(block, this.scope)
                const styles = block.styles ? style.font(block.styles) : {}

                return {
                    ...container,
                    ...styles,
                }
            },

            contextMenu(block, event) {
                event.preventDefault()

                if (block.onContext) {
                    event.stopPropagation()

                    this.navigate({
                        onClick: {
                            ...block.onContext,
                            left: Math.round(event.pageX),
                            top: Math.round(event.pageY),
                        },
                    })
                }
            },
        }

    }

</script>
