<template>

    <div class="GalleryBlock__wrapper"
         @contextmenu="$emit('contextmenu', $event)"
         @click="click"
    >



        <div class="swiper-container" ref="container">
            <div class="swiper-wrapper">
                <div v-for="(slide, index) in block.slides" :key="index" class="swiper-slide">
                    <template v-if="slide.blocks" >
                        <block-list :blocks="slide.blocks"
                                    scope="gallery"
                                    @navigate="navigate"
                                    @back="back"
                                    @refresh="refresh"
                                    @change="change"
                                    @contextmenu="contextMenu"
                        />
                    </template>
                    <template v-else>
                        <component
                            :is="getComponent( slide.content )"
                            :block="slide.content"
                            :style="getBlockContainerStyles(slide.content)"
                            @navigate="navigate"
                            @refresh="refresh"
                            @back="back"
                            @change="change"
                            @contextmenu="contextMenu(slide.content, $event)"
                            />
                    </template>

                    <div class="GalleryBlock__overlay" v-if="slide.overlay">
                        <div v-if="slide.overlay.gradientColors" :style="getGradientStyles(slide.overlay.gradientColors)" class="GalleryBlock__overlay-gradients">

                        </div>
                        <block-list
                            :blocks="slide.overlay.blocks"
                            @navigate="navigate"
                            @refresh="refresh"
                            @back="back"
                            @change="change"
                        />
                    </div>
                </div>
            </div>
            <div class="swiper-pagination" ref="pagination"></div>
        </div>

    </div>

</template>

<style>

    .swiper-container {
        width: 100%;
    }

    .swiper-container {
        margin-left: auto;
        margin-right: auto;
        position: relative;
        overflow: hidden;
        list-style: none;
        padding: 0;
        z-index: 1;
    }

    .swiper-wrapper {
        position: relative;
        width: 100%;
        height: 100%;
        z-index: 1;
        display: flex;
        transition-property: transform;
        box-sizing: content-box;
    }

    .swiper-slide {
        /* Center slide text vertically */
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: block;
        position: relative;
    }

    .GalleryBlock__overlay {
        position: absolute;
        bottom: 0;
        min-height: 20px;
        z-index: 20;
        width: 100%;
    }



    .swiper-slide {
        flex-shrink: 0;
        width: 100%;
        height: 100%;
        position: relative;
        transition-property: transform;
    }


    .swiper-pagination {
        position: absolute;
        text-align: center;
        transition: .3s opacity;
        transform: translate3d(0,0,0);
        z-index: 10;
    }

    .swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
        bottom: 10px;
        left: 0;
        width: 100%;
    }

    .swiper-container-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet {
        margin: 0 10px;
    }

    .swiper-pagination-bullet {
        width: 10px;
        height: 10px;
        display: inline-block;
        border-radius: 100%;
        background-color: rgb(239, 239, 239);
    }

    .swiper-pagination-bullet-active {
        background-color: black;
    }

    .GalleryBlock__wrapper {
        transition: all 0.3s;
    }
    .GalleryBlock__wrapper.__with-image {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        height: 100%;
    }

    .GalleryBlock__wrapper .ImageSelector {
        top: 20px;
        bottom: auto;;
    }

    .GalleryBlock__overlay-gradients {
        display: block;
        content: " ";
        position: absolute;
        left: 0;
        bottom: 0px;
        right: -10px;
        top: 0;
        /*background-image: linear-gradient(0deg, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));*/
        pointer-events: none;
    }

</style>


<script>

    import Swiper from 'swiper'

    import getComponent from './getComponent'
    import style from "./helper/style";

    export default {
        props: {
            block: {
                type: Object,
                required: true,
            },
        },

        methods: {
            click() {
                this.navigate(this.block);
            },

            navigate(block) {
                this.$emit('navigate', block);
            },

            refresh() {
                this.$emit('refresh');
            },

            back() {
                this.$emit('back');
            },

            change( block ) {
                this.$emit('change', this.block );
            },

            input( block ) {
                this.$emit('input', this.block );
            },

            getComponent( block ) {
                return getComponent( block );
            },

            getBlockContainerStyles(block) {
                const container = style.container(block)
                const styles = block.styles ? style.font(block.styles) : {}

                return {
                    ...container,
                    ...styles,
                }
            },

            getGradientStyles( colors ) {
                return {
                    backgroundImage: `linear-gradient(180deg, ${colors.join(', ')})`
                }
            },

            contextMenu(block, event) {
                event.preventDefault()

                if (block.onContext) {
                    this.navigate({
                        onClick: {
                            ...block.onContext,
                            left: Math.round(event.pageX),
                            top: Math.round(event.pageY),
                        },
                    })
                }
            },

            attachSwiper() {
                this.$nextTick(() => {
                    if (this.swiper) {
                        this.swiper.destroy()
                    }

                    this.swiper = new Swiper (this.$refs.container, {
                        loop: false,
                        pagination: {
                            el: this.$refs.pagination,

                        },
                    })
                })
            },
        },

        watch: {
            block() {
                this.attachSwiper()
            }
        },

        mounted() {
            this.attachSwiper()
        },
    };

</script>
