const scrollTo = async (element, top, animated = true) => {
  const duration = 300;
  let startPosition = element.scrollTop;
  let distance = top - startPosition;
  let startTime = null;

  if (!animated) {
    element.scrollTop = top;
    return;
  }

  if (Math.abs(distance) > 1500) {
    distance = distance > 0 ? 750 : -750;
    startPosition = top - distance;
    element.scrollTop = startPosition;
  }

  const easeInOutQuad = (t, b, c, d) => {
    t /= d / 2;
    if (t < 1) return (c / 2) * t * t + b;
    t--;
    return (-c / 2) * (t * (t - 2) - 1) + b;
  };

  return new Promise(resolve => {
    const animation = time => {
      if (!element) {
        return resolve();
      }

      if (startTime === null) {
        startTime = time;
      }

      const timeElasped = time - startTime;

      element.scrollTop = easeInOutQuad(timeElasped, startPosition, distance, duration);

      if (timeElasped < duration) {
        requestAnimationFrame(animation);
      } else {
        resolve();
      }
    };

    requestAnimationFrame(animation);
  });
};

const scrollInto = async (view, element, animated = true) => {
  if (view && element) {
    if (view.scrollTop > element.offsetTop || view.scrollTop + view.clientHeight < element.offsetTop) {
      const newScrollTop = element.offsetTop - 50;

      return scrollTo(view, newScrollTop, animated);
    }
  }
};

const scrollTop = async (element, animated = true) => {
  if (element) {
    return scrollTo(element, 0, animated);
  }
};

const scrollBottom = async (element, animated = true) => {
  if (element) {
    return scrollTo(element, element.scrollHeight - element.clientHeight, animated);
  }
};

export default {
  to: scrollTo,

  into: scrollInto,
  top: scrollTop,
  bottom: scrollBottom
};
