<template>

    <div class="InputDialog" @click="close">
        <div class="InputDialog__content" @click.stop>
            <form @submit.prevent="submit">
                <div v-if="!!title" class="InputDialog__title">{{ title }}</div>
                <div v-if="!!description" class="InputDialog__description">{{ description }}</div>

                <div v-if="inputType === 'code'">
                    <monaco-editor
                        width="100%"
                        height="290"
                        language="javascript"
                        v-model="inputValue"
                        :options="{
                            overviewRulerBorder: false,
                            minimap: {
                                enabled: false,
                                lineDecorationsWidth: 20
                            }
                        }"
                    />
                </div>
                <template v-else>
                    <text-input v-if="multiline === false" v-model="inputValue" :error="!!error" :placeholder="placeholder" autofocus />
                    <textarea-autosize v-if="multiline === true" v-model="inputValue" :error="!!error" :placeholder="placeholder" autofocus :max-height="300" />
                </template>

                <div v-if="!!error" class="InputDialog__error">{{ error }}</div>

                <div class="InputDialog__actions">
                    <primary-button type="submit" :loading="saving">{{ submitButtonTitle }}</primary-button>
                    <secondary-button @click="close">{{ cancelButtonTitle }}</secondary-button>
                </div>
            </form>
        </div>
    </div>

</template>

<style>

    .InputDialog {
        position: fixed;

        left: 0;
        top: 0;
        height: 100%;
        width: 100%;

        background-color: rgba(0,0,0,0.2);

        z-index: 10001;

        display: flex;
        align-items: center;
        justify-content: center;
    }

    .InputDialog__content {
        padding: 40px;
        background-color: white;
        border-radius: 5px;

        min-width: 460px;
        max-width: 460px;

        box-shadow: 0 5px 10px -3px rgba(0,0,0,0.6);
    }

    .InputDialog__title {
        font-weight: bold;
        font-size: 16px;
        margin-bottom: 10px;
    }

    .InputDialog__description {
        font-size: 14px;
        margin-top: -5px;
        margin-bottom: 10px;
    }

    .InputDialog__error {
        margin-top: 10px;
        color: darkred;
    }

    .InputDialog__actions {
        margin-top: 30px;
    }

    @media screen and (max-width: 900px) {

        .InputDialog__content {
            max-width: 96%;
            min-width: 96%;
            box-sizing: border-box;
        }

    }

</style>

<script>

    import PrimaryButton from "@/modules/common/components/ui/PrimaryButton";
    import SecondaryButton from "@/modules/common/components/ui/SecondaryButton";
    import TextareaAutosize from "@/modules/common/components/ui/TextareaAutosize";
    import TextInput from "@/modules/common/components/ui/TextInput";



    export default {

        components: {
            TextareaAutosize,
            TextInput,
            SecondaryButton,
            PrimaryButton,
            //'monaco-editor':  () => import("monaco-editor-vue"),
        },

        props: {
            navigate: {
                type: Function,
                required: true,
            },

            inputType: {
                type: String,
                default: 'text',
            },

            title: {
                type: String,
                default: null,
            },

            description: {
                type: String,
                default: null,
            },

            value: {
                type: String,
                default: '',
            },

            multiline: {
                type: Boolean,
                default: false,
            },

            placeholder: {
                type: String,
                default: 'Введите значение',
            },

            submitUrl: {
                type: String,
                required: true,
            },

            submitButtonTitle: {
                type: String,
                default: 'Сохранить',
            },

            cancelButtonTitle: {
                type: String,
                default: 'Отменить',
            },
        },

        data() {
            return {
                inputValue: this.value,
                saving: false,
                error: null,
            }
        },

        methods: {
            async submit() {
                this.saving = true
                try {
                    try {
                        await this.navigate({
                            type: "apiCall",
                            url: this.submitUrl,
                            apiParams: {
                                value: this.inputValue
                            }
                        })

                        this.close()
                    } catch (error) {
                        if (error.response && error.response.status === 422) {
                            this.error = error.response.data.reason
                        } else {
                            throw error
                        }
                    }
                } finally {
                    this.saving = false
                }
            },
            onChange( value ) {
                this.inputValue = value;
            },



            keydown(event) {
                if (event.key === 'Escape') {
                    event.preventDefault()
                    this.close()
                }
            },

            close() {
                this.$emit('close')
            },
        },
        mounted() {
            document.addEventListener('keydown', this.keydown)
        },

        beforeDestroy() {
            document.removeEventListener('keydown', this.keydown)
        },

    }

</script>
