<template>

    <div v-if="resultValue" class="HorizontalLoader" />

</template>

<style>

    .HorizontalLoader {
        height: 3px;
        overflow: hidden;
    }

    .HorizontalLoader:after {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        height: 3px;
        width: 100%;
        transform-origin: right;
        animation: HorizontalLoader 1.5s linear infinite;
    }

    .HorizontalLoader:after {
        background: linear-gradient(#2988ff, #2988ff);
    }

    @keyframes HorizontalLoader {
        0% {
            transform: scaleX(0.8) translateX(-130%);
        }
        20% {
            transform: scaleX(0.8) translateX(-20%);
        }
        60% {
            transform: scaleX(0.8) translateX(-5%);
        }
        80% {
            transform: scaleX(0.1) translateX(-5%);
        }
        100% {
            transform: scaleX(0.2) translateX(190%);
        }
    }

</style>

<script>

    export default {

        props: {
            value: {
                type: Boolean,
                default: true,
            },

            delay: {
                type: Number,
                default: 0,
            },

            duration: {
                type: Number,
                default: 0,
            },
        },

        data() {
            return {
                resultValue: this.delay > 0 ? false : this.value,
                handler: null,
                tamestamp: Date.now(),
            };
        },

        watch: {
            value() {
                this.process();
            },
        },

        methods: {
            process() {
                if (this.delay === 0) {
                    this.resultValue = this.value;
                }

                else {
                    if (this.value) {
                        this.handler = setTimeout(() => {
                            this.resultValue = true;
                            this.timestamp = Date.now();
                        }, this.delay);
                    } else {
                        this.clearHandler();

                        if (this.resultValue) {
                            if (Date.now() - this.timestamp < this.duration)  {
                                this.handler = setTimeout(() => {
                                    this.resultValue = false;
                                }, this.duration - (Date.now() - this.timestamp));
                            } else {
                                this.resultValue = false;
                            }
                        }
                    }
                }
            },

            clearHandler() {
                if (this.handler) {
                    clearTimeout(this.handler);
                    this.handler = null;
                }
            },
        },

        mounted() {
            this.process();
        },

        beforeDestroy() {
            this.clearHandler();
        },

    };

</script>
