export function enrichOnContextByElement(block, element) {
  if (block.onClick) {
    const rect = element.getBoundingClientRect()

    const enrichAction = (action) => {
      if (action.type === 'showContextMenu') {
        action.left = rect.left
        action.top = rect.top
        action.width = rect.width
        action.height = rect.height
      }
    }

    if (block.onClick instanceof Array) {
      block.onClick.forEach(enrichAction)
    } else {
      enrichAction(block.onClick)
    }
  }

  return block
}

export function enrichOnContextByPoint(block, x, y) {
  if (block.onClick) {
    const enrichAction = (action) => {
      if (action.type === 'showContextMenu') {
        action.left = x
        action.top = y
      }
    }

    if (block.onClick instanceof Array) {
      block.onClick.forEach(enrichAction)
    } else {
      enrichAction(block.onClick)
    }
  }

  return block
}
