import fileStore from "@/fileStore";
import isBoxBlock from "../getComponent";

const textColor = "#292929";
const titleTextColor = "#292929";
const titleLightTextColor = "#eee";
const cardBorderColor = "#D9DBDE";
const warningColor = "#EC135A";
const weakWarningColor = "#ecaf1a";
const linkColor = "#0379FE";
const linkHighlightColor = "rgb(181, 215, 255)";
const listItemPaddingVertical = 7;
const cardBackgroundLightColor = "#f7f7f7";
const cardBackgroundColor = "#eeeeee";
const notedTextColor = "#808080";
const headerBorderBottomColor = "#A7A7AA";
const checkboxColor = "#65aa58";
const underlayColor = "#F7F7F7";

const fontSize = {
  mini: '12px', // '12px',
  small: '15px', // '15px',
  medium: '16px', // '16px',
  large: '20px', // '20px',
  xlarge: '23px', // '24px',
  xxlarge: '25px', // '28px',
  xxxlarge: '30px', // '33px',
}

const transformFontSize = size => {
  return fontSize[size] ? fontSize[size] : size
};

const defaultContainerStyle = (block, scope = 'default') => {
  if (scope === 'header' || scope === 'gallery') {
    return {
      paddingLeft: 0,
      paddingRight: 0,
      marginTop: 0,
      marginBottom: 0,
    }
  }

  if (block.type === 'screen') {
    return {
      paddingLeft: 0,
      paddingRight: 0,
      marginTop: 0,
      marginBottom: 0,
    }
  } else {
    return {
      paddingLeft: 10,
      paddingRight: 10,
      marginTop: 10,
      marginBottom: 10,
    }
  }
};

const fontStyle = {
  mini: {
    fontSize: fontSize.mini
  },
  small: {
    fontSize: fontSize.small
  },
  medium: {
    fontSize: fontSize.medium
  },
  large: {
    fontSize: fontSize.large
  },
  xlarge: {
    fontSize: fontSize.xlarge
  },
  xxlarge: {
    fontSize: fontSize.xxlarge
  },
  xxxlarge: {
    fontSize: fontSize.xxxlarge
  },
  normal: {
    fontWeight: "normal"
  },
  bold: {
    fontWeight: "bold"
  },
  semibold: {
    fontWeight: "500"
  },
  italic: {
    fontStyle: "italic"
  },
  underlined: {
    textDecorationLine: "underline"
  },
  link: {
    color: linkColor,
      textDecorationStyle: "solid",
      textDecorationColor: linkColor,
      textDecorationLine: "underline"
  },
  muted: {
    opacity: 0.6
  },
  warning: {
    color: warningColor,
    textDecorationColor: warningColor
  },
}

const container = (block, scope = 'default') => {
  let containerStyle = {
    ...defaultContainerStyle(block, scope),
    ...block.containerStyle,
  }

  const px = (value) => value ? value + 'px' : null
  const bw = (value) =>
    value
    ? value === 'hairline'
      ? '.5px'
      : value + 'px'
    : null

  let result = {
    color: block.color ? block.color : containerStyle.color,
    fontWeight: block.isBold ? "bold" : "normal",
    fontSize: block.fontSize ? transformFontSize(block.fontSize) : transformFontSize('medium'),
    backgroundColor: containerStyle.bgColor,
    height: px(containerStyle.height),
    paddingTop: px(containerStyle.paddingTop),
    paddingLeft: px(containerStyle.paddingLeft),
    paddingBottom: px(containerStyle.paddingBottom),
    paddingRight: px(containerStyle.paddingRight),
    marginTop: px(containerStyle.marginTop),
    marginLeft: px(containerStyle.marginLeft),
    marginBottom: px(containerStyle.marginBottom),
    marginRight: px(containerStyle.marginRight),
    textAlign: containerStyle.align,
    opacity: containerStyle.opacity,
    borderRadius: px(containerStyle.borderRadius),
    borderColor: containerStyle.borderColor,
    borderWidth: bw(containerStyle.borderWidth),
    borderStyle: containerStyle.borderWidth
      ? containerStyle.borderStyle || 'solid'
      : null,
  };

  if (containerStyle.fontStyle && fontStyle[containerStyle.fontStyle]) {
    result = { ...result, ...fontStyle[containerStyle.fontStyle] }
  }

  if (
    (
      containerStyle.alignContent ||
      containerStyle.alignItems ||
      containerStyle.flexDirection ||
      containerStyle.flexWrap ||
      containerStyle.justifyContent
    ) && ! isBoxBlock(block)
  ) {
    result.display = "flex";
    result.alignContent = containerStyle.alignContent || undefined;
    result.alignItems = containerStyle.alignItems || undefined;
    result.flexDirection = containerStyle.flexDirection || undefined;
    result.flexWrap = containerStyle.flexWrap || undefined;
    result.justifyContent = containerStyle.justifyContent || undefined;
  }

  if (containerStyle.height) {
    result.height = containerStyle.height + "px";
  }
  if (containerStyle.backgroundUrl) {
    result["background-image"] = `url(${containerStyle.backgroundUrl})`;
    result["background-size"] = `cover`;
  }
  if (containerStyle.backgroundImage) {
    result["background-image"] = `url(${fileStore.bigThumbnailUrl(containerStyle.backgroundImage.hash)})`;
    result["background-size"] = `cover`;
  }

  const clean = (obj) => {
    for (const field in obj) {
      if (obj[field] === null || obj[field] === undefined) {
        delete obj[field]
      }
    }

    return obj
  }

  return clean(result);
};

const font = (styles) => {
  let result = {}

  styles.forEach((style) => {
    if ( typeof style === 'object') {
      result = {...result, ...style}
    }
    else {
      result = {...result, ...fontStyle[style]}
    }
  })

  return result
};

export default {
  container,
  font,
};
