        </div>

        <div class="ApplicationHeader__search">
            <text-input class="ApplicationHeader__search-input" v-model="query" placeholder="Глобальный поиск..." />

            <div v-if="query.length > 0" class="ApplicationHeader__search-clear" @click="query = ''">
