</template>

<style>

    .ApplicationHeader {
        padding: 7px 10px;

        display: flex;
        background-color: #eaeaea;
        border-bottom: 1px solid #d8d8d8;
    }

    .ApplicationHeader__search {
        flex-grow: 2;
        position: relative;
    }

    .ApplicationHeader__search-input {
        border: 1px solid #f5f5f5;
        background-color: #f5f5f5;
    }

    .ApplicationHeader__search-clear {
        position: absolute;
        right: 10px;
        top: 10px;

        cursor: pointer;
    }

    .ApplicationHeader__profile {
        width: 34px;
        margin-left: 5px;
        flex-shrink: 0;

        display: flex;
        align-items: center;
        justify-content: flex-end;

        cursor: pointer;
        color: silver;
    }

    .ApplicationHeader__profile-normal {
        display: block;
    }

    .ApplicationHeader__profile-hovered {
        display: none;
        color: black;
    }

    .ApplicationHeader__create  {
        width: 34px;
        flex-shrink: 0;

        display: flex;
        align-items: center;

        cursor: pointer;
        color: silver;
    }

    .ApplicationHeader__create:hover {
        color: black;
    }

    .ApplicationHeader__profile:hover .ApplicationHeader__profile-normal {
        display: none;
    }

    .ApplicationHeader__profile:hover .ApplicationHeader__profile-hovered {
        display: block;
    }

</style>

<script>

    import store from '@/store'

    import TextInput from '@/modules/common/components/ui/TextInput'

    export default {

        inject: {
            embed: {
                default: false,
            },
        },

        components: {
            TextInput,
        },

        data() {
            return {
                query: '',
                queryHandler: null,
            }
        },

        computed: {
            globalSearchScreen() {
                return store.globalSearchScreen
            },
        },

        watch: {
            query() {
                if (this.queryHandler) clearTimeout(this.queryHandler)
                if (this.query === '') {
                    this.debouncedQueryInput()
                } else {
                    this.queryHandler = setTimeout(() => this.debouncedQueryInput(), 500)
                }
            },

            globalSearchScreen() {
                if (this.globalSearchScreen === null) {
                    this.query = ''
                }
            }
        },

        methods: {
            profileClick() {
                this.$router.push('/user/profile').catch(() => {})
            },

            createClick() {
                this.$emit('navigate', {
                    onClick: {
                        type: 'navigate',
                        url: '/logic/create'
                    }
                })
                // тут надо как-то замкнуть на navigate, пока не понял как правильно
            },

            async debouncedQueryInput() {
                if (this.query.trim().length > 0) {
                    const response = await this.$api.get('search', { q: this.query })
                    store.globalSearchScreen = response.data
                } else {
                    store.globalSearchScreen = null
                }
            },
        },

        beforeDestroy() {
            if (this.queryHandler) clearTimeout(this.queryHandler)
        },

    }

</script>
