import axios from "axios";
import config from "@/config";
import store from "@/store";

function getSizeLabel(size) {
  if (size < 1000) {
    return size + " bytes";
  } else if (size < 1000000) {
    return Math.round(size / 1024) + "KB";
  } else if (size < 1000000000) {
    return Math.round((size / 1024 / 1024) * 100) / 100 + "MB";
  } else if (size < 1000000000000) {
    return Math.round((size / 1024 / 1024 / 1024) * 100) / 100 + "GB";
  }
}

async function uploadNext() {
  const uploading = store.uploadingFiles.find(f => f.uploading === true)
  if (uploading) {
    return;
  }

  const uploadFile = store.uploadingFiles.find(f => f.uploaded === false)

  const formData = new FormData();
  formData.append("Filedata", uploadFile.file);

  const options = {
    headers: {
      "Content-Type": "multipart/form-data"
    },
    onUploadProgress: progressEvent => {
      uploadFile.progress = Math.round((progressEvent.loaded * 100) / progressEvent.total);
    },
    cancelToken: uploadFile.cancelToken.token
  };

  try {
    uploadFile.uploading = true

    const response = await axios.post(
      config.fileStorage.endpoint(uploadFile.accountId),
      formData,
      options,
    )

    uploadFile.uploading = false
    uploadFile.uploaded = true

    await uploadFile.callback(response.data, uploadFile)
    await uploadNext()
  } catch (error) {
    uploadFile.uploading = false
    uploadFile.canceled = true
  }
}

async function addUploadFile(accountId, file, callback) {
  const cancelToken = axios.CancelToken.source()

  store.uploadingFiles.push({
    accountId,
    file: file,
    progress: 0,
    sizeLabel: getSizeLabel(file.size),
    cancelToken: cancelToken,
    uploading: false,
    uploaded: false,
    canceled: false,
    callback,
  })

  await uploadNext()
}

export default {
  addUploadFile,
}
