<script>

    export default {

        props: {
            maxHeight: {
                type: Number,
                default: 100000,
            },
        },

        data() {
            return {
                lastHardBreakCount: 0,
                timeoutHandler: null,
            };
        },

        methods: {
            updateSizeTextarea() {
                this.$el.style.height = Math.min(this.maxHeight, this.$el.scrollHeight) + 'px';
            },

            resetSizeTextarea() {
                this.$el.style.height = 'auto';
                this.updateSizeTextarea()
            },

            inputTextarea() {
                if (this.$el.value === '') {
                    this.resetSizeTextarea();
                    this.lastHardBreakCount = 0;
                } else {
                    const matches = this.$el.value.match(/\n/g);
                    const hardBreakCount = matches ? matches.length : 0;

                    if (this.lastHardBreakCount > hardBreakCount) {
                        this.resetSizeTextarea();
                    }

                    this.lastHardBreakCount = hardBreakCount;
                }
            },

            keydownTextarea(event) {
                if (event.key === 'Backspace') {
                    clearTimeout(this.timeoutHandler);
                    this.timeoutHandler = setTimeout(() => {
                        this.resetSizeTextarea();
                        this.timeoutHandler = null;
                    }, 500);
                }
            },
        },

        mounted () {
            this.$nextTick(() =>this.resetSizeTextarea());

            this.$el.addEventListener('scroll', this.updateSizeTextarea);
            this.$el.addEventListener('input', this.inputTextarea);
            this.$el.addEventListener('keydown', this.keydownTextarea);
        },

        beforeDestroy () {
            this.$el.removeEventListener('scroll', this.updateSizeTextarea);
            this.$el.removeEventListener('input', this.inputTextarea);
            this.$el.removeEventListener('keydown', this.keydownTextarea);

            if (this.timeoutHandler) {
                clearTimeout(this.timeoutHandler);
            }
        },

        render () {
            return this.$slots.default[0];
        },

    };

</script>
