<template>

    <div class="ScreenBlock"
         :class="classes"
         @click="click"
         @contextmenu="$emit('contextmenu', $event)"
    >
        <div class="ScreenBlock__left">
            <div class="ScreenBlock__logo">
                <template v-if="block.logo" >
                    <logo :params="block.logo" :size="isSmall ? 'medium' : 'normal'" />
                </template>
                <logo v-if="block.status" class="ScreenBlock__logo-status" :params="block.status" :size="isSmall ? 'mini' : 'small'" :disableStylish="true"  />
            </div>
        </div>

        <div class="ScreenBlock__right">
            <div class="ScreenBlock__right-wrapper">
                <div class="ScreenBlock__item-title" :style="{ color: block.titleColor }">
                    <div class="ScreenBlock__item-title-left">{{ block.order }}
                        {{block.title}}
                    </div>
                    <div class="ScreenBlock__item-title-right">
                        <span class="ScreenBlock__item-time">{{ humanDate }}</span>
                        <span v-if="block.unreadBullet" class="ScreenBlock__item-unread-bullet"></span>
                    </div>
                </div>
                <div v-if="block.description" class="ScreenBlock__description" :style="{ color: block.titleColor || ( block.containerStyle ? block.containerStyle.color : null ) }">
                    {{block.description}}
                </div>
            </div>
        </div>
    </div>

</template>

<style>

    .ScreenBlock {
        user-select: none;

        box-sizing: border-box;
        height: 75px;
        padding: 10px 0;
        position: relative;

        display: flex;

        font-size: inherit !important;
    }

    .ScreenBlock.__clickable {
        cursor: pointer;
    }

    .ScreenBlock.__unavailable {
        background: #F0F0F0;
    }

    .ScreenBlock__left {
        width: 70px;
        flex-shrink: 0;

        display: flex;
        align-items: center;
        justify-content: center;
    }

    .ScreenBlock__right {
        display: flex;

        align-items: center;

        overflow: hidden;
        flex-grow: 2;
    }

    .ScreenBlock__right-wrapper {
        width: 100%;
        max-height: 55px;

        display: flex;
        flex-direction: column;
    }

    .ScreenBlock__logo {
        position: relative;
    }

    .ScreenBlock__logo-status {
        position: absolute;
        right: -3px;
        bottom: -3px;
    }

    .ScreenBlock__item-title {
        display: flex;
        align-items: baseline;
        line-height: 16px;
    }

    .ScreenBlock__item-title-left {
        font-size: 14px;
        font-weight: bold;
        margin-bottom: 3px;

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        max-width: 100%;

        padding-right: 5px;
        flex-grow: 2;
    }

    .ScreenBlock__item-title-right {
        white-space: nowrap;
    }

    .ScreenBlock__item-unread-bullet {
        display: inline-block;
        width: 10px;
        height: 10px;
        border-radius: 10px;
        background-color: #2381cc;
        margin-right: 5px;
    }

    .ScreenBlock__item-time {
        font-size: 13px;
        color: #9f9f9f;
        margin-right: 10px;
    }

    .ScreenBlock__description {
        font-size: 13px;
        line-height: 15px;
        max-height: 30px;

        opacity: 0.6;

        overflow: hidden;
        text-overflow: ellipsis;

        padding-right: 5px;
    }

    .ScreenBlock.__selected {
        background-color: #2788cc !important;
        color: white !important;
    }

    .ScreenBlock.__selected .ScreenBlock__description,
    .ScreenBlock.__selected .ScreenBlock__item-time {
        color: white !important;
    }

    /**
     * Маленький скрин
     */

    .ScreenBlock.__small {
        height: 50px;
        padding: 7px 0;
    }

    .ScreenBlock.__small .ScreenBlock__left {
        width: 60px;
    }


</style>

<script>

    import {enrichOnContextByPoint} from "@/services/actions";
    import datetime from "@/services/datetime";

    import Logo from "@/modules/common/components/ui/Logo";

    export default {

        components: {
            Logo
        },

        props: {
            block: {
                type: Object,
                required: true,
            },

            selected: {
                type: Boolean,
                default: false,
            },

            small: {
                type: Boolean,
                default: false,
            },
        },

        computed: {
            classes() {
                let unavailable = false;
                if ( this.block.status && ! this.block.status.isAvailable  ) {
                    unavailable = true;
                }

                return {
                    __selected: this.selected || this.block.active,
                    __small: this.isSmall,
                    __clickable: this.clickable,
                    __unavailable: unavailable
                }
            },

            humanDate() {
                return this.block.updatedAtTimestamp ? datetime.humanDate(this.block.updatedAtTimestamp) : '';
            },

            clickable() {
                return !!this.block.onClick;
            },

            isSmall() {
                return this.small || this.block.imageSize === 'small'
            },
        },

        methods: {
            async click(event) {

                if (this.clickable) {
                    this.$emit('navigate', enrichOnContextByPoint(
                        this.block,
                        Math.round(event.pageX),
                        Math.round(event.pageY)
                    ))
                }
            },

            imageChanged( model ) {
                this.$emit('change', this.block)
            },

            refresh() {
                this.$emit('refresh');
            },

            navigate(block) {
                this.$emit('navigate', block);
            },

            back() {
                this.$emit('back');
            },
        },

    };

</script>
