const either = (primary, secondary) => (primary ? primary : secondary);

const fileStorageProtocol = either(process.env.VUE_APP_FILE_STORAGE_PROTOCOL, "https");
const fileStorageDomain = either(process.env.VUE_APP_FILE_STORAGE_DOMAIN, "fs.chatium.io");

export default {
  env: process.env.VUE_APP_ENV,

  build: {
    hash: process.env.VUE_APP_VERSION_HASH,
    date: process.env.VUE_APP_VERSION_DATE
  },

  backend: {
    protocol: process.env.VUE_APP_BACKEND_PROTOCOL,
    domain: process.env.VUE_APP_BACKEND_DOMAIN,
    url: process.env.VUE_APP_BACKEND_PROTOCOL + "://" + process.env.VUE_APP_BACKEND_DOMAIN,
    socket_url:
      process.env.VUE_APP_BACKEND_PROTOCOL +
      "://" +
      (process.env.VUE_APP_SOCKET_DOMAIN ? process.env.VUE_APP_SOCKET_DOMAIN : process.env.VUE_APP_BACKEND_DOMAIN)
  },

  fileStorage: {
    protocol: fileStorageProtocol,
    domain: fileStorageDomain,
    url: fileStorageProtocol + "://" + fileStorageDomain,

    endpoint: account =>
      fileStorageProtocol + "://" + fileStorageDomain + "/upload?accountId=" + account,

    uploadVideoEndpoint: account =>
      fileStorageProtocol + "://" + fileStorageDomain + "/upload_video?accountId=" + account
  },

  dev: {
    hrSocketId: process.env.CHATIUM_DEV_HR_SOCKET_ID || "__chatium:dev:hr__"
  }
};
