<template>

    <div v-if="imageUrl"
         class="ImageBlock"
         :class="{ __loaded: loaded }"
         @click="click"
         @contextmenu="$emit('contextmenu', $event)"
    >
        <img class="ImageBlock__image"
             :src="imageUrl"
             :height="size.height ? `${size.height}px` : 'auto'"
             :width="size.width ? `${size.width}px` : 'auto'"
             @load="load"
             ref="image"
        />
    </div>

</template>

<style>

    .ImageBlock {
        font-size: 0 !important;
    }

    .ImageBlock__image {
        max-width: 100%;
        opacity: 0;
        transition: opacity .4s;
    }

    .ImageBlock.__loaded .ImageBlock__image {
        opacity: 1;
    }

</style>

<script>

    import fileStore from "@/fileStore";
    import {enrichOnContextByElement, enrichOnContextByPoint} from "@/services/actions";

    export default {

        props: {
            block: {
                type: Object,
                required: true,
            }
        },

        data() {
            return {
                loaded: false,
                clientWidth: Math.min(window.innerWidth, 500),
            }
        },

        computed: {
            size() {
                const { width, height } = this.block.downloadUrl
                    ? this.block.imageSize
                        ? this.block.imageSize
                        : { width: null, height: null }
                    : fileStore.getSizesFromHash(this.block.image.hash)

                if (this.block.width || this.block.height) {
                    if (this.block.width && this.block.height) {
                        return {
                            width: this.block.width,
                            height: this.block.height,
                        }
                    } else {
                        if (this.block.width) {
                            const ratio = width / this.block.width

                            return {
                                width: this.block.width,
                                height: Math.floor(height / ratio),
                            }
                        } else {
                            const ratio = height / this.block.height

                            return {
                                height: this.block.height,
                                width: Math.floor(width / ratio),
                            }
                        }
                    }
                } else {
                    if (width > this.clientWidth) {
                        const ratio = width / this.clientWidth

                        return {
                            width: this.clientWidth,
                            height: Math.floor(height / ratio),
                        }
                    } else {
                        return { width, height }
                    }
                }
            },

            imageUrl() {
                let size = this.size;
                if ( this.block.image && size && ( size.width || size.height ) ) {
                    return fileStore.getFsThumbnailUrl( this.block.image.hash, size.width, size.height )
                }

                if ( this.block.image && this.block.image.hash ) {
                    return fileStore.mediumThumbnailUrl( this.block.image.hash )
                }
                else if ( this.block.downloadUrl ) {
                    return this.block.downloadUrl;
                }
            },
        },

        methods: {
            load() {
                this.loaded = true
                this.$emit('loaded')
            },

            click(event) {
                this.$emit('navigate', enrichOnContextByPoint(
                    this.block,
                    Math.round(event.pageX),
                    Math.round(event.pageY)
                ));
            },
        },

        mounted() {
            this.clientWidth = this.$el.clientWidth
        },
    };

</script>
