<template>

    <div class="FileUploader">
        <file-catcher v-if="files.length === 0" @catch="catchFiles" ref="fileCatcher" />

        <portal v-if="files.length > 0" to="presenter-stack">
            <div class="FileUploader__overlay" @click="cancel">
                <div class="FileUploader__modal" @click.stop tabindex="1" ref="modal">
                    <template v-if="oneImagePreview && files[0].dataUrl">
                        <vector-image-editor :image="files[0].dataUrl" @submit="editorSubmit" @cancel="cancel" ref="editor" />
                    </template>
                    <template v-else>
                        <div class="FileUploader__files">
                            <div class="FileUploader__file" v-for="item in files">
                                <div class="FileUploader__file-preview">
                                    <div v-if="item.image" class="FileUploader__file-preview-image" :style="{ backgroundImage: 'url(' + item.dataUrl + ')' }"></div>
                                    <svg v-else width="24" height="24" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd">
                                        <path d="M22 24h-20v-24h14l6 6v18zm-7-23h-12v22h18v-16h-6v-6zm3 15v1h-12v-1h12zm0-3v1h-12v-1h12zm0-3v1h-12v-1h12zm-2-4h4.586l-4.586-4.586v4.586z"/>
                                    </svg>
                                </div>
                                <div class="FileUploader__file-description">
                                    <div class="FileUploader__file-description-name">{{ item.file.name }}</div>
                                    <div class="FileUploader__file-description-size">{{ humanFileSize(item.file.size, true) }}</div>
                                </div>
                            </div>
                        </div>
                    </template>

                    <div class="FileUploader__content-actions">
                        <input class="FileUploader__content-actions-input" type="text" v-model="message" placeholder="Добавьте описание..." ref="input" />
                        <div class="FileUploader__content-action" @click="oneImagePreview ? $refs.editor.submit() : submit()">
                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="#0174d9">
                                <path d="M24 0l-6 22-8.129-7.239 7.802-8.234-10.458 7.227-7.215-1.754 24-12zm-15 16.668v7.332l3.258-4.431-3.258-2.901z"/>
                            </svg>
                        </div>
                    </div>
                </div>
            </div>
        </portal>
    </div>

</template>

<style>

    .FileUploader {
        user-select: none;
    }

    .FileUploader__overlay {
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        z-index: 10000;

        background-color: rgba(0, 0, 0, 0.6);

        display: flex;
        align-items: center;
        justify-content: center;
    }

    .FileUploader__modal {
        background-color: white;
        border-radius: 5px;

        outline: none;
    }

    .FileUploader__files {
        padding: 10px;
        width: 400px;
        max-width: 95vw;
    }

    .FileUploader__file {
        display: flex;
        margin-bottom: 10px;
    }

    .FileUploader__file:last-child {
        margin-bottom: 0;
    }

    .FileUploader__file-preview {
        width: 40px;
        flex-shrink: 0;

        display: flex;
        align-items: center;
        justify-content: center;

        margin-right: 10px;
    }

    .FileUploader__file-preview-image {
        width: 40px;
        height: 40px;
        border-radius: 5px;
        background-size: cover;
    }

    .FileUploader__file-description {
        overflow: hidden;
    }

    .FileUploader__file-description-name {
        font-weight: bold;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .FileUploader__file-description-size {
        color: #777;
    }

    .FileUploader__content-actions {
        border-top: 1px solid #eee;
        padding: 10px;

        display: flex;
    }

    .FileUploader__content-actions-input {
        font: inherit;
        border: none;
        outline: none;
        width: 100%;

        flex-grow: 2;
    }

    .FileUploader__content-actions-input::placeholder {
        color: #ccc;
        -webkit-font-smoothing: antialiased;
    }

    .FileUploader__content-action {
        outline: none;
        font: inherit;
        border: none;

        flex-shrink: 0;
        width: 40px;
        display: flex;
        align-items: center;
        justify-content: center;

        cursor: pointer;
    }


</style>

<script>

    import FileCatcher from "@/modules/common/components/ui/FileCatcher";
    import ImageEditor from '@/modules/common/components/ui/ImageEditor';
    import VectorImageEditor from '@/modules/common/components/ui/VectorImageEditor';
    import { formData } from "@/services/formData";

    export default {

        components: {
            FileCatcher,
            ImageEditor,
            VectorImageEditor,
        },

        props: {
            url: {
                type: String,
                required: true,
            },

            initialMessage: {
                type: String,
                default: '',
            },
        },

        data() {
            return {
                files: [],
                message: this.initialMessage,
            };
        },

        computed: {
            // Когда мы запостили один файл и это картинка
            oneImagePreview() {
                return this.files.length === 1 && this.files[0].image;
            },
        },

        watch: {
            initialMessage() {
                this.message = this.initialMessage;
            },

            files() {
                if (this.files.length > 0) {
                    this.$nextTick(() => this.$refs.input.focus());
                }
            },
        },

        methods: {
            catchFiles(files) {
                this.files = files.map((file) => {
                    const data = {
                        file: file,
                        image: /image/.test(file.type),
                        dataUrl: null,
                    };

                    if (data.image) {
                        const reader = new FileReader();
                        reader.onload = () => {
                            data.dataUrl = reader.result;
                        };
                        reader.readAsDataURL(file);
                    }

                    return data;
                });

                this.$nextTick(() => this.$refs.modal.focus());
            },

            selectFiles() {
                this.$refs.fileCatcher.selectFiles();
            },

            keydown(event) {
                if (this.files.length > 0) {
                    if (event.key === 'Escape') {
                        event.preventDefault();
                        this.cancel();
                    }

                    if (event.key === 'Enter') {
                        event.preventDefault();
                        this.submit();
                    }
                }
            },

            async submit() {
                const result = [];

                const __files = this.files;
                this.files = [];

                this.$nextTick(async () => {
                    for (let i = 0; i < __files.length; i++) {
                        const response = await fetch(this.url, {
                            method: 'POST',
                            body: formData({
                                Filedata: __files[i].file,
                                fullAnswer: 'true',
                            })
                        });

                        const data = await response.json();

                        result.push({
                            hash: data.hash,
                            name: __files[i].file.name,
                            meta: {
                                mime: __files[i].file.type,
                                name: __files[i].file.name,
                                size: __files[i].file.size,
                            },
                            url: data.download_url,
                            original: __files[i].file,
                        });
                    }

                    this.$emit('submit', result, this.message);
                });
            },

            editorSubmit(blob) {
                this.files = [{
                    file: blob,
                    image: /image/.test(blob.type),
                    dataUrl: null,
                }];

                this.submit();
            },

            cancel() {
                this.$emit('cancel');
                this.files = [];
            },

            humanFileSize(bytes, si) {
                const thresh = si ? 1000 : 1024;
                if (Math.abs(bytes) < thresh) {
                    return bytes + ' B';
                }
                const units = si
                    ? ['kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB']
                    : ['KiB', 'MiB', 'GiB', 'TiB', 'PiB', 'EiB', 'ZiB', 'YiB'];
                let u = -1;
                do {
                    bytes /= thresh;
                    ++u;
                } while (Math.abs(bytes) >= thresh && u < units.length - 1);
                return bytes.toFixed(1) + ' ' + units[u];
            },
        },

        mounted() {
            window.addEventListener('keydown', this.keydown);
        },

        beforeDestroy() {
            window.removeEventListener('keydown', this.keydown);
        },

    };

</script>
