<template>

    <div class="DesktopAppearance">
        <div class="DesktopAppearance__background"></div>

        <div class="DesktopAppearance__layout">
            <div class="DesktopAppearance__layout-left"></div>
            <div class="DesktopAppearance__layout-center" ref="centerColumn">
                <presenter class="DesktopAppearance__presenter" :key="url"
                           :show-header="true"
                           :url="url"
                           @navigate="$emit('navigate', $event)"
                           @title="$emit('title', $event)"
                           ref="presenter"
                />

                <div v-if="modals.length > 0" class="DesktopAppearance__layout-center-modal-overlay" @click="popModalAndRefresh">
                    <div class="DesktopAppearance__layout-center-modal" :style="modalStyles" @click.stop>
                        <modal-navigator-stack :urls="modals"
                                               @navigate="modalsNavigate"
                                               @close="popModalAndRefresh"
                                               ref="modals"
                        />
                    </div>
                </div>
            </div>
            <div class="DesktopAppearance__layout-right" :class="{ __inbox: !!inboxScreen }">
                <div v-if="!!inboxScreen" class="DesktopAppearance__inbox">
                    <!--application-header @navigate="$emit('navigate', $event)"/-->
                    <div class="DesktopAppearance__inbox-content">
                        <presenter url="inbox"
                                   :show-header="true"
                                   :screen="inboxScreen"
                                   :highlight-url="url"
                                   @navigate="$emit('navigate', $event)"
                                   ref="inbox"
                        />
                    </div>
                </div>
            </div>
        </div>

    </div>

</template>

