<script>

    import ApplicationHeader from "@/modules/application/ApplicationHeader";
    import ModalNavigatorStack from "@/modules/common/components/ModalNavigatorStack";
    import Navigator from "@/modules/common/components/Navigator";
    import Presenter from "@/modules/common/components/Presenter";

    export default {

        components: {
            ModalNavigatorStack,
            Navigator,
            ApplicationHeader,
            Presenter
        },

        inject: {
            embed: {
                default: false,
            },
        },

        props: {
            url: {
                type: String,
                required: true,
            },

            inbox: {
                type: Boolean,
                default: true,
            },
        },

        data() {
            return {
                inboxScreen: null,
                modals: [],
                modalStyles: {},
            }
        },

        watch: {
            $route() {
                this.modals = []
            },
        },

        methods: {
            refresh() {
                this.$refs.presenter.fetch()
            },

            resetSearch() {
                this.$refs.presenter.resetSearch()
            },

            unknownAction(action) {
                this.$refs.presenter.unknownAction(action)
            },

            pushModal(url) {
                this.modalStyles.left = (this.$refs.centerColumn.offsetLeft - 20) + 'px'
                this.modalStyles.width = (this.$refs.centerColumn.clientWidth + 40) + 'px'

                this.modals.push(url)
            },

            modalsNavigate(route) {
                if (route.openInModalScreen === false) {
                    this.modals = []
                }

                this.$emit('url', route)
            },

            popModalAndRefresh() {
                this.modals.pop()
                this.$nextTick(() => {
                    if (this.modals.length > 0) {
                        this.$refs.modals.refresh()
                    } else {
                        this.$refs.presenter.refresh()
                    }
                })
            },

            async fetchInbox() {
                if (this.inbox) {
                    const response = await this.$api.get(`inbox`, undefined, { skipErrors: true })
                    this.inboxScreen = response.data
                }
            },
        },

        async mounted() {
            this.$root.$on('authoraized', this.fetchInbox)

            try {
                if (this.embed === false) {
                    await this.fetchInbox()
                }
            } catch (error) {
                // Не смогли получить инбокс
            }
        },

    }

</script>
