<template>

    <navigator @url="route"
               @back="back"
               @refresh="refresh"
               @showContextMenu="showContextMenu"
               @showDialog="showDialog"
               @attachMedia="attachMedia"
               ref="navigator">
        <div v-for="(modal, index) in urls" :style="styleByIndex(index)">
            <presenter :key="modal.urls[modal.urls.length - 1]"
                       :show-header="true"
                       :url="modal.urls[modal.urls.length - 1]"
                       @navigate="navigate"
                       ref="presenter"
            />
        </div>

        <input-dialog v-if="dialog"
                      :title="dialog.title"
                      :description="dialog.description"
                      :value="dialog.value"
                      :placeholder="dialog.placeholder"
                      :input-type="dialog.inputType"
                      :multiline="dialog.multiline"
                      :submit-url="dialog.submitUrl"
                      :submit-button-title="dialog.submitButtonTitle"
                      :cancel-button-title="dialog.cancelButtonTitle"
                      :navigate="navigate"
                      @close="dialog = null"
        />

        <context-menu v-if="contextMenu"
                      :items="contextMenu.menu"
                      :left="contextMenu.left"
                      :top="contextMenu.top"
                      :width="contextMenu.width"
                      :height="contextMenu.height"
                      @navigate="contextMenuNavigate"
                      @close="contextMenu = null"
        />

        <modal-file-uploader v-if="modalAttachMedia"
                             :action="modalAttachMedia"
                             :navigate="navigate"
                             @close="modalAttachMedia = null"
        />
    </navigator>

</template>

<style>

    .ModalNavigatorStack {

    }

</style>

<script>

    import ModalFileUploader from "@/modules/common/components/ModalFileUploader";
    import Navigator from "@/modules/common/components/Navigator";
    import Presenter from "@/modules/common/components/Presenter";
    import ContextMenu from "@/modules/common/components/ui/ContextMenu";
    import InputDialog from "@/modules/common/components/ui/InputDialog";

    export default {

        components: {
            ModalFileUploader,
            InputDialog,
            ContextMenu,
            Navigator,
            Presenter
        },

        props: {
            urls: {
                type: Array,
                default: () => [],
            },
        },

        data() {
            return {
                contextMenu: null,
                needHideContextMenu: true,
                dialog: null,
                modalAttachMedia: null,
            }
        },

        methods: {
            route(route) {
                if (route.openInModalScreen !== undefined) {
                    this.$emit('navigate', route)
                } else {
                    this.urls[this.urls.length - 1].urls.push(route.url)
                }
            },

            back(data) {
                if (data.dismissModal === true) {
                    return this.$emit('close')
                }

                if (this.urls[this.urls.length - 1].urls.length > 1) {
                    this.urls[this.urls.length - 1].urls.pop()
                } else {
                    this.$emit('close')
                }
            },

            refresh() {
                this.$refs.presenter[this.urls.length - 1].refresh()
            },

            async navigate(block) {
                return this.$refs.navigator.navigate(block)
            },

            showContextMenu(contextMenu) {
                if (this.contextMenu) {
                    this.contextMenu = {
                        ...this.contextMenu,
                        menu: contextMenu.menu,
                    }
                    this.needHideContextMenu = false
                } else {
                    this.contextMenu = contextMenu
                    this.needHideContextMenu = true
                }
            },

            showDialog(dialog) {
                if (this.dialog) {
                    // it's necessary to defer here,
                    // because otherwise chaining of dialogs immediately one after another doesn't work
                    // may be bacause at this moment @close handler of old dialog is not yet processed
                    // and new dialog is cleared immediately by the old dialog @close handler
                    requestAnimationFrame(() => this.dialog = dialog)
                } else {
                    this.dialog = dialog
                }
            },

            attachMedia(action) {
                this.modalAttachMedia = action
            },

            async contextMenuNavigate(block) {
                await this.navigate(block)

                if (this.needHideContextMenu) {
                    this.contextMenu = null
                } else {
                    this.needHideContextMenu = true
                }
            },

            styleByIndex(index) {
                return {
                    position: 'absolute',
                    top: (20 * index) + 'px',
                    left: -(20 * index) + 'px',
                    right: -(20 * index) + 'px',
                    bottom: 0,
                    zIndex: 10000 + index,
                }
            }
        },
    }

</script>
