import Vue from "vue";

const store = Vue.observable({
  screens: [],
  screenByUrl: {}
});

store.set = (url, screen) => {
  store.screenByUrl = Object.freeze({
    ...store.screenByUrl,
    [url]: { ...screen }
  });

  store.screens = Object.values(store.screenByUrl);
};

store.fetch = async (api, url, query) => {
  let resultUrl = url;

  if (query) {
    resultUrl =
      url +
      (url.includes("?") ? "&" : "?") +
      query.param +
      "=" +
      encodeURIComponent(query.query);
  }

  let response = await api.get(resultUrl);

  if (response.appAction && response.appAction.type === "navigate") {
    response = await api.get(response.appAction.url);
  }

  store.set(url, response);

  return response;
};

export default store;
