<template>

    <div class="TextBlock"
         :class="{ __clickable: block.onClick }"
         @click="click"
         @contextmenu="$emit('contextmenu', $event)"
    >
        {{ block.text }}
        <span v-if="block.tokens"
              v-for="token in block.tokens"
              :style="token.s ? style.font(token.s) : null"
        >
            {{ token.v }}
        </span>

        <div v-if="block.blocks && block.blocks.length > 0">
            <component :is="blockListComponent" :blocks="block.blocks" @navigate="navigate"/>
        </div>
    </div>

</template>

<style>

    .TextBlock {
        word-break: break-word;
        white-space: pre-line;
        line-height: 150%;
    }

    .TextBlock.__clickable {
        cursor: pointer;
    }

</style>

<script>

    import style from '@/modules/common/components/presenter/blocks/helper/style.js';
    import { enrichOnContextByPoint } from "@/services/actions";
    import BlockList from "./BlockList";
    import {enrichOnContextByElement} from "../../../../../services/actions";

    export default {
        name: 'text-block',
        components: {BlockList},
        props: {
            block: {
                type: Object,
                required: true,
            },
        },

        data() {
            return {
                blockListComponent: BlockList,
                style,
            }
        },

        methods: {
            click(event) {
                this.$emit('navigate', enrichOnContextByPoint(
                    this.block,
                    Math.round(event.pageX),
                    Math.round(event.pageY)
                ));
            },

            navigate(block) {
                this.$emit('navigate', enrichOnContextByElement(block, this.$el));
            },
        },
    };

</script>
