<template>

    <div class="AudioFile">
        <audio preload="metadata" @timeupdate="timeUpdate" @ended="ended" ref="audio">
            <source :src="file.url" :type="file.mime_type" />
        </audio>

        <div class="AudioFile__controls">
            <div class="AudioFile__controls-left">
                <div class="AudioFile__button" :class="{ __playing: playing }" @click="toggle">
                    <svg class="AudioFile__button-play" width="24" height="24" viewBox="0 0 24 24" fill="currentColor"><path d="M3 22v-20l18 10-18 10z"/></svg>
                    <svg class="AudioFile__button-pause" width="24" height="24" viewBox="0 0 24 24" fill="currentColor"><path d="M11 22h-4v-20h4v20zm6-20h-4v20h4v-20z"/></svg>
                </div>
            </div>
            <div class="AudioFile__controls-right">
                <div class="AudioFile__timeline">
                    <div v-for="tick, index in ticks" class="AudioFile__timeline-tick" :class="{ __highlighted: heightlightIndex >= index }" :style="{ left: (3 * index) + 'px', height: tick * 1.5 + 'px' }"></div>
                </div>
                <div class="AudioFile__time">
                    {{ time }}
                </div>
            </div>
        </div>
    </div>

</template>

<style>

    .AudioFile__controls {
        display: flex;
    }

    .AudioFile__controls-left {
        flex-shrink: 0;
    }

    .AudioFile__controls-right {
        padding-left: 10px;
    }

    .AudioFile__button {
        width: 40px;
        height: 40px;
        border-radius: 40px;
        background-color: #63a3d0;
        color: white;

        display: flex;
        align-items: center;
        justify-content: center;

        cursor: pointer;
    }

    .AudioFile__button-play {
        position: relative;
        left: 3px;
    }

    .AudioFile__button.__playing .AudioFile__button-play {
        display: none
    }

    .AudioFile__button .AudioFile__button-pause {
        display: none;
    }

    .AudioFile__button.__playing .AudioFile__button-pause {
        display: block;
    }

    .AudioFile__timeline {
        position: relative;

        width: 200px;
        height: 20px;
    }

    .AudioFile__timeline-tick {
        width: 2px;
        position: absolute;
        bottom: 0;
        background-color: #bbbbbb;
    }

    .AudioFile__timeline-tick.__highlighted {
        background-color: #63a3d0;
    }

    .AudioFile__time {
        color: #bbbbbb;
        font-size: 13px;
        margin-top: 3px;
    }

</style>

<script>

    export default {

        props: {
            file: {
                type: Object,
                required: true,
            },
        },

        data() {
            return {
                playing: false,
                ticks: [
                    3, 8, 9, 10, 7, 10, 4, 2, 1, 4, 8, 10, 9, 3, 1, 1, 3, 8, 10, 4, 7, 10, 8, 9, 8, 9, 10, 7, 10, 6,
                    10, 4, 7, 10, 8, 9, 8, 9, 10, 7, 10, 6, 3, 8, 9, 10, 7, 10, 4, 2, 1, 4, 8, 10, 9, 3, 1, 1, 3, 1,
                ],
                heightlightIndex: -1,
                time: '0:00',
            };
        },

        methods: {
            toggle() {
                if (!this.playing) {
                    this.$refs.audio.play();
                    this.playing = true;
                } else {
                    this.$refs.audio.pause();
                    this.playing = false;
                }
            },

            timeUpdate(event) {
                const audio = this.$refs.audio;

                this.heightlightIndex = this.ticks.length * audio.currentTime / audio.duration;

                const current = Math.round(audio.currentTime);
                const minutes = Math.floor(current / 60);
                const seconds = current - minutes * 60;
                this.time = minutes + ':' + (seconds < 10 ? '0' + seconds : seconds);
            },

            ended() {
                this.playing = false;
                this.heightlightIndex = -1;
                this.time = '0:00';
            },
        },

    };

</script>
