                       @refresh="refresh"
                       @back="back"
        />
    </presenter-layout>

</template>

<style>

    .FullScreenGallery .GalleryBlock__wrapper {
        height: 100%;
        background: black;
    }

    .FullScreenGallery .GalleryBlock__wrapper .swiper-container {
        height: 100%;
    }

    .FullScreenGallery .swiper-container-horizontal>.swiper-pagination-bullets {
        bottom: auto;
        top: 10px;
    }

</style>

<script>

    import PresenterLayout from '@/modules/common/components/presenter/PresenterLayout';
    import GalleryBlock from "./blocks/GalleryBlock";
    import getComponent from "./blocks/getComponent";

    export default {

        components: {
            GalleryBlock,
            PresenterLayout,
        },

        props: {
            url: {
                type: String,
                default: '',
            },

            block: {
                type: Object,
                required: false,
            },

            canBack: {
                type: Boolean,
                default: false,
            },

            showHeader: {
                type: Boolean,
                default: false,
            },
        },

        methods: {
            navigate(block) {
                this.$emit('navigate', block);
            },

            refresh() {
                this.$emit('refresh');
            },

            back() {
                this.$emit('back');
            },
        },

    };

</script>
