<template>

    <div class="UserFieldRow">
        <div class="UserFieldRow__label">{{ label }}<span v-if="optional" class="UserFieldRow__label-optional">(не обязательно)</span></div>
        <div class="UserFieldRow__input">
            <text-input :value="value" :placeholder="placeholder" @input="input" ref="input" />
        </div>
        <div v-if="description" class="UserFieldRow__description">{{ description }}</div>
    </div>

</template>

<style>

    .UserFieldRow {
        margin-bottom: 20px;
    }

    .UserFieldRow__label {
        font-weight: bold;
        margin-bottom: 5px;
    }

    .UserFieldRow__label-optional {
        font-weight: 100;
        font-size: 13px;
        margin-left: 10px;
        color: silver;
    }

    .UserFieldRow__input {
        margin-bottom: 5px;
    }

    .UserFieldRow__description {
        font-weight: 100;
        color: gray;
    }

</style>

<script>

    import TextInput from "@/modules/common/components/ui/TextInput";

    export default {

        components: {
            TextInput,
        },

        props: {
            field: {
                type: String,
                required: true,
            },

            label: {
                type: String,
                default: null,
            },

            description: {
                type: String,
                default: null,
            },

            default: {
                type: String,
                default: null,
            },

            optional: {
                type: Boolean,
                default: false,
            },

            placeholder: {
                type: String,
                default: '',
            },

            object: {
                type: Object,
                default: () => ({}),
            },
        },

        data() {
            return {
                value: this.default || '',
            }
        },

        methods: {
            input(value) {
                this.value = value
                this.$emit('input', value)
            },

            focus() {
                this.$refs.input.focus()
            },

            async validate() {
                if (this.optional) return true

                return !!this.value
            },

            getField() {
                return this.field
            },

            getValue() {
                return this.value
            },
        },

    }

</script>
