import axios from "axios";
import appConfig from "@/config";
import store from "@/store";
import config from "@/config";

const windowSearchParams = new URLSearchParams(window.location.search);

function getAppAuthHeaders() {
  const xChatiumSignData2El = document.getElementById("x_chatium_sign_data2");
  const xChatiumSignLinkEl = document.getElementById("x_chatium_sign_link");
  const xChatiumProSignDataEl = document.getElementById("x_chatium_pro_sign_data");
  const xChatiumProfileEl = document.getElementById("x_chatium_profile");
  if (xChatiumProSignDataEl && xChatiumSignData2El && xChatiumSignLinkEl) {
    if (xChatiumSignData2El.text && xChatiumSignLinkEl.text) {
      return {
        "x-chatium-sign-data2": xChatiumSignData2El.text,
        "x-chatium-sign-link": xChatiumSignLinkEl.text,
        "x-chatium-pro-sign-data": xChatiumProSignDataEl.text,
        "x-chatium-profile": xChatiumProfileEl.text
      };
    }
  }
}

const appAuthHeaders = getAppAuthHeaders();

const getCookie = function(name) {
  const value = "; " + document.cookie;
  const parts = value.split("; " + name + "=");

  return parts.length === 2
    ? parts
      .pop()
      .split(";")
      .shift()
    : null;
};

const storeTokensFromResponse = function(data) {
  store.accessToken = data.access_token;
  store.refreshToken = data.refresh_token;
  store.expireAt = data.expire_at;

  localStorage.setItem("accessToken", data.access_token);
  localStorage.setItem("refreshToken", data.refresh_token);
  localStorage.setItem("expireAt", data.expire_at);
};

const restoreTokensFromLocalStore = function() {
  store.accessToken = localStorage.getItem("accessToken");
  store.refreshToken = localStorage.getItem("refreshToken");
  store.expireAt = localStorage.getItem("expireAt");
};

const migrationTokenParamName = "migrationToken";
let migrationTokenFailCounter = 5;

// reading migration token once during page initialization
// it can be passed via query param or via cookie
let migrationToken = null;
const queryParams = new URLSearchParams(window.location.search);
if (queryParams.get(migrationTokenParamName)) {
  // remember the token passed via URL query and immediately clear it from the location
  migrationToken = queryParams.get(migrationTokenParamName);
  queryParams.delete(migrationTokenParamName);
  const queryStr = queryParams.toString();
  history.replaceState(history.state, '', window.location.pathname + (queryStr && ("?" + queryStr)));
} else {
  migrationToken = getCookie("migrationToken");
}

async function migrateIfNeeded() {
  if (!migrationToken) {
    return false;
  }

  try {
    const migrationResponse = await instance.request({
      url: "/auth/migrate/token",
      method: "post",
      data: {
        migration_token: migrationToken,
        user_agent: navigator.userAgent
      }
    });

    if (migrationResponse.data.success) {
      if (
        migrationResponse.data.access_token &&
        migrationResponse.data.refresh_token &&
        migrationResponse.data.expire_at
      ) {
        console.log("👍 Migration successful");
        storeTokensFromResponse(migrationResponse.data);
        migrationToken = null;
        return true;
      }
    }
  } catch (error) {
    console.log("👎 Migration fail", error);
  }

  // give up after several failures
  if (migrationTokenFailCounter-- < 1) {
    migrationToken = null;
  }

  return false;
}

async function refreshIfNeeded() {
  // 5s gap to prevent failure on server-side during request
  if (store.expireAt && store.expireAt < Date.now() - 5000) {
    await refresh();
  }
}

let refreshListners = [];
let refreshing = false;

async function refresh() {
  if (refreshing) {
    await new Promise((resolve, reject) => {
      refreshListners.push({ resolve, reject });
    });

    return;
  }

  refreshing = true;
  try {
    console.log("🙏 Trying refresh tokens");

    // Синхронизируемся на случай нескольких вкладок
    restoreTokensFromLocalStore();

    try {
      // Пробуем получить новый токен
      const refreshResponse = await instance.request({
        url: "/auth/refresh",
        method: "post",
        data: {
          access_token: store.accessToken,
          refresh_token: store.refreshToken
        }
      });

      if (refreshResponse.data.success) {
        if (refreshResponse.data.access_token && refreshResponse.data.refresh_token && refreshResponse.data.expire_at) {
          storeTokensFromResponse(refreshResponse.data);
          console.log("👍 Refresh successful");
          refreshListners.forEach(promise => promise.resolve());

          // если пришло __maintenance_mode__, то это интерфейс миграций бекенда,
          // пропускаем как есть, там утентификация не нужна
        } else if (!refreshResponse.data.__maintenance_mode__) {
          console.log("👎 Refresh fail, empty tokens");
          refreshListners.forEach(promise => promise.reject());

          store.needAuth = true;

          return Promise.reject("Refresh fail, empty tokens");
        }
      }
    } catch (error) {
      console.log("👎 Refresh fail", error);
      refreshListners.forEach(promise => promise.reject());

      store.needAuth = true;

      return Promise.reject(error);
    }
  } finally {
    refreshing = false;
    refreshListners = [];
  }
}

const instance = axios.create();

instance.interceptors.request.use(
  function(config) {
    config.baseURL = `${location.protocol}//${location.hostname}:5000`;

    if (appAuthHeaders) {
      config.headers = {
        ...appAuthHeaders,
        ...config.headers
      };
    } else if (store.accessToken) {
      config.headers.common["Authorization"] = "Bearer " + store.accessToken;
    }

    config.headers.common["accept"] = "application/chatium.v1+json";
    config.headers.common["x-chatium-platform"] = "web";
    config.headers.common["x-chatium-version"] = appConfig.build.hash;

    config.requestStartTime = Date.now();

    return config;
  },
  function(error) {
    if (appConfig.env === "local") {
      console.error("Request error", error);
    }

    return Promise.reject(error);
  }
);

instance.interceptors.response.use(
  function(response) {
    response.config.requestTime = Date.now() - response.config.requestStartTime;
    return response;
  },
  function(error) {
    if (appConfig.env === "local") {
      console.error("Response error", error);
    }

    return Promise.reject(error);
  }
);

export const api = {
  async request(method, url, payload, options) {
    console.log('REQUEST', method, url)
    const params = {
      url: url,
      method: method
    };

    // if (url.startsWith('http://') || url.startsWith('https://')) {
    //   try {
    //     const parsedUrl = new URL(url)
    //     if (parsedUrl.host !== location.host) {
    //       params.url = `${config.backend.socket_url}/api/2.0/proxy/${url}`
    //       params.baseURL = ''
    //     }
    //   } catch (_) {
    //     // Ничего не делаем
    //   }
    // }

    params.withCredentials = false;

    if (method === "get" || method === "delete") {
      params.params = payload;
      if (params.params) {
        params.params.ccc = Date.now();
      } else {
        params.params = {
          ccc: Date.now()
        };
      }
    } else {
      params.data = payload;
    }

    try {
      const response = await instance.request(params);

      console.log("Request", method, url, response.config.requestTime, "ms");

      return response.data;
    } catch (error) {
      if (options && options.skipErrors) {
        return Promise.reject(error);
      }

      if (error.response) {
        if (error.response.status === 401) {
          // Принудительно пытаемся обновить токены
          await refresh();

          // И повторно отправляем запрос
          try {
            const response = await instance.request(params);
            return response.data;
          } catch (error) {
            if (error.response.status === 401) {
              if (method === 'get') {
                return Promise.resolve(error.response);
              } else {
                store.needAuth = true;
              }
            }

            return Promise.reject(error);
          }
        }

        if (error.response.status === 424) {
          store.needUserData = true;
          store.requestedUserData = error.response.data.fields;
          store.requestedAccount = error.response.data.account;
          store.requestedAuth = error.response.data.auth;
        }
      }

      return Promise.reject(error);
    }
  },

  async get(url, payload, options) {
    return this.request("get", url, payload, options);
  },

  async post(url, payload) {
    return this.request("post", url, payload);
  },

  async delete(url, payload) {
    return this.request("delete", url, payload);
  }
};

export const Api = {
  install: (Vue, options) => Vue.prototype.$api = api,
}
