import config from "@/config";

export default {
  getFsThumbnailUrl(hash, width, height) {
    if (!hash) {
      return null;
    }
    return `${config.fileStorage.url}/fileservice/file/thumbnail/h/${hash}/s/${width ? width : ""}x${
      height ? height : ""
    }`;
  },

  getDownloadUrl(hash) {
    return `${config.fileStorage.url}/fileservice/file/download/h/${hash}`;
  },

  mediumThumbnailUrl(hash) {
    return this.getFsThumbnailUrl(hash, 800);
  },

  bigThumbnailUrl(hash) {
    return this.getFsThumbnailUrl(hash, 1440);
  },

  normalSquare(hash) {
    return this.getFsThumbnailUrl(hash, 100, 100);
  },

  getSizesFromHash(hash) {
    if (!hash) {
      return { width: null, height: null };
    }
    let parts = hash.split(".");
    if (parts && parts[1]) {
      let size = parts[1];
      let sizeParts = size.split("x");
      if (sizeParts.length === 2) {
        return {
          width: sizeParts[0],
          height: sizeParts[1]
        };
      }
    }
  }
};
