<template>

    <presenter-layout class="AuthPresenter" :show-header="true" :url="url">
        <template slot="header-content-title">Авторизация</template>

        <request-page :url="url"
                      @navigate="$emit('navigate', $event)"
                      @confirmed="confirmed"
        />
    </presenter-layout>

</template>

<style>

    .AuthPresenter {
        background-color: white;
        min-height: 100%;
    }

</style>

<script>

    import RequestPage from "@/modules/auth/pages/RequestPage"

    export default {

        components: {
            RequestPage
        },

        props: {
            url: {
                type: String,
                default: null,
            },
        },

        methods: {
            confirmed() {
                this.$root.$emit('authoraized')
                this.$emit('refresh')
            },
        },

    }

</script>
