<template>

    <div class="ButtonBlock" :class="{ __unsupported: unsupported }">
        <button type="button"
                class="ButtonBlock__button"
                :class="classes"
                :style="styles"
                @click="click"
                @contextmenu="$emit('contextmenu', $event)"
                ref="button"
        >
            {{ block.title }}
        </button>
    </div>

</template>

<style>

    .ButtonBlock {
        position: relative;
    }

    .ButtonBlock.__unsupported {
        display: none;
    }

    .ButtonBlock__button {
        cursor: pointer;
        font-size: 15px;
        padding: 8px;
        outline: none;
        border-radius: 5px;
        width: 100%;
        background-color: #0379fe;
        border: 1px solid #0379fe;
        color: white;
    }

    .ButtonBlock__button.__flat {
        background-color: transparent;
        border: 1px solid #000000;
        color: #000000;
    }

</style>

<script>

    import { enrichOnContextByElement } from "@/services/actions";

    export default {

        props: {
            block: {
                type: Object,
                required: true,
            },
        },

        data() {
            return {
                disabled: false,
            }
        },

        computed: {
            classes() {
                return {
                    __flat: this.block.buttonType === 'flat',
                }
            },

            styles() {
                const flat = this.block.buttonType === 'flat'

                return {
                    backgroundColor: flat ? 'transparent' : this.block.bgColor,
                    borderColor: flat ? undefined : this.block.bgColor,
                    color: this.block.fontColor,
                    fontWeight: this.block.isBold ? 'bold' : 'normal',
                    fontSize: this.block.fontSize,
                    display: this.disabled ? "none" : "block"
                };
            },

            unsupported() {
                return this.block.onClick && this.block.onClick.type === 'selectContacts'
            },
        },

        methods: {
            click() {
                this.navigate(this.block);
            },

            navigate(block) {
                this.$emit('navigate', enrichOnContextByElement(block, this.$refs.button));
            },
        },

    };

</script>
