<template>

    <div class="Presenter" :class="{ ['__layout-' + layout]: true }" @click="$emit('click')">
        <component v-if="customScreen === 'presenter'"
                   :is="component" ref="presenter"
                   :block="data"
                   :can-back="canBackWithUrl"
                   :highlight-url="highlightUrl"
                   :show-header="showHeader"
                   :url="url"
                   @navigate="navigate"
                   @refresh="refresh"
                   @query="query"
                   @back="back"
        />

        <presenter-layout v-if="customScreen === 'user-profile'"
                          class="Presenter__custom-screen"
                          :show-header="showHeader"
        >
            <template slot="header-content-title">Профиль пользователя</template>

            <profile-page @navigate="navigate" />
        </presenter-layout>

        <presenter-layout v-if="customScreen === 'user-auth'"
                          class="Presenter__custom-screen"
                          :show-header="showHeader"
        >
            <template slot="header-content-title">Авторизация</template>

            <request-page @navigate="navigate" />
        </presenter-layout>
    </div>

</template>

<style>

    .Presenter {
        background-color: white;
        border-radius: 5px;

        width: 100%;
        min-height: 100%;
    }

    .Presenter.__layout-fixed {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
    }

</style>

<script>

    import ProfilePage from "@/modules/auth/pages/ProfilePage";
    import RequestPage from "@/modules/auth/pages/RequestPage";
    import socket from '@/socket';
    import screenStore from '@/store/screen';

    import ChatPresenter from './presenter/ChatPresenter';
    import BlocksPresenter from './presenter/BlocksPresenter';
    import FullScreenGalleryPresenter from './presenter/FullScreenGalleryPresenter';
    import WebPresenter from './presenter/WebPresenter';
    import PaymentPresenter from './presenter/PaymentPresenter';
    import ErrorPresenter from './presenter/ErrorPresenter';
    import PermissionPresenter from './presenter/PermissionPresenter';
    import NotFoundPresenter from './presenter/NotFoundPresenter';
    import LoadingPresenter from './presenter/LoadingPresenter';
    import AuthPresenter from './presenter/AuthPresenter';

    export default {
        components: {
            RequestPage,
            ProfilePage
        },

        props: {
            url: {
                type: String,
                default: null,
            },

            screen: {
                type: Object,
                default: null,
            },

            highlightUrl: {
                type: String,
                default: null,
            },

            canBack: {
                type: Boolean,
                default: false,
            },

            showHeader: {
                type: Boolean,
                default: false,
            },
        },

        data() {
            return {
                fetched: false,
                error: null,
                permissionDenied: false,
                notFound: false,
                socketDisposer: null,
                queryData: null,
            };
        },

        computed: {
            storedScreen() {
                return this.url ? screenStore.screenByUrl[this.url] : undefined;
            },
            // если стек сказал что можно назад, или текущий блок дал урл по которому можно вернуться
            canBackWithUrl() {
                if ( this.canBack ) {
                    return true;
                }
                if ( this.data && this.data.backUrl ) {
                    return true;
                }
                return false;
            },

            data() {
                // Если передан уже загруженный экран, то отображаем его
                if (this.screen && !this.fetched) {
                    return this.screen;
                }

                // Потом смотрим, есть ли скрин в кеше
                return this.storedScreen ? this.storedScreen.data : null;
            },

            component() {
                if (this.error) {
                    if (this.permissionDenied) {
                        return PermissionPresenter;
                    } else if (this.notFound) {
                        return NotFoundPresenter;
                    } else {
                        return ErrorPresenter;
                    }
                }
                else if (this.data) {
                    if (this.data.statusCode === 401) {
                        return AuthPresenter;
                    } else if (this.data.needChatiumPro) {
                        return PaymentPresenter;
                    } else if (this.data.chat) {
                        return ChatPresenter;
                    } else if (this.data.fullScreenGallery) {
                        return FullScreenGalleryPresenter;
                    } else if (this.data.webview) {
                        return WebPresenter;
                    } else if (this.data.blocks) {
                        return BlocksPresenter;
                    }

                    return ErrorPresenter;
                }

                return LoadingPresenter;
            },

            layout() {
                let layout = this.data && this.data.layout
                    ? this.data && this.data.layout
                    : 'fixed'

                if (this.data && this.data.footer) {
                    layout = 'fixed'
                }

                return layout
            },

            path() {
                try {
                    const url = new URL(this.url)
                    return url.pathname
                } catch (e) {
                    return this.url
                }
            },

            customScreen() {
                return this.path === '/user/profile'
                    ? 'user-profile'
                    : this.path === '/user/auth'
                        ? 'user-auth'
                        : 'presenter'
            },
        },

        watch: {
            data() {
                this.updateSubscriptions()
            },
        },

        methods: {
            updateSubscriptions() {
                if (this.socketDisposer) {
                    this.socketDisposer();
                }

                if (this.data) {
                    if (this.data.title) {
                        this.$emit('title', this.data.title)
                    }

                    this.error = null;

                    if (this.data.socketId) {
                        this.socketDisposer = socket.subscribeToSocket(
                            this.data.socketId,
                            () => this.fetch(),
                            this.url === 'inbox' ? 1000 : 0,
                        );
                    }

                    if (this.data.markAsReadUrl && this.component !== ChatPresenter) {
                        this.$api.post(this.data.markAsReadUrl);
                    }
                }
            },

            navigate(block) {
                this.$emit('navigate', block);
            },

            refresh() {
                this.fetch();
            },

            back() {
                if (this.data.backUrl ) {
                    this.$emit('navigate', {
                        onClick: {
                            type: 'navigate',
                            url: this.data.backUrl
                        }
                    });

                    return;
                }
                this.$emit('back');
            },

            unknownAction(action) {
                if (this.$refs.presenter.unknownAction) {
                    this.$refs.presenter.unknownAction(action)
                }
            },

            /**
             * Получает данные по апи и кеширует их
             */
            async fetch() {
                if (this.socketDisposer) {
                    this.socketDisposer();
                    this.socketDisposer = null;
                }

                if (!this.url) {
                    return;
                }

                try {
                    const response = await this.screenStoreFetch();

                    this.fetched = true
                    this.error = null;
                    this.permissionDenied = false;
                    this.notFound = false;
                    this.$emit('loaded', response);
                } catch (error) {
                    if (error.response && error.response.status === 403) {
                        this.permissionDenied = true;
                    } else if (error.response && error.response.status === 404) {
                        this.notFound = true;
                    }
                    this.error = error;
                }
            },

            /**
             * Low-level re-fetch that allows retry logic
             * Used by development mode hot-reloader
             */
            screenStoreFetch() {
                if (this.url) {
                    return screenStore.fetch(this.$api, this.url, this.queryData);
                }
            },

            query(data) {
                this.queryData = data;
                this.fetch();
            },

            resetSearch() {
                if (this.$refs.presenter && this.component === BlocksPresenter) {
                    this.$refs.presenter.resetSearch();
                }
            },
        },

        beforeMount() {
            const screen = screenStore.screenByUrl[this.url];

            // Принудительно отключаю кеширование для страниц с поиском,
            // так как очень сложно обновлять UI
            if (screen && screen.data && screen.data.search) {
                screenStore.set(this.url, null);
            }
        },

        mounted() {
            if (this.screen) {
                this.updateSubscriptions()
            } else {
                this.fetch();
            }
        },

        beforeDestroy() {
            if (this.socketDisposer) {
                this.socketDisposer();
            }
        },

    };

</script>
