<template>

    <input class="TextInput" :class="{ __error: error }"
           type="text"
           :value="value"
           :placeholder="placeholder"
           @input="$emit('input', $event.target.value)"
           @keypress="$emit('keypress', $event)"
           ref="input"
    />

</template>

<style>

    .TextInput {
        padding: 8px 15px;
        font: inherit;
        outline: none;
        border-radius: 3px;
        border: 1px solid silver;
        width: 100%;
    }

    .TextInput::placeholder {
        color: silver;
        font-weight: 100;
    }

    .TextInput:focus {
        border-color: rgba(2, 121, 254, 0.38);
        box-shadow: 0 0 0 2px rgba(2, 121, 254, 0.38);
    }

    .TextInput.__error {
        background-color: #fcf4f4;
        border: 1px solid #fe7676;
    }

    .TextInput.__error:focus {
        border-color: rgba(254, 2, 57, 0.38);
        box-shadow: 0 0 0 2px rgba(254, 2, 57, 0.38);
    }

</style>

<script>

    export default {

        props: {
            value: {
                type: String,
                default: '',
            },

            error: {
                type: Boolean,
                default: false,
            },

            autofocus: {
                type: Boolean,
                default: false,
            },

            placeholder: {
                type: String,
                default: '',
            },
        },

        computed: {
            listners() {
                return this.$listeners
            },
        },

        methods: {
            focus() {
                this.$refs.input.focus()
            },
        },

        mounted() {
            if (this.autofocus) {
                this.$nextTick(() => this.focus())
            }
        },

    }

</script>
