<template>
    <div class="VideoBlock" @contextmenu="$emit('contextmenu', $event)">
        <div :style="spacerOuterStyle">
            <div class="VideoPlaceholder" :class="spacerInnerClass" :id='playerElementId'></div>
        </div>
    </div>
</template>

<style>
    .VideoBlock {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        width: 100%;
    }

    .SpacerInner {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
    }

    .SpacerInner > div:first-child {
        height: 100%;
    }

    .VideoPlaceholder {
        background: black;
    }
</style>

<script>
    export default {
        data() {
            return {
                player: null,
            };
        },

        props: {
            block: {
                type: Object,
                required: true,
            },
        },

        mounted() {
            const integrosPlayer = document.createElement("script");
            integrosPlayer.setAttribute(
                "src",
                "https://static.cdn.integros.com/player/latest/integros_player.js"
            );
            document.head.appendChild(integrosPlayer);

            this.displayVideoPlayer(this.block);
        },

        computed: {
            file() {
                if ( this.block.file ) {
                    return this.block.file;
                }
                // для старых версий
                if ( this.block.videoHash ) {
                    this.block.hash = this.block.videoHash;
                    return this.block;
                }
                return {}
            },
            spacerOuterStyle() {
                if (this.block.videoAspectRatio) {
                    return {
                        paddingBottom: this.getVideoSpacerOuterPaddingBottom(),
                        width: '100%',
                        position: 'relative'
                    };
                }
                return {};
            },
            spacerInnerClass() {
                if (this.block.videoAspectRatio) {
                    return [ 'SpacerInner' ];
                }
                return [];
            },
            playerElementId() {
                return "video-player-" + this.block.key.replace(" ", "-");
            },
        },

        methods: {

            displayVideoPlayer(block) {
                // Have to wait for the Integros player resource to load into
                // the page before can display the player
                if (undefined === window.Integros) {
                    window.setTimeout(
                        () => this.displayVideoPlayer(block),
                        1000
                    );
                    return;
                }

                let videoOptions = {};
                if ( block.hlsUrl ) {
                    videoOptions.hls = block.hlsUrl;
                }
                else {
                    videoOptions.hls = block.url;
                }
                if ( block.mp4Url ) {
                    videoOptions.mp4 = block.mp4Url;
                }


                const playerOptions = {
                    title: "",
                    subtitle: "",
                    width: '100%',
                    height: '100%'
                };

                this.player = Integros.Player.create(this.playerElementId, videoOptions, playerOptions)
            },

            getVideoSpacerOuterPaddingBottom() {
                if (this.block.videoAspectRatio[1] > this.block.videoAspectRatio[0]) {
                  // Square block for the vertical video
                  return '100%';
                } else {
                  return this.block.videoAspectRatio[1] / this.block.videoAspectRatio[0] * 100 + '%';
                }
            },
        }
    };
</script>
