export function normalizePhone(phone) {
  const result = phone.replace(/\D/g, "");

  if (result.length === 10) {
    return "7" + result;
  }

  if (result.length === 11 && result.slice(0, 1) === "8") {
    return "7" + result.slice(1);
  }

  return result;
}

export function humanizePhone(phone) {
  phone = normalizePhone(phone);

  if (phone.length === 11 && phone[0] === "7") {
    return (
      "+" +
      phone.slice(0, 1) +
      " " +
      phone.slice(1, 4) +
      " " +
      phone.slice(4, 7) +
      "-" +
      phone.slice(7, 9) +
      "-" +
      phone.slice(9, 11)
    );
  }

  return phone;
}
