import Vue from "vue";

const store = Vue.observable({
  authKey: localStorage.getItem("authKey") || "",
  accessToken: localStorage.getItem("accessToken") || null,
  refreshToken: localStorage.getItem("refreshToken") || null,
  expireAt: parseInt(localStorage.getItem("expireAt")) || 0,
  token: null,
  host: null,
  url: '/',

  user: null,
  blocks: [],
  favorites: [],
  visibility: "visible",
  needAuth: false,
  needUserData: false,
  requestedUserData: [],
  requestedAccount: {},
  requestedAuth: {},
  clipboard: {},
  globalSearchScreen: null,
  uploadingFiles: [],
  accountId: 0,

  transparent: true,

  showBanner: true,

  layout: 'mobile',
});

store.setBlocks = blocks => {
  blocks.forEach(block => {
    const found = store.blocks.find(
      storeBlock => store.onClick && block.onClick && store.onClick.url === block.onClick.url
    );
    if (!found) {
      store.blocks.push(block);
    }
  });
};

store.getAccountUrl = function(accountId, url) {
  return location.protocol + "//" + location.host + url;
};

store.addToClipboard = function(key, value) {
  this.clipboard[key] = JSON.stringify(value);
};
store.existsInClipboard = function(key) {
  return this.clipboard[key];
};
store.getFromClipboard = function(key) {
  return JSON.parse(this.clipboard[key]);
};

export default store;
