<template>

    <div class="ApplicationLayout" :class="{ __mobile: mobile }">
        <router-view />
        <portal-target name="global-modal" />
    </div>

</template>

<style>

    html, body {
        height: 100%;
        overflow-x: hidden;
    }

    body {
        margin: 0;
        padding: 0;
    }

    * {
        box-sizing: border-box;
    }

    .ApplicationLayout {
        font-size: 15px;
        font-family: Helvetica, Arial, sans-serif;

        height: 100%;
    }

    .ApplicationLayout.__mobile {
        height: auto;
    }

</style>

<script>

    import store from "@/store";
    import cache from "@/cache";
    import socket from "@/socket";
    import config from "@/config";

    export default {

        computed: {
            accessToken() {
                return store.accessToken;
            },

            user() {
                return store.user;
            },

            userUid() {
                return store.user ? store.user.uid : null;
            },

            unreadCount() {
                return store.user ? store.user.unreadChatsCount : 0;
            },

            favorites() {
                return store.favorites;
            },

            mobile() {
                return store.layout === 'mobile'
            },
        },

        methods: {
            async updateTokenFromStore() {
                if (store.accessToken) {
                    socket.setToken('jwt@' + store.accessToken);
                }
            },

            visabilityChange() {
                store.visibility = document.visibilityState;
            },
        },

        watch: {
            accessToken() {
                this.updateTokenFromStore();
            },

            unreadCount() {
                const icon = document.getElementById('icon');

                if (this.unreadCount > 0) {
                    icon.href = '/chatium-notification-blue.png';
                } else {
                    icon.href = '/chatium.png';
                }
            },

            favorites() {
                localStorage.setItem('__favorites', JSON.stringify(store.favorites));
            },
        },

        async mounted() {
            await this.updateTokenFromStore();

            document.addEventListener('visibilitychange', this.visabilityChange);

            window.dev = {
                api: this.$api,
                router: this.$router,
                store: store,
                cache: cache,
                socket: socket,
                config: config,
            }
        },

        beforeDestroy() {
            document.removeEventListener('visibilitychange', this.visabilityChange);
        },

    };

</script>
