<template>

    <div class="LinkToApplicationBlock" @contextmenu="$emit('contextmenu', $event)">
        <PrimaryButton class="LinkToApplicationBlock__button" @click="click" >
            {{ block.title }}
        </PrimaryButton>
        <div class="LinkToApplicationBlock__qr">
            <img class="LinkToApplicationBlock__qr-image" :src="block.imageUrl"/>
            <div class="LinkToApplicationBlock__qr-message">Отсканируйте QR-код приложением Chatium</div>

        </div>
    </div>

</template>

<style>
    .LinkToApplicationBlock {

    }
    .LinkToApplicationBlock__button {
        width: 100%;
    }
    .LinkToApplicationBlock__qr {
        text-align: center;

    }
    .LinkToApplicationBlock__qr-image {
        width: 200px;

    }
    .LinkToApplicationBlock__qr-message {
        padding-top: 10px;
        padding-bottom: 10px;
    }
    @media (max-width: 767px) {
        .LinkToApplicationBlock__qr {
            display: none;
        }
        .LinkToApplicationBlock__button {
            display: block;
        }
    }
    @media (min-width: 767px) {
        .LinkToApplicationBlock__qr {
            display: block;
        }
        .LinkToApplicationBlock__button {
            display: none;
        }
    }
</style>

<script>
    import PrimaryButton from "../../ui/PrimaryButton";
    import {enrichOnContextByElement} from "../../../../../services/actions";
    export default {
        components: {PrimaryButton},
        props: {
            block: {
                type: Object,
                required: true,
            },
        },

        data() {
            return {
                disabled: false,
            }
        },

        computed: {},

        methods: {
            click() {
                this.navigate({
                    onClick: {
                        type: 'navigate',
                        url: this.block.url,
                        replace: true
                    }
                })
            },

            navigate(block) {
                this.$emit('navigate', enrichOnContextByElement(block, this.$el));
            },
        },
    }
</script>
