<template>

    <form class="RequestPage" @submit.prevent="submit">
        <div class="RequestPage__logo">
            <img src="/chatium.png" alt="Chatium" width="64" height="64">
        </div>
        <div class="RequestPage__header">Номер телефона</div>
        <div class="RequestPage__input-wrapper">
            <input class="RequestPage__input __key"
                   v-model="authKey"
                   :disabled="requested"
                   type="text"
                   placeholder="+7 999 999-99-99"
                   autofocus
                   ref="inputKey"
            />
        </div>
        <div v-if="phoneError" class="RequestPage__description __error">Неверно указан телефонный номер</div>
        <div v-if="!requested" class="RequestPage__description">Мы вышлем СМС с кодом для <br> входа в приложение</div>

        <div v-if="requested" class="RequestPage__input-wrapper">
            <input class="RequestPage__input __code"
                   v-model="authCode"
                   type="text"
                   placeholder="0000"
                   autocomplete="one-time-code"
                   autofocus
                   ref="inputCode"
            />
        </div>
        <div v-if="codeError" class="RequestPage__description __error">Неверно указан код</div>
        <div v-if="requested" class="RequestPage__description">Мы выслали СМС с кодом для входа</div>

        <div class="RequestPage__action-wrapper">
            <primary-button v-if="!requested" class="RequestPage__button" type="submit" :loading="requesting">Получить код для входа</primary-button>
            <primary-button v-if="requested" class="RequestPage__button" type="submit" :loading="confirming">Войти в приложение</primary-button>
        </div>

        <div v-if="url" class="RequestPage__qr-code">
            <div class="RequestPage__qr-code-description">
                Поставьте на телефон <br>
                приложение <img class="RequestPage__qr-code-description-logo" src="/img/logos/logo_round.png" alt="" width="16" height="16"> <strong>Chatium</strong> <br>
                и отсканируйте в нем qr-код <br>
            </div>
            <img :src="qrImage"
                 width="200"
                 height="200"
            >
        </div>
        <div v-if="url" class="RequestPage__application-button">
            <div class="RequestPage__application-button-or">или</div>
            <primary-button class="RequestPage__button" type="button" @click="openInApplication">Открыть в приложении <img class="RequestPage__qr-code-description-logo" src="/img/logos/logo_round.png" alt="" width="16" height="16"></primary-button>
        </div>
    </form>

</template>

<style>

    .RequestPage {
        max-width: 100%;
        padding: 50px 40px 0 40px;

        text-align: center;
    }

    .RequestPage__logo {
        margin-bottom: 40px;
    }

    .RequestPage__header {
        font-size: 18px;
        font-weight: bold;
    }

    .RequestPage__input-wrapper {
        margin-bottom: 20px;
    }

    .RequestPage__input {
        font: inherit;
        font-size: 18px;
        border: 0;
        border-bottom: 1px solid silver;
        border-radius: 0;
        outline: none;

        padding: 10px 0;

        width: 300px;
        text-align: center;
    }

    .RequestPage__input:disabled {
        border: none;
        color: gray;
    }

    .RequestPage__input::placeholder {
        font: inherit;
        color: silver;
    }

    .RequestPage__input.__key {
        max-width: 180px;
    }

    .RequestPage__input.__code {
        font-size: 42px;
        font-weight: bold;
        letter-spacing: 5px;
        padding-left: 10px;
        max-width: 180px;
    }

    .RequestPage__button {
        min-width: 300px;
    }

    .RequestPage__description {
        font-size: 12px;
        -webkit-font-smoothing: antialiased;

        margin-bottom: 40px;
    }

    .RequestPage__description.__error {
        color: red;
        margin-bottom: 10px;
    }

    .RequestPage__qr-code {
        margin-top: 40px;
    }

    .RequestPage__qr-code-description-logo {
        position: relative;
        top: 3px;
    }

    .RequestPage__application-button {
        margin-top: 20px;
        display: none;
    }

    .RequestPage__application-button-or {
        margin-bottom: 20px;
    }

    @media screen and (max-width: 600px) {

        .RequestPage__qr-code {
            display: none;
        }

        .RequestPage__application-button {
            display: block;
        }

    }

</style>

<script>

    import store from '@/store';
    import { normalizePhone, humanizePhone } from "@/services/text";
    import PrimaryButton from "@/modules/common/components/ui/PrimaryButton";

    export default {

        components: {
            PrimaryButton
        },

        props: {
            url: {
                type: String,
                default: null,
            },
        },

        data() {
            return {
                authKey: humanizePhone(normalizePhone(store.authKey)),
                authCode: '',
                requesting: false,
                requested: false,
                confirming: false,

                phoneError: false,
                codeError: false,
            };
        },

        computed: {
            qrImage() {
                return `https://app.chatium.io/api/1.0/qr?text=${this.url}`
            },
        },

        watch: {
            code() {
                if (this.authCode.length === 4) {
                    this.submitCode();
                }
            },
        },

        methods: {
            submit() {
                if (this.requested) {
                    this.submitCode()
                } else {
                    this.submiKey()
                }
            },

            async submiKey() {
                const authKey = normalizePhone(this.authKey);

                if (authKey.length !== 11) {
                    this.phoneError = true
                    return false;
                }

                this.phoneError = false
                this.authKey = humanizePhone(authKey)

                this.requesting = true
                try {
                    const response = await this.$api.post('/auth/credentials', { phone: authKey });

                    if (response.success) {
                        store.authKey = this.authKey;
                        localStorage.setItem('authKey', this.authKey);

                        this.requested = true
                        this.$nextTick(() => this.$refs.inputCode.focus())
                    } else {
                        this.phoneError = true;
                    }
                } finally {
                    this.requesting = false
                }
            },

            async submitCode() {
                const authCode = this.authCode.replace(/\D/g, "")

                if (authCode.length !== 4) {
                    this.codeError = true
                    return false;
                }

                this.codeError = false

                this.confirming = true
                try {
                    const response = await this.$api.post('/auth/token', {
                        phone: localStorage.getItem('authKey'),
                        code: authCode,
                        user_agent: navigator.userAgent
                    });

                    const tokenRefreshedSuccessful = response.success === true
                        && response.access_token
                        && response.refresh_token
                        && response.expire_at;

                    if (tokenRefreshedSuccessful) {
                        store.accessToken = response.access_token;
                        store.refreshToken = response.refresh_token;
                        store.expireAt = response.expire_at;

                        localStorage.setItem('accessToken', response.access_token);
                        localStorage.setItem('refreshToken', response.refresh_token);
                        localStorage.setItem('expireAt', response.expire_at);

                        if (this.$listeners.confirmed) {
                            this.$emit('confirmed')
                        } else {
                            await this.$router.back()
                        }
                    } else {
                        this.codeError = true
                    }
                }  finally {
                    this.confirming = false
                }
            },

            openInApplication() {
                this.$emit('navigate', {
                    onClick: {
                        type: 'navigate',
                        url: 'https://app.chatium.io/applink?autoOpen=0&link=' + encodeURIComponent(this.url)
                    }
                })
            },
        },

        mounted() {
            this.$refs.inputKey.focus();
        },

    };

</script>
