const store = {
  // [url]: {data},
};

const cache = {
  has: key => !!store[key],
  get: key => store[key],
  set: (key, value) => {
    store[key] = value;
    // localStorage.setItem("cache__v1__" + key, JSON.stringify(value));
  },

  init: () => {
    for (let i = 0; i < localStorage.length; i++) {
      let key = localStorage.key(i);
      let matched = /^cache__v1__(.*)/.exec(key);
      if (matched !== null) {
        store[matched[1]] = localStorage.getItem(key);
      }
    }
  }
};

// cache.init();

export default cache;
