<template>

    <div class="PresenterLayout">
        <div v-if="isAndroid && store.showBanner" class="PresenterLayout__android">
            <div class="PresenterLayout__android-close" @click="store.showBanner = false">
                <svg width="14" height="14" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" fill="currentColor">
                    <path d="M12 11.293l10.293-10.293.707.707-10.293 10.293 10.293 10.293-.707.707-10.293-10.293-10.293 10.293-.707-.707 10.293-10.293-10.293-10.293.707-.707 10.293 10.293z"/>
                </svg>
            </div>
            <div class="PresenterLayout__android-logo">
                <img src="/img/logos/logo_round.png" width="50" height="50" />
            </div>
            <div class="PresenterLayout__android-name">
                <div class="PresenterLayout__android-name-app">Chatium</div>
                <div class="PresenterLayout__android-name-company">ООО Платформа Чатиум</div>
            </div>
            <div class="PresenterLayout__android-navigate" @click="navigatePlayMarket">
                ОТКРЫТЬ
            </div>
        </div>
        <div v-if="showHeader && !hasHeaderBlocks"
             class="PresenterLayout__header"
             :class="{ __clickable: hasDropdownBlocks, __image: hasHeaderBlocks }"
             :style="{ backgroundImage: hasHeaderBlocks ? `url(${block.header.image.downloadUrl})` : null }"
        >
            <slot name="header-left">
                <div class="PresenterLayout__header-left">
                    <div v-if="canBack" class="PresenterLayout__header-back-wrapper" @click="$emit('back')">
                        <svg class="PresenterLayout__header-back" width="18" height="18" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor">
                            <path d="M20 .755l-14.374 11.245 14.374 11.219-.619.781-15.381-12 15.391-12 .609.755z"/>
                        </svg>
                    </div>
                </div>
            </slot>
            <div class="PresenterLayout__header-content" @click="toggleDropdown">
                <slot name="header-content">
                    <div class="PresenterLayout__header-title">
                        <slot name="header-content-title"></slot>

                        <div v-if="hasDropdownBlocks" class="PresenterLayout__dropdown-blocks-icon" :class="{ __open: showDropdownBlocks }">
                            <svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" viewBox="0 0 24 24">
                                <path d="M0 7.33l2.829-2.83 9.175 9.339 9.167-9.339 2.829 2.83-11.996 12.17z"/>
                            </svg>
                        </div>
                    </div>

                    <div class="PresenterLayout__header-description">
                        <slot name="header-content-description"></slot>
                    </div>
                </slot>
            </div>
            <div class="PresenterLayout__header-right">
                <slot name="header-right" />
            </div>

            <horizontal-loader v-model="loading" :delay="300" :duration="1500" class="PresenterLayout__header-loader"/>
        </div>
        <div class="PresenterLayout__content" :class="{ __overlay: showDropdownBlocks }" @click="contentClick" ref="content">
            <div v-if="hasDropdownBlocks" key="dropdownBlocks"
                 class="PresenterLayout__dropdown-blocks"
                 :class="{ __hidden: !showDropdownBlocks }"
                 @click.stop
                 ref="dropdownWrapper"
            >
                <div class="PresenterLayout__dropdown-blocks-content" ref="dropdownContent">
                    <block-list :blocks="block.dropdownMenuBlocks" @navigate="navigate" @refresh="refresh" @back="back" ref="dropdownBlocks" />
                </div>
            </div>

            <div v-if="!hasDefaultOutSlot" class="PresenterLayout__content-wrapper" @scroll="scroll" ref="wrapper">

                <div v-if="hasHeaderBlocks" class="PresenterLayout__cover" :style="{ height: `${coverHeight}px`}" ref="cover">
                    <div class="PresenterLayout__cover-blocks" :style="coverStyles">
                        <block-list :blocks="headerBlocks"
                                    scope="header"
                                    @navigate="navigate"
                                    @back="back"
                                    @refresh="refresh"
                                    @loaded="updateCoverHeight"
                                    ref="headerBlocks"
                        />
                    </div>

                    <div class="PresenterLayout__cover-top-gradient" :style="topGradientStyles" />
                    <div v-if="block.headerButton" class="PresenterLayout__cover-dots" @click="coverDotsClick">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="currentColor">
                            <path d="M6 12c0 1.657-1.343 3-3 3s-3-1.343-3-3 1.343-3 3-3 3 1.343 3 3zm9 0c0 1.657-1.343 3-3 3s-3-1.343-3-3 1.343-3 3-3 3 1.343 3 3zm9 0c0 1.657-1.343 3-3 3s-3-1.343-3-3 1.343-3 3-3 3 1.343 3 3z"/>
                        </svg>
                    </div>

                    <div class="PresenterLayout__cover-content">
                        <div class="PresenterLayout__cover-bottom-gradient" :style="bottomGradientStyles" />
                        <div v-if="block.header && block.header.logo && block.header.logo.icon && block.header.logo.icon.image"
                             class="PresenterLayout__cover-logo"
                             :class="{ __clickable: coverLogoClickable }"
                             :style="{ backgroundImage: `url(${block.header.logo.icon.image})` }"
                             @click="coverLogoClick"
                        />
                        <div class="PresenterLayout__cover-title"
                             :class="{ __clickable: coverTitleClickable }"
                             @click="coverTitleClick"
                        >
                            <slot name="header-content-block-title" />
                        </div>
                        <div class="PresenterLayout__cover-description"
                             :class="{ __clickable: coverDescriptionClickable }"
                             @click="coverDescriptionClick"
                        >
                            <slot name="header-content-block-description" />
                        </div>
                    </div>
                </div>

                <div v-if="hasPinnedBlocks" class="PresenterLayout__pinned-blocks" key="pinedBlocks">
                    <block-list :blocks="block.pinnedBlocks" @navigate="navigate" @refresh="refresh" @back="back" />
                </div>

                <slot />
            </div>

            <template v-else>
                <div v-if="hasPinnedBlocks" class="PresenterLayout__pinned-blocks" key="pinedBlocks">
                    <block-list :blocks="block.pinnedBlocks" @navigate="navigate" @refresh="refresh" @back="back" />
                </div>

                <slot name="default-out" />
            </template>
        </div>

        <div v-if="hasFooterSlot" class="PresenterLayout__footer">
            <slot name="footer" />
        </div>

        <div class="PresenterLayout__floating-qr" @click="showQr = !showQr"></div>

        <portal v-if="showQr" to="presenter-stack">
            <div class="PresenterLayout__qr-overlay" @click="showQr = false">
                <div class="PresenterLayout__qr-overlay-card">
                    <div class="PresenterLayout__qr-overlay-description">
                        Поставьте на телефон <br>
                        приложение <img class="PresenterLayout__qr-overlay-description-logo" src="/img/logos/logo_round.png" alt="" width="26" height="26"> <strong>Chatium</strong> <br>
                        и отсканируйте в нем qr-код <br>
                    </div>
                    <img :src="qrImage" width="400" height="400">
                </div>
            </div>
        </portal>
    </div>

</template>

