</style>

<script>

    import store from '@/store'
    import { enrichOnContextByPoint } from "@/services/actions";

    import HorizontalLoader from '@/modules/common/components/ui/HorizontalLoader'
    import SecondaryButton from '@/modules/common/components/ui/SecondaryButton'
    import BlockList from '@/modules/common/components/presenter/blocks/BlockList';
    import FileUploadQueue from "../ui/FileUploadQueue";

    export default {

        components: {
            FileUploadQueue,
            SecondaryButton,
            HorizontalLoader,
            BlockList,
        },

        props: {
            url: {
                type: String,
                default: '',
            },

            block: {
                type: Object,
                default: null,
            },

            loading: {
                type: Boolean,
                default: false,
            },

            canBack: {
                type: Boolean,
                default: false,
            },

            showHeader: {
                type: Boolean,
                default: false,
            },
        },

        data() {
            return {
                store,

                showDropdownBlocks: this.block && this.block.dropdownMenuInitiallyVisible,
                coverHeight: 0,
                coverScroll: 0,
                presenterWidth: 0,

                showQr: false,
            }
        },

        computed: {
            qrImage() {
                return `https://app.chatium.io/api/1.0/qr?text=${this.url}`
            },

            hasDropdownBlocks() {
                return this.block
                    && this.block.dropdownMenuBlocks
                    && this.block.dropdownMenuBlocks.length > 0
            },

            hasPinnedBlocks() {
                return this.block
                    && this.block.pinnedBlocks
                    && this.block.pinnedBlocks.length > 0
            },

            headerBlocks() {
                const blocks = []

                if (this.block && this.block.header) {
                    const header = this.block.header

                    if (header.image && header.image.downloadUrl) {
                        blocks.push({
                            type: 'image',
                            ...header.image,
                            containerStyle: {
                                paddingLeft: 0,
                                paddingRight: 0,
                                marginTop: 0,
                                marginBottom: 0,
                            },
                        })
                    }

                    if (header.blocks && header.blocks.length > 0) {
                        blocks.push(...header.blocks)
                    }
                }

                return blocks
            },

            hasHeaderBlocks() {
                return this.headerBlocks.length > 0
            },

            hasDefaultOutSlot() {
                return !!this.$slots['default-out']
            },

            hasFooterSlot() {
                return !!this.$slots['footer']
            },

            isAndroid() {
                return navigator.userAgent.match(/Android/i)
            },

            coverStyles() {
                return {
                    transform: `translateY(${(this.coverScroll < 20 ? 0 : this.coverScroll - 20) * 0.5}px)`
                }
            },

            topGradientStyles() {
                if (this.block && this.block.header && this.block.header.topGradientColors) {
                    return {
                        backgroundImage: `linear-gradient(180deg, ${this.block.header.topGradientColors.join(', ')})`
                    }
                }
            },

            bottomGradientStyles() {
                if (this.block && this.block.header && this.block.header.bottomGradientColors) {
                    return {
                        backgroundImage: `linear-gradient(180deg, ${this.block.header.bottomGradientColors.join(', ')})`
                    }
                }
            },

            coverLogoClickable() {
                return this.block && this.block.header && this.block.header.logo && this.block.header.logo.onClick
            },

            coverTitleClickable() {
                return this.block && this.block.header && this.block.header.title && this.block.header.title.onClick
            },

            coverDescriptionClickable() {
                return this.block && this.block.header && this.block.header.description && this.block.header.description.onClick
            },
        },

        watch: {
            block() {
                this.hideDropdown()
                this.updateCoverHeight()
            },

            hasHeaderBlocks() {
                this.updateCoverHeight()
            },
        },

        methods: {
            showDropdown() {
                if (this.hasDropdownBlocks && this.$refs.dropdownWrapper) {
                    this.showDropdownBlocks = true
                    this.$refs.dropdownWrapper.style.height = `${ this.$refs.dropdownBlocks.$el.clientHeight }px`
                }
            },

            hideDropdown() {
                this.showDropdownBlocks = false

                if (this.$refs.dropdownWrapper) {
                    this.$refs.dropdownWrapper.style.height = 0
                }
            },

            toggleDropdown() {
                this.showDropdownBlocks ? this.hideDropdown() : this.showDropdown()
            },

            contentClick(event) {
                if (this.showDropdownBlocks) {
                    event.stopPropagation()
                    event.preventDefault()

                    this.toggleDropdown()
                }
            },

            navigate(block) {
                this.$emit('navigate', block);
            },

            refresh(block) {
                this.$emit('refresh', block);
            },

            back(block) {
                this.$emit('back', block);
            },

            resize() {
                this.presenterWidth = this.$el.clientWidth
            },

            scroll(event) {
                const target = event.target

                // Учитывает перескроллы как вверх, так и вниз
                this.coverScroll = target.scrollTop <= 0 || target.scrollTop > this.coverHeight
                    ? 0
                    : Math.max(0, Math.min(target.scrollHeight - target.clientHeight, target.scrollTop))
            },

            navigatePlayMarket() {
                window.location = `market://details?id=com.chatium.app&url=https%3A%2F%2Fapp.chatium.io%2Fapplink%2F%3Flink%3D${ encodeURIComponent(this.url) }`
            },

            coverDotsClick(event) {
                if (this.block.headerButton) {
                    this.navigate(enrichOnContextByPoint(this.block.headerButton, event.pageX, event.pageY))
                }
            },

            coverLogoClick(event) {
                if (this.block.header.logo) {
                    this.navigate(enrichOnContextByPoint(this.block.header.logo, event.pageX, event.pageY))
                }
            },

            coverTitleClick(event) {
                if (this.block.header.title) {
                    this.navigate(enrichOnContextByPoint(this.block.header.title, event.pageX, event.pageY))
                }
            },

            coverDescriptionClick(event) {
                if (this.block.header.description) {
                    this.navigate(enrichOnContextByPoint(this.block.header.description, event.pageX, event.pageY))
                }
            },

            updateCoverHeight() {
                if (this.$refs.headerBlocks) {
                    this.coverHeight = this.$refs.headerBlocks.$el.clientHeight
                }
            }
        },

        mounted() {
            this.updateCoverHeight()

            this.requested = false

            window.addEventListener('resize', this.resize)
            document.body.addEventListener('scroll', this.scroll)

            this.resize()
        },

        beforeDestroy() {
            window.removeEventListener('resize', this.resize)
            document.body.removeEventListener('scroll', this.scroll)
        },

    };

</script>
