<template>

    <presenter-layout class="BlocksPresenter"
                      :class="{ __blocks: block.blocks.length > 0 || block.footer }"
                      :can-back="canBack"
                      :show-header="emptyHeader ? false : showHeader"
                      :block="block"
                      :url="url"
                      @navigate="navigate"
                      @refresh="refresh"
                      @back="back"
    >
        <template slot="header-content-title">{{ block.title }}</template>
        <template slot="header-content-description">{{ block.description }}</template>

        <template slot="header-content-block-title">{{ block.header && block.header.title ? block.header.title.text : block.title }}</template>
        <template slot="header-content-block-description">{{ block.header && block.header.description ? block.header.description.text : block.description }}</template>

        <template slot="header-right">
            <div v-if="block.headerButton" class="BlocksPresenter__header-button" @click="headerButtonClick" ref="headerButton">
                <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                    <path d="M6 12c0 1.657-1.343 3-3 3s-3-1.343-3-3 1.343-3 3-3 3 1.343 3 3zm9 0c0 1.657-1.343 3-3 3s-3-1.343-3-3 1.343-3 3-3 3 1.343 3 3zm9 0c0 1.657-1.343 3-3 3s-3-1.343-3-3 1.343-3 3-3 3 1.343 3 3z"/>
                </svg>
            </div>
        </template>
        <template slot="default">
            <search-block v-if="block.search"
                          :block="block.search"
                          @query="query"
                          ref="searchBlock"
            />
            <block-list v-if="block.blocks.length > 0"
                        :blocks="block.blocks"
                        :selected-url="highlightUrl"
                        @navigate="navigate"
                        @back="back"
                        @refresh="refresh"
                        ref="blocks"
            />
        </template>
        <template v-if="block.footer" slot="footer">
            <block-list v-if="block.footer.blocks.length > 0"
                        :blocks="block.footer.blocks"
                        :selected-url="highlightUrl"
                        @navigate="navigate"
                        @back="back"
                        @refresh="refresh"
                        ref="footer"
            />
        </template>
    </presenter-layout>

</template>

<style>

    .BlocksPresenter.__blocks .PresenterLayout__content-wrapper {
        padding-bottom: 20px;
    }

    .BlocksPresenter__header-button {
        height: 26px;
        width: 26px;

        display: flex;
        align-items: center;
        justify-content: center;

        cursor: pointer;
    }

</style>

<script>

    import { enrichOnContextByElement } from "@/services/actions";

    import PresenterLayout from "@/modules/common/components/presenter/PresenterLayout";
    import BlockList from "@/modules/common/components/presenter/blocks/BlockList";
    import SearchBlock from "@/modules/common/components/presenter/blocks/SearchBlock";
    import store from "@/store";
    import FileUploadQueue from "../ui/FileUploadQueue";
    import FooterBlock from "./blocks/FooterBlock";

    export default {

        components: {
            FooterBlock,
            FileUploadQueue,
            PresenterLayout,
            BlockList,
            SearchBlock,
        },

        data() {
            return {
                store: store,
            };
        },

        props: {
            url: {
                type: String,
                default: '',
            },

            block: {
                type: Object,
                required: true,
            },

            canBack: {
                type: Boolean,
                default: false,
            },

            showHeader: {
                type: Boolean,
                default: false,
            },

            highlightUrl: {
                type: String,
                default: null,
            },
        },

        computed: {
            emptyHeader() {
                return (!this.block || (!this.block.title && !this.block.description && !this.block.headerButton))
            },
        },

        methods: {
            navigate(block) {
                this.$emit('navigate', block);
            },

            refresh() {
                this.$emit('refresh');
            },

            query(text) {
                this.$emit('query', text);
            },

            back() {
                this.$emit('back');
            },

            resetSearch() {
                if (this.$refs.searchBlock) {
                    this.$refs.searchBlock.resetSearch();
                }
            },

            headerButtonClick() {
                this.navigate(enrichOnContextByElement(this.block.headerButton, this.$refs.headerButton))
            }
        },

        mounted() {
            try {
                if (this.$refs.footer) {
                    this.$refs.blocks.$el.style.paddingBottom = this.$refs.footer.$el.clientHeight + 'px'
                }
            } catch (_) {
                // Согласитесь, это не повод не показывать страницу...
            }
        },

    };

</script>
