const fix = number => (number < 10 ? "0" + number : number);

const humanDate = (datetime, full = false) => {
  const now = new Date();
  const date = new Date(datetime < 1000000000000 ? datetime * 1000 : datetime);

  // 1 вариант const months = ['января', 'февраля', 'марта', 'апреля', 'мая', 'июня', 'июля', 'августа', 'сентября', 'октября', 'ноября', 'декабря'];
  // 2 вариант
  const months = ["янв", "фев", "мар", "апр", "мая", "июня", "июля", "авг", "сент", "окт", "нояб", "дек"];

  const year = date.getFullYear();
  const month = date.getMonth();
  const day = date.getDate();

  const result = [];

  // Если это сегодня, то можно дату не писать
  if (full === true || now.getFullYear() !== year || now.getMonth() !== month || now.getDate() !== day) {
    // 1 вариант result.push(fix(day) + '.' + fix(month + 1) + '.' + year);
    // 2 вариант
    result.push(day + " " + months[month]);
  }

  const hours = date.getHours();
  const minutes = date.getMinutes();

  result.push(fix(hours) + ":" + fix(minutes));

  return result.join(" в ");
};

const formatDate = (datetime, full) => {
  const date = new Date(datetime < 1000000000000 ? datetime * 1000 : datetime);

  const now = new Date();

  const months = [
    "января",
    "февраля",
    "марта",
    "апреля",
    "мая",
    "июня",
    "июля",
    "августа",
    "сентября",
    "октября",
    "ноября",
    "декабря"
  ];

  const year = date.getFullYear();
  const month = date.getMonth();
  const day = date.getDate();

  // Если это сегодня, то можно дату не писать
  if (full === true || now.getFullYear() !== year) {
    return day + " " + months[month] + " " + year;
  }

  if (sameDate(date, Date.now())) {
    return "Сегодня";
  }

  return day + " " + months[month];
};

const formatTime = datetime => {
  const date = new Date(datetime < 1000000000000 ? datetime * 1000 : datetime);

  const hours = date.getHours();
  const minutes = date.getMinutes();

  return fix(hours) + ":" + fix(minutes);
};

// Возвращает истину если даты равны
const sameDate = (a, b) => {
  return new Date(a).setHours(0, 0, 0, 0) === new Date(b).setHours(0, 0, 0, 0);
};

// Возвращает дату без времени
const truncTime = dateTime => {
  const date = new Date(dateTime);
  date.setHours(0, 0, 0, 0);

  return date;
};

export default {
  humanDate,
  formatDate,
  formatTime,

  sameDate,
  truncTime
};
