<template>

    <div class="ProfilePage__wrapper">
        <div class="ProfilePage">
            <file-catcher @catch="catchFiles" ref="fileCatcher" />

            <template v-if="user">
                <form @submit.prevent="submit">
                    <div class="ProfilePage__avatar-selector">
                        <logo class="ProfilePage__avatar" :params="avatar" @click="avatarClick" size="large" />
                        <secondary-button small @click="avatarClick" :loading="uploadingPhoto">Выбрать фотографию</secondary-button>
                    </div>

                    <user-field-row
                        field="firstName"
                        label="Имя"
                        :default="firstName"
                        :optional="false"
                        placeholder="Ваше имя"
                        ref="firstName"
                    />

                    <user-field-row
                        field="lastName"
                        label="Фамилия"
                        :default="lastName"
                        :optional="true"
                        placeholder="и фамилия"
                        ref="lastName"
                    />

                    <div class="ProfilePage__actions">
                        <primary-button type="submit" :loading="saving">Сохранить</primary-button>
                    </div>
                </form>
                <div v-if="!!blocks" class="ProfilePage__blocks">
                    <block-list v-model="blocks" :blocks="blocks" @navigate="navigate"/>
                </div>
            </template>
        </div>
    </div>

</template>

<style>

    .ProfilePage__wrapper {
        display: flex;
        justify-content: center;
    }

    .ProfilePage {
        max-width: 350px;
        flex-grow: 2;
    }

    .ProfilePage__header {
        font-weight: bold;
        font-size: 22px;
        margin-bottom: 30px;
        text-align: center;
    }

    .ProfilePage__avatar-selector {
        display: flex;
        flex-flow: column;
        align-items: center;

        margin-bottom: 40px;
    }

    .ProfilePage__avatar {
        cursor: pointer;

        margin-bottom: 10px;
    }

    .ProfilePage__username {
        margin-bottom: 40px;
    }

    .ProfilePage__actions {
        text-align: center;
    }

    .ProfilePage__blocks {
        margin-top: 20px;
    }

    @media screen and (min-width: 900px) {

        .ProfilePage {
            max-width: 450px;
            padding: 50px;
            background-color: white;
            border-radius: 5px;
        }

    }

</style>

<script>

    import axios from "axios";
    import config from "@/config";
    import store from "@/store";

    import Logo from "@/modules/common/components/ui/Logo";
    import UserFieldRow from "@/modules/auth/pages/grantPage/UserFieldRow";
    import PrimaryButton from "@/modules/common/components/ui/PrimaryButton";
    import SecondaryButton from "@/modules/common/components/ui/SecondaryButton";
    import FileCatcher from "@/modules/common/components/ui/FileCatcher";
    import BlockList from "@/modules/common/components/presenter/blocks/BlockList";

    export default {

        components: {
            Logo,
            UserFieldRow,
            PrimaryButton,
            SecondaryButton,
            FileCatcher,
            BlockList,
        },

        props: {

        },

        data() {
            return {
                user: null,

                firstName: '',
                lastName: '',
                username: '',

                avatarBgColor: '#CCC',
                avatarImage: '',
                avatarHash: null,

                uploadingPhoto: false,
                saving: false,

                blocks: null,
            }
        },

        computed: {
            initials() {
                return (
                    this.firstName
                        ? this.firstName.slice(0, 1)
                        : ''
                ) + (
                    this.lastName
                        ? this.lastName.slice(0, 1)
                        : ''
                )
            },

            avatar() {
                return {
                    text: this.initials,
                    bgColor: this.avatarBgColor,
                    image: this.avatarImage,
                }
            },

            uploadUrl() {
                return config.fileStorage.url + '/fileservice/widget/upload?fullAnswer=true&accountId=chatium'
            },
        },

        methods: {
            navigate(block) {
                this.$emit('navigate', block);
            },

            async catchFiles(files) {
                this.uploadingPhoto = true;
                try {
                    const file = files.shift()

                    if (!file.type.includes('image/')) {
                        return false
                    }

                    const formData = new FormData();
                    formData.append('Filedata', file);

                    const response = await axios.post(
                        this.uploadUrl,
                        formData,
                        { headers: { 'Content-Type': 'multipart/form-data' } }
                    );

                    this.avatarImage = response.data.download_url
                    this.avatarHash = response.data.hash
                } finally {
                    this.uploadingPhoto = false;
                }
            },

            avatarClick() {
                this.$refs.fileCatcher.selectFiles()
            },

            async submit() {
                this.saving = true
                try {
                    const validations = await Promise.all([
                        this.$refs.firstName.validate(),
                        this.$refs.lastName.validate(),
                    ])

                    // Ищем, есть ли невалидные?
                    const invalid = validations.filter(validation => validation === false)
                    if (invalid.length > 0) {
                        return
                    }

                    const data = {
                        first_name: this.$refs.firstName.getValue(),
                        last_name: this.$refs.lastName.getValue(),
                    }

                    if (this.avatarHash) {
                        data.avatar = {
                            ...this.user.avatar,
                            image: this.avatarHash,
                        }
                    }

                    await this.$api.post('user/profile', data)

                    this.$emit('navigate', {
                        onClick: {
                            type: 'showToast',
                            toast: 'Профиль сохранен',
                        }
                    })
                } finally {
                    this.saving = false
                }
            },

            onKeyDown(event) {
                if (event.key === 'Escape') {
                    event.preventDefault()
                    this.cancel()
                }
            },
        },

        async mounted() {
            store.layout = 'fixed'

            this.user = await this.$api.get('user/profile')
            this.firstName = this.user.first_name
            this.lastName = this.user.last_name
            this.blocks = this.user.blocks

            if (this.user.avatar) {
                this.avatarBgColor = this.user.avatar.bgColor
                this.avatarImage = this.user.avatar.image
            }

            document.addEventListener('keydown', this.onKeyDown)
        },

        beforeDestroy() {
            document.removeEventListener('keydown', this.onKeyDown)
        },

    }

</script>
