<template>

    <div class="ChatMessageFiles">
        <component v-for="file in preparedFiles" class="ChatMessageFiles__file" :key="file.file.url" :is="file.component" :file="file.file" />
    </div>

</template>

<style>

    .ChatMessageFiles__file {
        margin-right: 20px;
    }

    .ChatMessageFiles__file:last-child {
        margin-bottom: 0;
    }

</style>

<script>

    import UnknownFile from "@/modules/chat/components/chatMessage/files/UnknownFile";
    import ImageFile from "@/modules/chat/components/chatMessage/files/ImageFile";
    import AudioFile from "@/modules/chat/components/chatMessage/files/AudioFile";
    import VideoFile from "@/modules/chat/components/chatMessage/files/VideoFile";
    import SvgFile from "@/modules/chat/components/chatMessage/files/SvgFile";

    export default {

        props: {
            files: {
                type: Array,
                required: true,
            },
        },

        computed: {
            preparedFiles() {
                return this.files.map((file) => {
                    let component = UnknownFile;

                    if (['audio/webm', 'audio/aac', 'audio/ogg'].includes(file.mime_type)) {
                        component = AudioFile;
                    }

                    if (['video/mp4', 'video/webm'].includes(file.mime_type)) {
                        component = VideoFile;
                    }

                    if (/image/.test(file.mime_type)) {
                        component = ImageFile;
                    }

                    if (['image/svg+xml'].includes(file.mime_type)) {
                        component = SvgFile;
                    }

                    return {
                        file: file,
                        component: component,
                    };
                });
            },
        },

    };

</script>
