import TextBlock from "./TextBlock";
import ImageBlock from "./ImageBlock";
import ScreenBlock from "./ScreenBlock";

import BoxBlock from "./BoxBlock";
import UnknownBlock from "./UnknownBlock";
import ButtonBlock from "./ButtonBlock";
import SearchBlock from "./SearchBlock";
import SectionBlock from "./SectionBlock";
import VideoBlock from "./VideoBlock";
import InlineVideoBlock from "./InlineVideoBlock";
import AudioBlock from "./AudioBlock";
import PaymentBlock from "./payment/PaymentBlock";

import GalleryBlock from "./GalleryBlock";
import LinkToApplicationBlock from "./LinkToApplicationBlock";

let map = {
  text: TextBlock,
  image: ImageBlock,
  button: ButtonBlock,
  search: SearchBlock,
  section: SectionBlock,
  video: VideoBlock,
  inlineVideo: InlineVideoBlock,
  audio: AudioBlock,
  screen: ScreenBlock,
  payment: PaymentBlock,
  box: BoxBlock,
  gallery: GalleryBlock,
  linkToApplication: LinkToApplicationBlock,
};

export default function getComponent(block) {
  if (isBoxBlock(block)) {
    return BoxBlock;
  }

  if (map[block.type]) {
    return map[block.type];
  }
  return UnknownBlock;
}


export function isBoxBlock(block) {
  return (block.type === "text" && (!block.text || block.text === "") && block.blocks && block.blocks.length > 0);
}
