<template>

    <div class="UnknownBlock" @click="click">
        <div>Unknown block: type={{block.type}}</div>
        <textarea rows=10 class="UnknownBlock__textarea">{{ JSON.stringify(block,null,2)   }}</textarea>
    </div>

</template>

<style>
    .UnknownBlock {
        padding: 10px;
        background: #F0F0E7;
    }
    .UnknownBlock__textarea {
        width: 100%;

    }
</style>

<script>

    export default {

        props: {
            block: {
                type: Object,
                required: true,
            },
        },

        methods: {
            click() {
                const click = this.block.onClick;

                if (click) {
                    if (click.type === 'navigate') {
                        this.$emit('navigate', this.block);
                    } else {
                        console.log('Unknown action', click);
                    }
                }
            },
        },

    };

</script>
