<template>

    <div class="MobileAppearance">
        <presenter class="MobileAppearance__presenter"
                   :key="url"
                   :url="url"
                   :show-header="!minimal"
                   @navigate="$emit('navigate', $event)"
                   @title="$emit('title', $event)"
                   ref="presenter"
        />

        <div v-if="modals.length > 0" class="MobileAppearance__modal-overlay" @click="modals.pop()">
            <div class="MobileAppearance__modal" @click.stop>
                <modal-navigator-stack :urls="modals"
                                       @navigate="modalsNavigate"
                                       @close="modals.pop()"
                />
            </div>
        </div>
    </div>

</template>

<style>

    .MobileAppearance__presenter {
        border-radius: 20px;
    }

    .MobileAppearance__modal-overlay {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,0.5);
    }

    .MobileAppearance__modal {
        position: absolute;
        top: 20px;
        left: 0;
        right: 0;
        bottom: 0;
    }

</style>

<script>

    import Presenter from "@/modules/common/components/Presenter";
    import ModalNavigatorStack from "@/modules/common/components/ModalNavigatorStack";

    export default {

        components: {
            Presenter,
            ModalNavigatorStack,
        },

        props: {
            url: {
                type: String,
                required: true,
            },

            inbox: {
                type: Boolean,
                default: true,
            },
        },

        data() {
            return {
                minimal: this.$route && this.$route.query.content !== undefined,
                modals: [],
            }
        },

        watch: {
            $route() {
                this.modals = []
            },
        },

        methods: {
            refresh() {
                this.$refs.presenter.fetch()
            },

            resetSearch() {
                this.$refs.presenter.resetSearch()
            },

            unknownAction(action) {
                this.$refs.presenter.unknownAction(action)
            },

            pushModal(url) {
                this.modals.push(url)
            },

            modalsNavigate(route) {
                if (route.openInModalScreen === false) {
                    this.modals = []
                }

                this.$emit('url', route)
            },
        },

    }

</script>
