<template>

    <div class="Application">
        <!-- Один общий навигатор который выполняет всю чернуовую работу -->
        <navigator :key="path"
                   @url="route"
                   @back="back"
                   @toast="showToast"
                   @showContextMenu="showContextMenu"
                   @showDialog="showDialog"
                   @attachMedia="attachMedia"
                   @refresh="appearance === 'mobile' ? $refs.mobile.refresh() : $refs.desktop.refresh()"
                   @resetSearch="appearance === 'mobile' ? $refs.mobile.resetSearch() : $refs.desktop.resetSearch()"
                   @unknown="appearance === 'mobile' ? $refs.mobile.unknownAction($event) : $refs.desktop.unknownAction($event)"
                   ref="navigator"
        />

        <!-- Так отображается на мобиле -->
        <mobile-appearance v-if="appearance === 'mobile'"
                           :url="url"
                           :inbox="false"
                           @navigate="$refs.navigator.navigate($event)"
                           @title="title"
                           ref="mobile"
        />

        <!-- Так отображается на десктопе -->
        <desktop-appearance v-if="appearance === 'desktop'"
                            :url="url"
                            :inbox="false"
                            @navigate="$refs.navigator.navigate($event)"
                            @url="route"
                            @title="title"
                            ref="desktop"
        />

        <input-dialog v-if="dialog"
                      :title="dialog.title"
                      :description="dialog.description"
                      :value="dialog.value"
                      :placeholder="dialog.placeholder"
                      :input-type="dialog.inputType"
                      :multiline="dialog.multiline"
                      :submit-url="dialog.submitUrl"
                      :submit-button-title="dialog.submitButtonTitle"
                      :cancel-button-title="dialog.cancelButtonTitle"
                      :navigate="dialogNavigate"
                      @close="dialog = null"
        />
        <context-menu v-if="contextMenu"
                      :items="contextMenu.menu"
                      :left="contextMenu.left"
                      :top="contextMenu.top"
                      :width="contextMenu.width"
                      :height="contextMenu.height"
                      @navigate="contextMenuNavigate"
                      @close="contextMenu = null"
        />

        <portal-target name="presenter-stack" />

        <transition name="ApplicationToast">
            <div v-if="toast" class="Application__toast" @click="toast = null">
                {{ toast }}
            </div>
        </transition>

        <modal-file-uploader v-if="modalAttachMedia"
                             :action="modalAttachMedia"
                             :navigate="navigatorNavigate"
                             @close="modalAttachMedia = null"
        />

        <file-catcher @catch="fileCatch" ref="fileCatcher" />
    </div>

</template>

<style>

.Application {
    -webkit-tap-highlight-color: transparent;
    height: 100%;
}

.Application__fullscreen {
    position: absolute;
    background: white;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 10000;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Application__toast {
    position: absolute;
    top: 30px;
    right: 30px;
    z-index: 10000;

    padding: 20px 30px;
    max-width: 70vw;
    overflow: hidden;

    background-color: #151629d1;
    border-radius: 5px;
    color: white;
    cursor: pointer;
}

/* Используется для анимации тостов Vue */
.ApplicationToast-enter-active,
.ApplicationToast-leave-active {
    transition: all .2s ease;
}
.ApplicationToast-enter, .ApplicationToast-leave-to {
    transform: translateY(-50px);
    opacity: 0;
}
/* Используется для анимации тостов Vue */

</style>

<script>

import config from "@/config";
import socket from "@/socket";
import store from "@/store";

import Navigator from "@/modules/common/components/Navigator";
import Presenter from "@/modules/common/components/Presenter";
import DesktopAppearance from "@/modules/application/DesktopAppearance";
import MobileAppearance from "@/modules/application/MobileAppearance";
import FileCatcher from "@/modules/common/components/ui/FileCatcher";
import ModalFileUploader from "@/modules/common/components/ModalFileUploader";

import GrantPage from "@/modules/auth/pages/GrantPage";
import ApplicationHeader from "@/modules/application/ApplicationHeader";
import ContextMenu from "@/modules/common/components/ui/ContextMenu";
import InputDialog from "@/modules/common/components/ui/InputDialog";
import FloatFileUploadQueue from "@/modules/common/components/ui/FloatFileUploadQueue";

export default {

    components: {
        DesktopAppearance,
        MobileAppearance,
        ModalFileUploader,
        ContextMenu,
        InputDialog,
        ApplicationHeader,
        Navigator,
        Presenter,
        GrantPage,
        FloatFileUploadQueue,
        FileCatcher,
    },

    props: {
        path: {
            type: String,
            require: true,
        },
    },

    data() {
        return {
            appearance: this.getAppearance(),

            devHrSocketDisposer: null,

            toast: null,
            toastTimeout: null,

            contextMenu: null,
            needHideContextMenu: true,

            dialog: null,
            modalAttachMedia: null,

            store,
        };
    },

    computed: {
        url() {
            if (this.$route.query.url) {
                return this.$route.query.url
            }

            const protocol = location.protocol.split(':').shift()
            const host = location.hostname

            return `${protocol}://${host}:5050${this.path}`
        },

        needAuth() {
            return store.needAuth
        },

        needUserData() {
            return store.needUserData
        },

        requestedUserData() {
            return store.requestedUserData
        },

        globalSearchScreen() {
            return store.globalSearchScreen
        },
    },

    watch: {
        needAuth() {
            // Требуется авторизация в ApiCall
            if (this.needAuth) {
                store.needAuth = false
                this.$router.push('/user/auth')
            }
        }
    },

    methods: {
        getAppearance() {
            return window.innerWidth < 900 ? 'mobile' : 'desktop'
        },

        async dialogNavigate(block) {
            await this.navigatorNavigate(block)
        },

        route(route) {
            if (route.openInModalScreen === true) {
                if (this.appearance === 'desktop') {
                    this.$refs.desktop.pushModal({ urls: [ route.url ] })
                } else {
                    this.$refs.mobile.pushModal({ urls: [ route.url ] })
                }
                return
            }

            if (route.url.startsWith('/api/2.0/proxy/')) {
                route.url = route.url.replace('/api/2.0/proxy/', '')
            }

            if (this.$route.query.url) {
                if (route.url.startsWith('http://') || route.url.startsWith('https://')) {
                    try {
                        const parsedUrl = new URL(route.url)

                        if (parsedUrl.hostname === location.hostname) {
                            this.$router[route.replace ? 'replace' : 'push'](parsedUrl.pathname).catch((_) => {});
                            return;
                        }
                    } catch (_) {}

                    let queryParams = new URLSearchParams(window.location.search)
                    queryParams.set('url', route.url)
                    this.$router.push('?' + queryParams.toString())
                } else {
                    try {
                        const parsedUrl = new URL(this.$route.query.url)
                        parsedUrl.pathname = route.url

                        let queryParams = new URLSearchParams(window.location.search)
                        queryParams.set('url', parsedUrl.toString())
                        this.$router.push('?' + queryParams.toString())
                    } catch (_) {}
                }

                return;
            }

            const protocol = location.protocol.split(':').shift()
            const host = location.host
            const url = route.url.replace(`${protocol}://${host}/`, '')

            this.$router[route.replace ? 'replace' : 'push'](url.startsWith('/') ? url : '/' + url).catch((_) => {});
        },

        back() {
            this.$router.back()
        },

        confirmed() {
            location.reload()
        },

        showContextMenu(contextMenu) {
            if (this.contextMenu) {
                this.contextMenu = {
                    ...this.contextMenu,
                    menu: contextMenu.menu,
                }
                this.needHideContextMenu = false
            } else {
                this.contextMenu = contextMenu
                this.needHideContextMenu = true
            }
        },

        showDialog(dialog) {
            if (this.dialog) {
                // it's necessary to defer here,
                // because otherwise chaining of dialogs immediately one after another doesn't work
                // may be bacause at this moment @close handler of old dialog is not yet processed
                // and new dialog is cleared immediately by the old dialog @close handler
                requestAnimationFrame(() => this.dialog = dialog)
            } else {
                this.dialog = dialog
            }
        },

        attachMedia(action) {
            this.modalAttachMedia = null
            this.$nextTick(() => this.modalAttachMedia = action)
        },

        showToast(toast) {
            this.toast = toast;
            this.toastTimeout = setTimeout(() => this.toast = null, 2000);
        },

        async contextMenuNavigate(block) {
            await this.navigatorNavigate(block)
            if (this.needHideContextMenu) {
                this.contextMenu = null
            } else {
                this.needHideContextMenu = true
            }
        },

        async navigatorNavigate( block ) {
            return this.$refs.navigator.navigate(block)
        },

        grantCancel() {
            this.navigatorNavigate({
                onClick: {
                    type: 'goBack',
                },
            })
        },

        granted() {
            this.navigatorNavigate({
                onClick: {
                    type: 'refresh',
                },
            })
        },

        title(title) {
            document.title = title

            if (typeof window['appSetScreenTitle'] === "function") {
                window['appSetScreenTitle'](title)
            }
        },

        resize() {
            this.appearance = this.getAppearance()
        },

        fileCatch(files) {
            this.$root.$emit('file-catch', files)
        },
    },

    async mounted() {
        window.addEventListener('resize', this.resize)
        this.resize()

        this.$root.$on('file-select', () => this.$refs.fileCatcher.selectFiles())

        if (config.env === 'development') {
            /**
             * Hot-reload last presenter in stack when backend is rebuilt and sent special socket event
             * @see backend/src/core/development.ts triggerClientHotReload
             */
            this.devHrSocketDisposer = socket.subscribeToSocket(
                config.dev.hrSocketId,
                async () => {
                    const presenter = this.appearance === 'mobile'
                        ? this.$refs.mobile.$refs.presenter
                        : this.$refs.desktop.$refs.presenter

                    // try to refetch screen up to 3 times
                    // this is necessary because sometimes next backend rebuild/restart appears too quickly
                    let retryConter = 3;
                    while (true) {
                        try {
                            await presenter.screenStoreFetch();
                            break;
                        } catch (err) {
                            if (retryConter--) {
                                console.warn(
                                    '%ApplicationPage:dev-hot-reloader', 'font-weight:700;color:magenta;',
                                    `Failed to refetch screen ${presenter.url}. Will retry in 2 seconds`, err
                                );
                                await new Promise(resolve => setTimeout(resolve, 2000));
                            } else {
                                presenter.error = err;
                                break;
                            }
                        }
                    }
                }
            );
        }
    },

    beforeDestroy() {
        window.removeEventListener('resize', this.resize)

        if (this.devHrSocketDisposer) {
            this.devHrSocketDisposer();
        }

        if (this.toastTimeout) {
            clearTimeout(this.toastTimeout)
        }
    },

}

</script>
