<template>

    <div class="SearchBlock" :style="containerStyles" @contextmenu="$emit('contextmenu', $event)">
        <text-input class="SearchBlock__input" :class="{ '__left-icon': !!block.leftIcon }" v-model="query" type="text" :placeholder="placeholder" />

        <div v-if="query.length > 0" class="SearchBlock__clear" @click="query = ''">
            <svg width="12" height="12" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd" viewBox="0 0 24 24">
                <path d="M12 11.293l10.293-10.293.707.707-10.293 10.293 10.293 10.293-.707.707-10.293-10.293-10.293 10.293-.707-.707 10.293-10.293-10.293-10.293.707-.707 10.293 10.293z"/>
            </svg>
        </div>

        <div v-if="block.leftIcon" class="SearchBlock__left-icon">
            <i class="fas" :class="{ ['fa-' + block.leftIcon.appIcon]: true }" />
        </div>
    </div>

</template>

<style>

    .SearchBlock {
        padding: 10px 10px;
        position: relative;
    }

    .SearchBlock__input.__left-icon {
        padding-left: 35px;
    }

    .SearchBlock__input {
        padding-right: 25px;
        font-size: 14px;
    }

    .SearchBlock__clear {
        position: absolute;
        right: 20px;
        top: 19px;

        cursor: pointer;
    }

    .SearchBlock__left-icon {
        position: absolute;
        left: 20px;
        top: 18px;
        color: gray;
    }

</style>

<script>

    import style from './helper/style';

    import TextInput from "@/modules/common/components/ui/TextInput";

    export default {

        components: {
            TextInput,
        },

        props: {
            block: {
                type: Object,
                required: true,
            },
        },

        data() {
            return {
                query: this.block.defaultValue ? this.block.defaultValue : '',
                lastProcessed: null,
                handler: null,
            };
        },

        watch: {
            query() {
                if (this.handler) {
                    clearTimeout(this.handler);
                    this.handler = null;
                }

                this.handler = setTimeout(() => {
                    this.process();
                }, this.delay);
            },
        },

        computed: {
            containerStyles() {
                return style.container(this.block);
            },

            placeholder() {
                return this.block.inputPlaceholder ? this.block.inputPlaceholder : '';
            },

            minLength() {
                return this.block.minLength > 0 ? this.block.minLength : 3;
            },

            delay() {
                return this.block.delayMs > 0 ? this.block.delayMs : 3;
            },
        },

        methods: {
            process() {
                const query = this.query.length >= this.minLength ? this.query : '';

                if (this.lastProcessed !== query) {
                    this.$emit('query', query === '' ? null : {
                        param: this.block.queryParamKey,
                        query,
                    });
                    this.lastProcessed = query;
                }
            },

            resetSearch() {
                this.query = '';
                this.process();
            },
        },

        beforeDestroy() {
            if (this.handler) {
                clearTimeout(this.handler);
                this.handler = null;
            }
        },

    };

</script>
