<template>

    <block-list class="BoxBlock" :class="classes"
                :blocks="block.blocks"
                @contextmenu="$emit('contextmenu', $event)"
                @navigate="$emit('navigate', $event)"
                @back="$emit('back', $event)"
                @refresh="$emit('refresh', $event)"
                @change="$emit('change', $event)"
                @input="$emit('input', $event)"
    />

</template>

<style>

    .BoxBlock {
        position: relative;
    }

    .BoxBlock.__with-image {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        height: 100%;
    }

    .BoxBlock.__flex {
        display: flex;
    }

    .BoxBlock.__flex-row {
        flex-direction: row;
    }

    .BoxBlock.__flex-column {
        flex-direction: column;
    }

    .BoxBlock.__flex-row > div {
        flex-grow: 1;
        flex-shrink: 1;
    }

</style>


<script>

    export default {
        props: {
            block: {
                type: Object,
                required: true,
            },
        },

        computed: {
            classes() {
                if (this.block.containerStyle) {
                    return {
                        '__with-image': this.block.containerStyle.backgroundImage,
                        '__flex': !!this.block.containerStyle.flexDirection,
                        '__flex-row': this.block.containerStyle.flexDirection === 'row',
                        '__flex-column': this.block.containerStyle.flexDirection === 'column',
                    }
                }
            },
        },
    };

</script>
