<template>

    <presenter-layout class="ChatPresenterLayout"
                      :block="block"
                      :can-back="canBack"
                      :show-header="showHeader"
                      :url="url"
                      @navigate="navigate"
                      @refresh="refresh"
                      @back="back"
                      :loading="loading"
    >
        <template slot="header-content-title">{{ block.title }}</template>
        <template slot="header-content-description">
            <template v-if="currentTypingNames.length > 0">
                <div class="ChatPresenter__typing">
                    {{ typingNames }}<span class="ChatPresenter__header-typing-dot __dot-1">.</span><span class="ChatPresenter__header-typing-dot __dot-2">.</span><span class="ChatPresenter__header-typing-dot __dot-3">.</span>
                </div>
            </template>
            <template v-else>
                {{ block.description }}
            </template>
        </template>
        <template slot="header-right">
            <div v-if="block.headerButton" class="ChatPresenter__header-button" @click="headerButtonClick" ref="headerButton">
                <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                    <path d="M6 12c0 1.657-1.343 3-3 3s-3-1.343-3-3 1.343-3 3-3 3 1.343 3 3zm9 0c0 1.657-1.343 3-3 3s-3-1.343-3-3 1.343-3 3-3 3 1.343 3 3zm9 0c0 1.657-1.343 3-3 3s-3-1.343-3-3 1.343-3 3-3 3 1.343 3 3z"/>
                </svg>
            </div>
        </template>
        <template slot="default-out">
            <div class="ChatPresenter" :style="styles">
                <div v-if="pinnedMessage" class="ChatPresenter__pinned" @click="scrollToReply(pinnedMessage)">
                    <div class="ChatPresenter__pinned-header">Прикрепленное сообщение</div>
                    <div class="ChatPresenter__pinned-text">{{ pinnedMessage.text }}</div>
                    <div v-if="canUnpin" class="ChatPresenter__pinned-unpin" @click.stop="pin(null)">
                        <svg width="24" height="24" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd"
                             clip-rule="evenodd" fill="currentColor">
                            <path d="M12 0c6.623 0 12 5.377 12 12s-5.377 12-12 12-12-5.377-12-12 5.377-12 12-12zm0 1c6.071 0 11 4.929 11 11s-4.929 11-11 11-11-4.929-11-11 4.929-11 11-11zm0 10.293l5.293-5.293.707.707-5.293 5.293 5.293 5.293-.707.707-5.293-5.293-5.293 5.293-.707-.707 5.293-5.293-5.293-5.293.707-.707 5.293 5.293z"/>
                        </svg>
                    </div>
                </div>
                <div class="ChatPresenter__content" ref="content" @scroll.passive="scroll" @click="interceptInternalLinkClick">
                    <div v-if="!hasPrevPage && block.blocks && block.blocks.length > 0" class="ChatPresenter__blocks-inside">
                        <block-list :blocks="block.blocks" @navigate="navigate" @refresh="refresh" @back="back" />
                    </div>

                    <chat-messages :messages="messages"
                                   :last-read="lastReadAt"
                                   :last-read-id="fetchedLastReadMessageId"
                                   :last-message-id="lastMessageId"
                                   :can-pin="!!block.chat.pinned && !!block.chat.pinned.update_url"
                                   :user="block.chat.current_author"
                                   @navigate="navigate"
                                   @reply="reply"
                                   @edit="edit"
                                   @delete="destroy"
                                   @pin="pin"
                                   @react="react"
                                   @scroll-to="scrollToReply"
                                   @contextMenu="contextMenu"
                    />
                </div>
                <footer-block v-if="!canAddMessage && block.footer" :block="block.footer" @navigate="navigate" @back="back" @refresh="refresh" />
                <div v-if="canAddMessage" class="ChatPresenter__footer">
                    <footer-block class="ChatPresenter__footer-add-message" v-if="block.footer" :block="block.footer" @navigate="navigate" @back="back" @refresh="refresh" />
                    <div v-if="showEmojiSelector === false && (hasNewMessages || needScrollToBottom)" class="ChatPresenter__scroll-bottom" @click="fetchLatest">
                        <svg width="12" height="12" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
                            <path d="M23.245 4l-11.245 14.374-11.219-14.374-.781.619 12 15.381 12-15.391-.755-.609z" />
                        </svg>
                        <div v-if="hasNewMessages" class="ChatPresenter__scroll-bottom-new-message"></div>
                    </div>
                    <div v-if="replyMessage" class="ChatPresenter__footer-reply">
                        <div class="ChatPresenter__footer-reply-left" @click="replyMessage = null">
                            <svg width="18" height="18" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill-rule="evenodd" clip-rule="evenodd" fill="currentColor">
                                <path d="M12 0c6.623 0 12 5.377 12 12s-5.377 12-12 12-12-5.377-12-12 5.377-12 12-12zm0 1c6.071 0 11 4.929 11 11s-4.929 11-11 11-11-4.929-11-11 4.929-11 11-11zm0 10.293l5.293-5.293.707.707-5.293 5.293 5.293 5.293-.707.707-5.293-5.293-5.293 5.293-.707-.707 5.293-5.293-5.293-5.293.707-.707 5.293 5.293z"/>
                            </svg>
                        </div>
                        <div class="ChatPresenter__footer-reply-right">
                            <div class="ChatPresenter__footer-reply-message" :class="{ '__with-image': !!replyMessageImage }">
                                <div v-if="replyMessageImage" class="ChatPresenter__footer-reply-image" :style="{ backgroundImage: 'url(' + replyMessageImage.thumbnail_url_400 + ')' }"></div>
                                <div v-if="replyMessage.author" class="ChatPresenter__footer-reply-author">{{ replyMessage.author.name }}</div>
                                <div class="ChatPresenter__footer-reply-text">{{ replyMessageText }}</div>
                            </div>
                        </div>
                    </div>
                    <div v-if="editMessage" class="ChatPresenter__footer-reply">
                        <div class="ChatPresenter__footer-reply-left" @click="editMessage = null">
                            <svg width="18" height="18" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill-rule="evenodd" clip-rule="evenodd" fill="currentColor">
                                <path d="M12 0c6.623 0 12 5.377 12 12s-5.377 12-12 12-12-5.377-12-12 5.377-12 12-12zm0 1c6.071 0 11 4.929 11 11s-4.929 11-11 11-11-4.929-11-11 4.929-11 11-11zm0 10.293l5.293-5.293.707.707-5.293 5.293 5.293 5.293-.707.707-5.293-5.293-5.293 5.293-.707-.707 5.293-5.293-5.293-5.293.707-.707 5.293 5.293z"/>
                            </svg>
                        </div>
                        <div class="ChatPresenter__footer-reply-right">
                            <div class="ChatPresenter__footer-reply-message">
                                <div class="ChatPresenter__footer-reply-action">Редактирование</div>
                                <div class="ChatPresenter__footer-reply-author">{{ editMessage.author.name }}</div>
                                <div class="ChatPresenter__footer-reply-text">{{ editMessageText }}</div>
                            </div>
                        </div>
                    </div>
                    <div class="ChatPresenter__footer-message">
                        <resizable :max-height="210" ref="resizable">
                            <textarea class="ChatPresenter__footer-input" v-model="message" @keydown="keydown" placeholder="Напишите сообщение..." rows="1" ref="input" />
                        </resizable>

                        <div class="ChatPresenter__footer-user" :class="{ __admin: isAdmin, __clickable: canBeAsGroup }" @click="switchAdmin" @mousedown.prevent>
                            <message-avatar v-if="block.chat.group_author" class="ChatPresenter__footer-user-group" :avatar="block.chat.group_author.avatar"></message-avatar>
                            <message-avatar v-if="block.chat.current_author" class="ChatPresenter__footer-user-current" :avatar="block.chat.current_author.avatar"></message-avatar>
                            <message-avatar v-else class="ChatPresenter__footer-user-current" :avatar="{ bgColor: '#ee0e5b', image: 'https://fs.getcourse.ru/fileservice/file/thumbnail/h/ffbc971ec98b2623bc4d93348dac66b0.png/s/200x200' }"></message-avatar>
                        </div>

                        <div class="ChatPresenter__footer-attach" @click="$refs.fileUploader.selectFiles()">
                            <svg width="24" height="24" fill="currentColor">
                                <path d="M17.843 1c2.159 0 3.912 1.753 3.912 3.912 0 .395-.053 1.704-1.195 2.813l-8.465 8.465c-.596.671-2.12 1.279-3.299.099-1.178-1.177-.586-2.685.088-3.29l4.409-4.409.707.707-3.164 3.163.014.003-1.411 1.413.004.003c-.97 1.151.618 2.93 1.977 1.572l8.383-8.384c.656-.652.94-1.393.94-2.155 0-1.601-1.299-2.9-2.9-2.9-.783 0-1.495.311-2.018.818l-.003-.003c-.573.573-11.502 11.494-11.534 11.527l-.002-.002c-.795.812-1.286 1.923-1.286 3.148 0 2.483 2.017 4.5 4.5 4.5.65 0 1.84.007 3.52-1.668l10.273-10.267.707.707-10.477 10.477c-1.004 1.077-2.435 1.751-4.023 1.751-3.035 0-5.5-2.465-5.5-5.5 0-1.577.666-3 1.731-4.004 10.668-10.667 10.835-10.839 11.295-11.297.277-.278 1.215-1.199 2.817-1.199"/>
                            </svg>
                        </div>

                        <div class="ChatPresenter__footer-emoji" @mouseenter="emojiIconEnter" @mouseleave="emojiIconLeave">
                            <svg width="24" height="24" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd" fill="currentColor">
                                <path d="M12 0c6.623 0 12 5.377 12 12s-5.377 12-12 12-12-5.377-12-12 5.377-12 12-12zm0 1c6.071 0 11 4.929 11 11s-4.929 11-11 11-11-4.929-11-11 4.929-11 11-11zm-.045 17.51h-.015c-2.285 0-4.469-1.189-6.153-3.349l.789-.614c1.489 1.911 3.394 2.963 5.364 2.963h.013c1.987-.004 3.907-1.078 5.408-3.021l.791.611c-1.693 2.194-3.894 3.405-6.197 3.41zm-3.468-10.01c.552 0 1 .448 1 1s-.448 1-1 1-1-.448-1-1 .448-1 1-1zm7.013 0c.552 0 1 .448 1 1s-.448 1-1 1-1-.448-1-1 .448-1 1-1z"/>
                            </svg>
                        </div>

<!--                        <div class="ChatPresenter__footer-recorder" :class="{ __recording: recording }" @click="$refs.fileRecorder.startRecord()">-->
<!--                            <svg width="24" height="24" fill="currentColor">-->
<!--                                <path d="M16 24h-8v-1h3.5v-3.018c-3.63-.256-6.5-3.287-6.5-6.982v-2h1v2.01c.005 3.307 2.692 5.99 6 5.99 3.311 0 6-2.689 6-6v-2h1v2c0 3.695-2.87 6.726-6.5 6.982v3.018h3.5v1zm-9-19c0-2.76 2.24-5 5-5s5 2.24 5 5v8c0 2.76-2.24 5-5 5s-5-2.24-5-5v-8zm9 0c0-2.208-1.792-4-4-4s-4 1.792-4 4v8c0 2.208 1.792 4 4 4s4-1.792 4-4v-8z"/>-->
<!--                            </svg>-->
<!--                        </div>-->
                    </div>
                </div>
            </div>

            <div v-if="showEmojiSelector" class="ChatPresenter__emoji" @mouseenter="emojiSelectorEnter" @mouseleave="emojiSelectorLeave">
                <emoji-selector @select="emoji" />
            </div>

            <file-uploader :url="block.chat.files_put_url" @submit="filesSubmit" @cancel="filesCancel" :initial-message="message" ref="fileUploader" />
            <file-recorder :url="block.chat.files_put_url" @submit="filesSubmit" @cancel="filesCancel" @recording="recording = $event" ref="fileRecorder" />
        </template>
    </presenter-layout>

</template>

<style>

    .ChatPresenter {
        display: flex;
        flex-direction: column;

        height: 1px;
        flex-grow: 2;
    }

    .ChatPresenter__pinned {
        padding: 10px 60px 10px 30px;
        border-bottom: 1px solid #eaeaea;
        overflow: hidden;
        flex-shrink: 0;
        position: relative;
        cursor: pointer;

        font-weight: 100;
        font-size: 13px;
    }

    .ChatPresenter__pinned:after {
        content: " ";
        position: absolute;
        left: 14px;
        top: 7px;
        bottom: 7px;
        width: 3px;
        background-color: #2988ff;
    }

    .ChatPresenter__pinned-header {
        color: #2988ff;
        font-weight: bold;
        font-size: 13px;
        padding-bottom: 3px;
    }

    .ChatPresenter__pinned-text {
        max-height: 3.6em;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .ChatPresenter__pinned-unpin {
        position: absolute;
        right: 20px;
        top: 50%;
        margin-top: -12px;
        color: #9f9f9f;
    }

    .ChatPresenter__pinned-unpin:hover {
        color: #338ac3;
    }

    .ChatPresenter__content {
        overflow: auto;
        transform: translateZ(0);
        flex-grow: 2;
    }

    .ChatPresenter__footer {
        max-height: 50vh;
        flex-shrink: 0;
        border-top: 1px solid #eaeaea;

        position: relative;
    }

    .ChatPresenter__scroll-bottom {
        height: 30px;
        line-height: 30px;
        background: rgba(243, 243, 243, 0.91);
        text-align: center;
        position: absolute;
        top: -40px;
        right: 20px;
        width: 50px;
        border: 1px solid #c0c0c038;
        border-radius: 3px;
        cursor: pointer;
    }

    .ChatPresenter__scroll-bottom-new-message {
        width: 8px;
        height: 8px;
        border-radius: 8px;
        background-color: #2788cc;

        position: absolute;
        top: 5px;
        right: 5px;
    }

    .ChatPresenter__footer-add-message {
        bottom: 100%;
    }

    .ChatPresenter__footer-user {
        position: absolute;
        top: 7px;
        left: 20px;

        user-select: none;
    }

    .ChatPresenter__footer-user.__clickable {
        cursor: pointer;
    }

    .ChatPresenter__footer-user-group,
    .ChatPresenter__footer-user-current {
        position: absolute;
        top: 0;
        left: 0;

        box-shadow: 0 0 0 3px white;
        z-index: 200;

        transition: .2s;
    }

    .ChatPresenter__footer-user-group {
        left: -15px;
        z-index: 100;
    }

    .ChatPresenter__footer-user.__admin .ChatPresenter__footer-user-group {
        left: 0;
        z-index: 200;
    }

    .ChatPresenter__footer-user.__admin .ChatPresenter__footer-user-current {
        left: -15px;
        z-index: 100;
    }

    .ChatPresenter__footer-reply {
        display: flex;
        background-color: white;
    }

    .ChatPresenter__footer-reply-left {
        cursor: pointer;

        flex-shrink: 0;
        width: 70px;

        display: flex;
        align-items: center;
        justify-content: center;

        color: #9f9f9f;

        padding-top: 10px;
    }

    .ChatPresenter__footer-reply-right {
        overflow: hidden;
    }

    .ChatPresenter__footer-reply-message {
        position: relative;
        padding: 0 10px 0 5px;
        border-left: 3px solid silver;
        margin: 10px 0 0 0;
    }

    .ChatPresenter__footer-reply-message.__with-image {
        padding-left: 40px;
    }

    .ChatPresenter__footer-reply-image {
        position: absolute;
        left: 5px;
        width: 30px;
        height: 30px;
        border: 1px solid silver;
        border-radius: 3px;
        background-size: cover;
        background-repeat: no-repeat;
    }

    .ChatPresenter__footer-reply-action {
        font-weight: bold;
        font-size: 13px;
        color: #2381cc;
    }

    .ChatPresenter__footer-reply-author {
        font-weight: bold;
        font-size: 13px;
        color: gray;

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .ChatPresenter__footer-reply-text {
        font-size: 13px;
        color: gray;

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        max-width: 100%;
    }

    .ChatPresenter__footer-message {
        position: relative;
    }

    .ChatPresenter__footer-input {
        width: 100%;

        outline: none;
        border: none;

        font: inherit;
        font-weight: 100;

        padding: 0 90px 0 70px;
        margin: 15px 0;
        resize: none;

        line-height: 21px;
    }

    .ChatPresenter__footer-input::placeholder {
        color: #a2a2a2;
    }

    .ChatPresenter__footer-attach,
    .ChatPresenter__footer-emoji,
    .ChatPresenter__footer-recorder {
        cursor: pointer;
        position: absolute;
        top: 0;
        right: 10px;

        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 50px;

        color: #9f9f9f;
    }

    .ChatPresenter__footer-emoji {
        right: 50px;
    }

    .ChatPresenter__footer-emoji:hover,
    .ChatPresenter__footer-attach:hover,
    .ChatPresenter__footer-recorder:hover {
        color: #338ac3;
    }

    .ChatPresenter__footer-recorder.__recording,
    .ChatPresenter__footer-recorder.__recording:hover {
        color: red;
    }

    .ChatPresenter__footer-recorder.__recording:before {
        content: " ";
        position: absolute;
        left: 2px;
        right: 2px;
        top: 7px;
        bottom: 6px;
        background-color: rgba(255, 100, 100, 0.2);
        border-radius: 20px;
        animation: ChatPresenter__footer-recorder 1s infinite;
    }

    .ChatPresenter__emoji {
        position: absolute;
        bottom: 70px;
        right: 20px;
        box-shadow: 0 0 10px -2px rgba(0,0,0,0.2);
        border-radius: 5px;
    }

    .ChatPresenter__header-button {
        height: 26px;
        width: 26px;

        display: flex;
        align-items: center;
        justify-content: center;

        cursor: pointer;
    }

    @keyframes ChatPresenter__footer-recorder {
        0% {
            transform: scale(1);
            background-color: rgba(255, 100, 100, 0.2);
        }
        50% {
            transform: scale(1.2);
            background-color: rgba(255, 100, 100, 0.3);
        }
        100% {
            transform: scale(1);
            background-color: rgba(255, 100, 100, 0.2);
        }
    }

    .ChatPresenter__typing {
        color: #338ac3;
    }

    .ChatPresenter__header-typing-dot {
        animation: ChatPresenter__header-typing-dot 1s infinite;
        animation-timing-function: linear;
        display: inline-block;
    }

    .ChatPresenter__header-typing-dot.__dot-2 {
        animation-delay: .33s;
    }

    .ChatPresenter__header-typing-dot.__dot-3 {
        animation-delay: .66s;
    }

    @keyframes ChatPresenter__header-typing-dot {
        0% {
            transform: translateY(0);
        }
        33% {
            transform: translateY(-1px);
        }
        66% {
            transform: translateY(1px);
        }
        100% {
            transform: translateY(0);
        }
    }

</style>

<script>

    import FooterBlock from "@/modules/common/components/presenter/blocks/FooterBlock";
    import { enrichOnContextByElement, enrichOnContextByPoint } from "@/services/actions";
    import linkifyStr from "linkifyjs/string";
    import nanoid from 'nanoid';

    import store from '@/store';
    import socket from '@/socket';

    import scroll from '@/services/scroll';

    import PresenterLayout from '@/modules/common/components/presenter/PresenterLayout';
    import FileUploader from '@/modules/common/components/ui/FileUploader';
    import FileRecorder from '@/modules/common/components/ui/FileRecorder';
    import Resizable from '@/modules/common/components/ui/Resizable';
    import ChatMessage from '@/modules/chat/components/ChatMessage';
    import ChatMessages from '@/modules/chat/components/ChatMessages';
    import EmojiSelector from '@/modules/common/components/ui/EmojiSelector';
    import BlockList from '@/modules/common/components/presenter/blocks/BlockList';
    import MessageAvatar from "@/modules/chat/components/chatMessage/MessageAvatar";

    export default {

        components: {
            FooterBlock,
            PresenterLayout,
            FileUploader,
            FileRecorder,
            Resizable,
            ChatMessage,
            ChatMessages,
            EmojiSelector,
            BlockList,
            MessageAvatar,
        },

        props: {
            url: {
                type: String,
                defualt: '',
            },

            block: {
                type: Object,
                required: true,
            },

            canBack: {
                type: Boolean,
                default: false,
            },

            showHeader: {
                type: Boolean,
                default: false,
            },
        },

        data() {
            return {
                id: 0,
                messages: [],
                lastMessageId: this.block.chat.last_message_id,
                message: '',

                replyMessage: null,
                replyMessageImage: null,
                replyMessageText: '',
                editMessage: null,
                editMessageText: '',
                lastSentMessage: null,

                loading: false,
                showEmojiSelector: false,
                hasNewMessages: false,
                lastReadAt: this.block.chat.last_read_at || 0,
                recording: false,
                needScrollToBottom: false,

                socketMessagesDisposer: null,
                socketTypingDisposer: null,
                socketLastReadDisposer: null,
                socketReactionsDisposer: null,

                typingSubscription: null,
                currentTypingNames: [],

                fetchOnScroll: false,   // Индикатор того, что мы пытаемся загрузить сообщение по событию скролла. Тротлинг скрола.
                hasNextPage: false,      // Есть данные сверху?
                hasPrevPage: false,      // Есть данные снизу?
                pageSize: 100,          // По сколько грузить сообщений (сверху и снизу или pageSize * 2 для последних сообщений)

                lastScrollPosition: null,
                scrollDelayHandler: null,
                scrollDirection: null,
                scrollPrecessing: false,

                highlightHandler: null,

                // Последнее прочитанное полученное с сервера. Оно не меняется, чтобы оставалась
                // плашка о новых сообщениях
                fetchedLastReadMessageId: this.block.chat.last_read_message_id === this.block.chat.last_message_id ? null : this.block.chat.last_read_message_id,
                lastReadMessageId: this.block.chat.last_read_message_id,   // Последнее прочитанное сообщение
                lastReadCreatedAt: null,   // Дата последнего прочитанного сообщения
                lastMarkedMessageId: null, // Последнее сообщение которое было отправлено на сервер (чтобы не слать дважды)
                lastKnownChangeId: null,

                isAdmin: false,

                pinnedMessage: this.block.chat.pinned && this.block.chat.pinned.message ? this.block.chat.pinned.message : null,
                canUnpin: this.block.chat.pinned && this.block.chat.pinned.update_url,
            };
        },

        computed: {
            visibility() {
                return store.visibility;
            },

            typingNames() {
                if (this.currentTypingNames.length === 1) {
                    return this.currentTypingNames[0] + ' печатает'
                } else if (this.currentTypingNames.length < 4) {
                    return this.currentTypingNames.slice(0, -1).join(', ') + ' и ' + this.currentTypingNames.slice(-1) + ' печатают';
                } else {
                    return this.currentTypingNames.slice(0, 3).join(', ') + ' и еще ' + (this.currentTypingNames.length - 3) + ' печатают';
                }
            },

            canBeAsGroup() {
                return !!this.block.chat.group_author;
            },

            canAddMessage() {
                return !!this.block.chat.messages_add_url;
            },

            styles() {
                return {
                    backgroundColor: this.block.chat.appearance ? this.block.chat.appearance.background_color : '#ffffff'
                }
            }
        },

        watch: {
            message() {
                if (this.typingSubscription) {
                    this.typingSubscription.updateTyping();
                }
            },

            block() {
                this.fetchedLastReadMessageId = this.block.chat.last_read_message_id === this.block.chat.last_message_id ? null : this.block.chat.last_read_message_id;
                this.lastReadMessageId = this.block.chat.last_read_message_id
                this.lastMessageId = this.block.chat.last_message_id
                this.lastReadAt = this.block.chat.last_read_at || 0
                this.pinnedMessage = this.block.chat.pinned && this.block.chat.pinned.message ? this.block.chat.pinned.message : null;
            },
        },

        methods: {
            escape(text) {
                if (!text) return ''

                // Тут мы вместо кода и инлайн кода вставляем нанойды
                // Потом линкифуем текст. Потом нанойды заменяем на код.
                // Это надо чтобы в коде не подсвечивались ссылки.
                const replacements = []

                const replacedText = text
                    .replace(/\n?```\n?([\s\S]*?)\n?```\n?/g, (_, code) => {
                        const id = nanoid()
                        replacements.push({ from: id, to: `<pre class="multiline-code"><code>${ code }</code></pre>` })
                        return id
                    })
                    // escaping backtick inside using backslash is supported too `back \` tick inside`
                    .replace(/`.*?[^\n\\]`/g, code => {
                        const id = nanoid()
                        replacements.push({
                            from: id,
                            to: `<code class="inline-code">${ code.slice(1, -1).replace('\\`', '`') }</code>`,
                        })
                        return id
                    })
                    .replace(/\*([\s\S])\*/g, (_, text) => {
                        const id = nanoid()
                        replacements.push({ from: id, to: `<strong>${ text }</strong>` })
                        return id
                    })
                    .replace(/\*([\S][^*]*[\S])\*/g, (_, text) => {
                        const id = nanoid()
                        replacements.push({ from: id, to: `<strong>${ text }</strong>` })
                        return id
                    })

                let linkifyedText = linkifyStr(replacedText, {
                    defaultProtocol: 'https',
                    target: 'blank',
                    tagName: 'a',
                });

                replacements.forEach(item => linkifyedText = linkifyedText.replace(item.from, item.to))

                return linkifyedText
            },

            escapeMessage(message) {
                message.textEscaped = message.type !== 'System' ? this.escape(message.text) : message.text;

                if (message.replyTo) {
                    message.replyTo.textEscaped = this.escape(message.replyTo.text);
                }

                return message;
            },

            async markAsRead() {
                if (this.block.chat.mark_as_read_url && this.lastMarkedMessageId !== this.lastReadMessageId) {
                    await this.$api.post(this.block.chat.mark_as_read_url, {
                        messageId: this.lastReadMessageId,
                    });

                    this.lastMarkedMessageId = this.lastReadMessageId;
                }
            },

            // Просто загружает сообщения с заданными опциями и искейпит тексты и подсвечиваает ссылки.
            async fetchMessages(options = {}) {
                this.loading = true;
                try {
                    const response = await this.$api.get(this.block.chat.messages_get_url, options);

                    response.data.messages = response.data.messages.reverse().map(this.escapeMessage);
                    if (response.data.lastChangeId) {
                        this.lastKnownChangeId = response.data.lastChangeId;
                    }

                    return response;
                } finally {
                    this.loading = false;
                }
            },

            async fetchChanges(lastChangeId) {
                this.loading = true;
                try {
                    const response = await this.$api.get(this.block.chat.messages_changes_url, { lastKnownChangeId: lastChangeId });
                    const changes = response.changes;

                    if (changes.length > 0) {
                        if (changes[0].prevId === lastChangeId) {
                            changes.forEach((change) => {
                                const existsMessage = this.messages.find((message) => message.id === change.messageId)

                                if (change.operation === 'create') {
                                    if (!existsMessage) {
                                        this.messages.push(this.escapeMessage(change.message));
                                    }
                                    this.lastMessageId = change.message.id
                                    this.hasNewMessages = true
                                } else if (change.operation === 'update') {
                                    if (existsMessage) {
                                        existsMessage.text = change.message.text
                                        existsMessage.textEscaped = this.escape(change.message.text)
                                        existsMessage.updatedAt = change.message.updatedAt
                                        existsMessage.updatedAtTimestamp = change.message.updatedAtTimestamp
                                    }
                                } else if (change.operation === 'delete') {
                                    this.messages = this.messages.filter((message) => message.id !== change.messageId)
                                }
                            })

                            this.lastKnownChangeId = changes[changes.length - 1].id
                        }
                    }

                    return response;
                } finally {
                    this.loading = false;
                }
            },

            // Загружает предыдущие сообщения. Пользователь начал мотать назад (смотреть более старые).
            async fetchPrevPage() {
                if (this.fetchOnScroll || !this.hasPrevPage) {
                    return false;
                }

                this.fetchOnScroll = true;
                try {
                    const messageId = this.messages[0].id;

                    const response = await this.fetchMessages({
                        pageWith: messageId,
                        sliceBefore: this.pageSize,
                    });

                    // -1 потому что там еще сообщение от которого паггинируем
                    if (response.data.messages.length - 1 < this.pageSize) {
                        this.hasPrevPage = false;
                    }

                    const scrollTop = this.$refs.content.scrollTop;

                    this.messages = [
                        // Не берем сообщение которое запросили (верхнее, которое уже и так есть)
                        ...response.data.messages.filter((message) => message.id !== messageId),
                        ...this.messages,
                    ].slice(0, this.pageSize * 2);

                    this.$nextTick(() => {
                        const element = document.getElementById(messageId);
                        if (element) {
                            const commentShift = element.classList.contains('__same-author') ? 31 : 10;
                            this.$refs.content.scrollTop = element.offsetTop - 40 - commentShift + scrollTop;
                        }
                    });

                    this.hasNextPage = true;
                } finally {
                    this.fetchOnScroll = false;
                }
            },

            // Загружает следующие сообщения (более новые). Пользователь находится на ранних сообщениях и крутит вниз.
            async fetchNextPage() {
                if (this.fetchOnScroll || !this.hasNextPage) {
                    return false;
                }

                this.fetchOnScroll = true;
                try {
                    const messageId = this.messages[this.messages.length - 1].id;

                    const response = await this.fetchMessages({
                        pageWith: messageId,
                        sliceAfter: this.pageSize,
                    });

                    // -1 потому что там еще сообщение от которого паггинируем
                    if (response.data.messages.length - 1 < this.pageSize) {
                        this.hasNextPage = false;
                    }

                    const element = document.getElementById(messageId);
                    const screenTop = element.offsetTop - this.$refs.content.scrollTop;

                    this.messages = [
                        ...this.messages,
                        // Не берем сообщение которое запросили (верхнее, которое уже и так есть)
                        ...response.data.messages.filter((message) => message.id !== messageId),
                    ].slice(-this.pageSize * 2);

                    this.$nextTick(() => {
                        const element = document.getElementById(messageId);
                        if (element) {
                            this.$refs.content.scrollTop = element.offsetTop - screenTop;
                        }
                    });

                    this.hasPrevPage = true;
                } finally {
                    this.fetchOnScroll = false;
                }
            },

            // Загружает самые последние сообщения. Предполагается, что пользователь смотрит где-то наверху
            // и нажал показать самый низ.
            async fetchLatest() {
                if (!this.hasNextPage) {
                    // Тут трункейтим сообщения, потому что они могли подкопиться (это своеобразный GC)
                    scroll.bottom(this.$refs.content).then(() => {
                        this.messages = this.messages.slice(-this.pageSize * 2);
                    });
                    return false;
                }

                this.fetchOnScroll = true;
                try {
                    const response = await this.fetchMessages({
                        sliceBefore: this.pageSize,
                    });

                    this.messages = response.data.messages;
                    this.$nextTick(() => {
                        this.fetchOnScroll = false;

                        scroll.bottom(this.$refs.content);
                    });

                    this.hasNextPage = false;
                    this.hasPrevPage = response.data.messages.length === this.pageSize * 2;
                } finally {
                    this.fetchOnScroll = false;
                }
            },

            // Получает новые сообщения, когда по пушу пришло уведомление
            async fetchNewChanges() {
                // Запоминаем текущее положение (в самом низу экрана или нет)
                const scrolledBottom = this.scrolledBottom()

                await this.fetchChanges(this.lastKnownChangeId);

                this.$nextTick(async () => {
                    // Если в самом низу экрана, то оскроллим
                    // вниз при поступлении новых сообщений
                    if (scrolledBottom) {
                        await this.scrollBottom()
                    }
                    // await this.scrollOrSetHasNewMessage(forceScroll);
                    await this.processDelayScroll();
                });
            },

            async fetchLastRead() {
                if (this.block.chat.last_read_get_url) {
                    const response = await this.$api.get(this.block.chat.last_read_get_url);
                    this.lastReadAt = response.data.lastReadAt;
                }
            },

            submit() {
                const message = this.message.trim();
                const replyMessage = this.replyMessage;
                const editMessage = this.editMessage;

                if (message.length === 0) {
                    return;
                }

                this.message = '';
                this.replyMessage = null;
                this.editMessage = null;
                // Тут не приходит событие в ресайзер инпут, так как нет по сути пользовательского ввода
                // И тогда я вручную дергаю его на перечет после установки пустого message
                this.$nextTick(async () => {
                    const messageId = nanoid();

                    this.$refs.resizable.resetSizeTextarea();

                    let optimisticMessage = null;

                    if (editMessage) {
                        const existsMessage = this.messages.find((message) => message.id === editMessage.id);
                        // А его может не быть, так как можно ускролить
                        if (existsMessage) {
                            existsMessage.text = message;
                            existsMessage.textEscaped = this.escape(message);
                            existsMessage.updatedAt = new Date().getTime()
                            existsMessage.updatedAtTimestamp = Math.round(new Date().getTime() / 1000)

                            this.lastSentMessage = existsMessage;
                        }
                    } else {
                        // Работаем в нексттике, потому что реплай мессадж должен пройти до дома (убраться)
                        // Иначе будет косяк с тем, что останется скроллинг
                        if (this.block.chat.current_author) {
                            const now = Date.now()

                            optimisticMessage = {
                                id: messageId,
                                __local: true,
                                bgColor: null,
                                canEdit: true,
                                author: this.isAdmin ? this.block.chat.group_author : this.block.chat.current_author,
                                type: 'message',
                                text: message,
                                textEscaped: this.escape(message),
                                data: null,
                                blocks: null,
                                replyTo: replyMessage,
                                files: [],
                                reactions: null,
                                isOutgoing: true,
                                createdAt: now,
                                updatedAt: now,
                            };

                            if (!this.hasNextPage) {
                                this.messages.push(optimisticMessage);
                                this.$nextTick(() => this.scrollBottom())
                            }

                            this.fetchedLastReadMessageId = null;
                            this.lastSentMessage = optimisticMessage;
                            this.lastReadCreatedAt = now;
                        }
                    }

                    let response = null;

                    if (editMessage) {
                        response = await this.$api.post(this.block.chat.messages_edit_url, {
                            id: editMessage.id,
                            text: message,
                            asGroup: this.isAdmin,
                        });
                    } else {
                        response = await this.$api.post(this.block.chat.messages_add_url, {
                            id: messageId,
                            replyTo: replyMessage ? { id: replyMessage.id } : null,
                            text: message,
                            asGroup: this.isAdmin,
                        });

                        await this.processDelayScroll();

                        this.$root.$emit('chat.message', {
                            url: this.block.screenUrl,
                            message: response.data.added.text,
                        });
                    }

                    if (optimisticMessage) {
                        optimisticMessage.id = response.data.added.id;
                        optimisticMessage.__local = false;
                        optimisticMessage.type = response.data.added.type;
                        optimisticMessage.text = response.data.added.text;
                        optimisticMessage.textEscaped = this.escape(response.data.added.text);
                        optimisticMessage.data = response.data.added.data;
                        optimisticMessage.blocks = response.data.added.blocks;
                        optimisticMessage.createdAt = response.data.added.createdAt;
                        optimisticMessage.updatedAt = response.data.added.updatedAt;
                    }
                });

                this.$refs.input.focus();
            },

            keydown(event) {
                if (event.key === 'Enter' && event.shiftKey === false && event.ctrlKey === false && event.metaKey === false) {
                    event.preventDefault();
                    this.submit();
                }

                if (event.key === 'Enter' || event.key === 'Escape') {
                    this.showEmojiSelector = false;
                }

                if (event.key === 'Escape') {
                    if (this.replyMessage) {
                        this.replyMessage = null;
                        event.stopPropagation();
                    }

                    if (this.editMessage) {
                        this.editMessage = null;
                        this.message = '';
                        event.stopPropagation();
                        this.$nextTick(() => this.$refs.resizable.resetSizeTextarea());
                    }
                }

                if (event.key === 'ArrowUp' && this.message === '' && this.lastSentMessage) {
                    event.preventDefault();
                    this.edit(this.lastSentMessage);
                }
            },

            async filesSubmit(files, message = '') {
                const messageId = nanoid();
                const replyMessage = this.replyMessage;

                let optimisticMessage = null;

                if (this.block.chat.current_author) {
                    const now = Date.now()

                    optimisticMessage = {
                        id: messageId,
                        __local: true,
                        bgColor: null,
                        canEdit: true,
                        author: this.isAdmin ? this.block.chat.group_author : this.block.chat.current_author,
                        type: 'message',
                        text: message,
                        textEscaped: this.escape(message),
                        data: null,
                        blocks: null,
                        replyTo: replyMessage,
                        files: files.map((file) => ({
                            "url": file.url,
                            "thumbnail_url_400": file.url + '/s/400x400/a/31740/sc/192',
                            "thumbnail_url_50": file.url + '/s/50x50/a/31740/sc/192',
                            "mime_type": file.meta.mime,
                            "meta": {
                                "mime": file.meta.mime,
                            },
                        })),
                        reactions: null,
                        isOutgoing: true,
                        createdAt: now,
                        updatedAt: now,
                    };

                    if (!this.hasNextPage) {
                        this.messages.push(optimisticMessage);
                        this.$nextTick(() => this.scrollBottom())
                    }

                    this.fetchedLastReadMessageId = null;
                    this.lastSentMessage = optimisticMessage;
                    this.lastReadCreatedAt = now;
                }

                this.message = '';
                this.replyMessage = null;

                // Тут не приходит событие в ресайзер инпут, так как нет по сути пользовательского ввода
                // И тогда я вручную дергаю его на перечет после установки пустого message
                this.$nextTick(() => this.$refs.resizable.resetSizeTextarea());

                this.lastSentMessage = optimisticMessage;

                const response = await this.$api.post(this.block.chat.messages_add_url, {
                    id: messageId,
                    replyTo: replyMessage ? { id: replyMessage.id } : null,
                    text: message,
                    files: files,
                    asGroup: this.isAdmin,
                });

                await this.processDelayScroll();

                if (optimisticMessage) {
                    optimisticMessage.id = response.data.added.id;
                    optimisticMessage.__local = false;
                    optimisticMessage.type = response.data.added.type;
                    optimisticMessage.text = response.data.added.text;
                    optimisticMessage.textEscaped = this.escape(response.data.added.text);
                    optimisticMessage.data = response.data.added.data;
                    optimisticMessage.blocks = response.data.added.blocks;
                    optimisticMessage.createdAt = response.data.added.createdAt;
                    optimisticMessage.updatedAt = response.data.added.updatedAt;
                }

                this.$root.$emit('chat.message', {
                    url: this.block.screenUrl,
                    message: response.data.added.text,
                });

                this.$refs.input.focus();
            },

            filesCancel() {
                this.$refs.input.focus();
            },

            navigate(block) {
                this.$emit('navigate', block);
            },

            refresh() {
                console.log('Chat presenter need refresh')
            },

            back() {
                this.$emit('back');
            },

            emoji(char) {
                const el = this.$refs.input;
                const text = char;

                let val = el.value, endIndex, startIndex, range;

                if (typeof el.selectionStart != 'undefined' && typeof el.selectionEnd != 'undefined') {
                    startIndex = el.selectionStart;
                    endIndex = el.selectionEnd;
                    el.value = val.substring(0, startIndex) + text + val.substring(el.selectionEnd);
                    el.selectionStart = el.selectionEnd = startIndex + text.length;
                } else if (typeof document.selection != 'undefined' && typeof document.selection.createRange != 'undefined') {
                    el.focus();
                    range = document.selection.createRange();
                    range.text = text;
                    range.select();
                }

                this.message = this.$refs.input.value;
                this.$refs.input.focus();
            },

            emojiIconEnter() {
                this.showEmojiSelector = true;

                if (this.selectorTimeout) {
                    clearTimeout(this.selectorTimeout);
                }
            },

            emojiIconLeave() {
                this.selectorTimeout = setTimeout(this.hideEmojiSelector, 300);
            },

            emojiSelectorEnter() {
                if (this.selectorTimeout) {
                    clearTimeout(this.selectorTimeout);
                }
            },

            emojiSelectorLeave() {
                this.selectorTimeout = setTimeout(this.hideEmojiSelector, 300);
            },

            hideEmojiSelector() {
                this.showEmojiSelector = false;
            },

            reply(message) {
                this.editMessage = null;

                this.replyMessage = message;
                this.replyMessageImage = message.files && message.files.find((file) => /image/.test(file.meta.mime));
                this.replyMessageText = message.textEscaped;

                if (this.replyMessageText === '') {
                    this.replyMessageText = this.replyMessageImage ? '(фотография)' : '(файл)';
                }

                this.$refs.input.focus();
            },

            edit(message) {
                this.replyMessage = null;

                this.editMessage = message;
                this.editMessageText = message.textEscaped;
                this.message = message.text;

                this.$refs.input.focus();
                this.$nextTick(() => this.$refs.resizable.resetSizeTextarea());
            },

            async destroy(message) {
                const response = await this.$api.post(this.block.chat.messages_delete_url, {
                    id: message.id,
                });

                this.messages = this.messages.filter(m => m.id !== message.id)

                this.$refs.input.focus();
            },

            async pin(message) {
                const response = await this.$api.post(this.block.chat.pinned.update_url, {
                    messageId: message ? message.id : null,
                });

                this.pinnedMessage = message ? message : null;

                this.$refs.input.focus();
            },

            async react(data) {
                const response = await this.$api.post(this.block.chat.messages_react_url, {
                    messageId: data.message.id,
                    reaction: data.reaction,
                    action: data.action,
                })

                if (response.success) {
                    const message = this.messages.find((message) => message.id === data.message.id);
                    if (message) {
                        message.reactions = response.data.reactions
                    }
                }
            },

            scrolledBottom() {
                const element = this.$refs.content
                return element.scrollHeight - element.clientHeight - element.scrollTop < 60
            },

            async scrollBottom(animated = true) {
                this.hasNewMessages = false

                return scroll.bottom(
                    this.$refs.content,
                    animated,
                )
            },

            async scrollOrSetHasNewMessage(forceScroll = false) {
                // Если окно браузера активно
                if (forceScroll || (this.visibility === 'visible' && !this.hasNewMessages)) {
                    if (forceScroll || this.scrolledBottom()) {
                        // Пришло сообдщение и его показываем, а трункейтим после скрола чтобы было все плавненько
                        await this.scrollBottom(false)

                        this.messages = this.messages.slice(-this.pageSize * 2);

                        return;
                    }
                }

                this.hasNewMessages = true;
            },

            async scroll() {
                if (!this.$refs.content || this.scrollPrecessing) {
                    return;
                }

                if (this.lastScrollPosition === null) {
                    this.lastScrollPosition = this.$refs.content.scrollTop;
                    return;
                }

                this.scrollDirection = this.lastScrollPosition > this.$refs.content.scrollTop ? 'up' : 'down';
                this.lastScrollPosition = this.$refs.content.scrollTop;

                this.scrollPrecessing = true;
                try {
                    await this.processScroll(this.scrollDirection);
                } finally {
                    this.scrollPrecessing = false;
                }

                if (this.scrollDelayHandler) {
                    clearTimeout(this.scrollDelayHandler);
                }

                this.scrollDelayHandler = setTimeout(async () => {
                    await this.processDelayScroll();
                    this.scrollDelayHandler = null;
                }, 2000);

                const element = this.$refs.content;
                this.needScrollToBottom = this.hasNextPage || element.scrollHeight - element.clientHeight - element.scrollTop > 200;
            },

            /**
             * Prevent account-local links wrapped by linkified with target=_blank to open new browser window
             *  and perform normal local local routing (navigate) instead.
             * @see escape
             */
            interceptInternalLinkClick(evt) {
                if (
                    evt.target && evt.target.nodeName === 'A' &&
                    evt.target.className === 'linkified' &&
                    evt.target.origin === window.location.origin
                ) {
                    evt.preventDefault();
                    evt.stopPropagation();
                    this.$router.push({path: evt.target.pathname});
                }
            },

            async processScroll(direction) {
                const element = this.$refs.content;

                if (direction === 'up' && element.scrollTop < 50) {
                    await this.fetchPrevPage();
                }

                if (direction === 'down' && element.scrollHeight - element.scrollTop < 50 + element.clientHeight) {
                    await this.fetchNextPage();
                }

                if (this.hasNewMessages) {
                    // Если окно браузера активно и прокручено менее чем на 30 пикселей вверх (то считаем что мы в самом низу)
                    if (this.visibility === 'visible' && (element.scrollHeight - element.clientHeight - element.scrollTop < 60)) {
                        this.hasNewMessages = false;
                    }
                }
            },

            async processDelayScroll() {
                const elements = [...this.$refs.content.querySelectorAll('.ChatMessage')];
                if (elements.length === 0) return;

                const scrollTop = this.$refs.content.scrollTop;
                const clientHeight = this.$refs.content.clientHeight;
                let element = null;

                // Если последнее сообщение не видно, то просто запоминаем первое верхнее
                if (this.hasNextPage === true || elements[elements.length - 1].offsetTop > scrollTop + clientHeight) {
                    element = elements.find((element) => element.offsetTop > scrollTop);
                } else {
                    element = elements[elements.length - 1];
                }

                const messageId = element.id;
                const message = this.messages.find((message) => message.id === messageId);

                if (message && (this.lastReadCreatedAt === null || this.lastReadCreatedAt < message.createdAt)) {
                    this.lastReadMessageId = message.id;
                    this.lastReadCreatedAt = message.createdAt;

                    await this.markAsRead();
                }
            },

            scrollToNewMessages() {
                this.hasNewMessages = false;

                scroll.bottom(this.$refs.content);
            },

            messageElementIntoView(messageElement, highlight = false, animated = false) {
                const contentElement = this.$refs.content;

                scroll.into(contentElement, messageElement, animated);

                if (this.highlightHandler === null && highlight) {
                    messageElement.classList.add('__highlighted');

                    this.highlightHandler = setTimeout(() => {
                        messageElement.classList.remove('__highlighted');
                        this.highlightHandler = null;
                    }, 1000);
                }
            },

            async scrollToReply(message) {
                const messageElement = document.getElementById(message.id);
                if (messageElement) {
                    this.messageElementIntoView(messageElement, true, true);
                } else {
                    const options = { pageWith: message.id, sliceBefore: this.pageSize, sliceAfter: this.pageSize };
                    const response = await this.fetchMessages(options);
                    this.messages = response.data.messages;

                    this.$nextTick(() => {
                        const element = document.getElementById(message.id);
                        if (element) {
                            this.messageElementIntoView(element, true, true);
                        }

                        this.hasPrevPage = true;
                        this.hasNextPage = true;
                    });
                }
            },

            switchAdmin() {
                if (this.canBeAsGroup) {
                    this.isAdmin = !this.isAdmin;
                }
            },

            documentKeyDown(event) {
                if (event.key === 'Escape') {
                    this.fetchedLastReadMessageId = null
                    event.stopPropagation()
                }
            },

            updateReactions(data) {
                const message = this.messages.find((message) => message.id === data.messageId);
                if (message) {
                    message.reactions = data.reactions
                }
            },

            async contextMenu(event, message) {
                event.preventDefault()
                window.getSelection().empty()

                const messageElement = document.getElementById(message.id);
                if (messageElement) {
                    this.messageElementIntoView(messageElement, true, true);
                }

                if (this.block.chat.on_context_api_call_url) {
                    const response = await this.$api.post(this.block.chat.on_context_api_call_url, {
                        messageId: message.id,
                    })

                    this.$emit(
                        'navigate',
                        enrichOnContextByPoint(
                            { onClick: response.appAction },
                            Math.round(event.pageX),
                            Math.round(event.pageY)
                        )
                    )
                }
            },

            unknownAction(action) {
                if (action.type === 'chat:replyToMessage' || action.type === 'chat:editMessage') {
                    const message = this.messages.find(message => message.id === action.id)
                    if (message) {
                        action.type === 'chat:replyToMessage'
                            ? this.reply(message)
                            : this.edit(message)
                    }
                }
            },

            headerButtonClick() {
                this.navigate(enrichOnContextByElement(this.block.headerButton, this.$refs.headerButton))
            },
        },

        async mounted() {
            // Если есть последнее непрочитанное сообщение, то грузим сообщения вокруг него иначе последние
            const options = this.lastReadMessageId === null ? {} : { pageWith: this.lastReadMessageId, sliceBefore: this.pageSize, sliceAfter: this.pageSize };
            const response = await this.fetchMessages(options);
            this.messages = response.data.messages;

            // После того как получили сообщения с сервера и обновился дом делаем плавный скроллинг
            this.$nextTick(() => {
                // Если есть сообщение на которое надо позиционироваться, то делаем скроллинг до него
                if (this.lastReadMessageId) {
                    const message = this.messages.find((message) => message.id === this.lastReadMessageId);
                    if (message) {
                        this.lastReadMessageId = message.id;
                        this.lastReadCreatedAt = message.createdAt;
                    }

                    const element = document.getElementById(this.lastReadMessageId);
                    if (element) {
                        this.messageElementIntoView(element, false, false);
                        this.processDelayScroll();
                    }

                    this.hasPrevPage = this.messages.length === this.pageSize + 1;
                    if (this.lastMessageId !== this.lastReadMessageId) {
                        this.hasNextPage = true;
                    }
                }
                // иначе в самый низ
                else {
                    this.hasPrevPage = response.data.messages.length === this.pageSize * 2;
                    this.hasNextPage = false;

                    scroll.bottom(this.$refs.content, false);
                    this.processDelayScroll();

                    if (this.fetchedLastReadMessageId !== this.lastReadMessageId) {
                        this.markAsRead();
                    }
                }
            });

            await this.fetchLastRead();

            if (this.block.chat.messages_socket_id) {
                this.socketMessagesDisposer = socket.subscribeToSocket(
                    this.block.chat.messages_socket_id,
                    () => this.fetchNewChanges(),
                );
            }

            if (this.block.chat.reactions_socket_id) {
                this.socketReactionsDisposer = socket.subscribeToData(
                    this.block.chat.reactions_socket_id,
                    (data) => this.updateReactions(data),
                );
            }

            if (this.block.chat.typing_socket_data) {
                this.typingSubscription = socket.subscribeToTyping(this.block.chat.typing_socket_data);

                if (this.typingSubscription) {
                    this.socketTypingDisposer = this.typingSubscription.listen(
                        (currentTypingNames) => this.currentTypingNames = currentTypingNames
                    );
                }
            }

            if (this.block.chat.last_read_socket_id) {
                this.socketLastReadDisposer = socket.subscribeToSocket(
                    this.block.chat.last_read_socket_id,
                    () => this.fetchLastRead(),
                    1000,
                );
            }

            if (this.$refs.input) {
                this.$refs.input.focus();
            }

            document.addEventListener('keydown', this.documentKeyDown)
        },

        beforeDestroy() {
            if (this.socketMessagesDisposer) {
                this.socketMessagesDisposer();
            }

            if (this.socketReactionsDisposer) {
                this.socketReactionsDisposer();
            }

            if (this.socketTypingDisposer) {
                this.socketTypingDisposer();
            }

            if (this.socketLastReadDisposer) {
                this.socketLastReadDisposer();
            }

            if (this.highlightHandler !== null) {
                clearTimeout(this.highlightHandler);
            }

            if (this.scrollDelayHandler) {
                clearTimeout(this.scrollDelayHandler);
            }

            document.removeEventListener('keydown', this.documentKeyDown)
        },

    };

</script>
