<template>

    <div class="MessageAvatar" :class="classes" :style="styles" @click="$emit('click')">{{ caption }}</div>

</template>

<style>

    .MessageAvatar {
        position: relative;

        height: 40px;
        width: 40px;
        border-radius: 40px;

        background-color: silver;
        background-size: cover;

        display: flex;
        font-size: 18px;
        align-items: center;
        justify-content: center;
    }

    .MessageAvatar.__clickable {
        cursor: pointer;
    }

    .MessageAvatar.__default:after {
        content: " ";
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;

        background: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0));

        border-radius: 50px;
    }


</style>

<script>

    export default {

        props: {
            avatar: {
                type: Object,
                required: true,
            },

            clickable: {
                type: Boolean,
                default: false,
            },
        },

        computed: {
            styles() {
                return {
                    color: this.avatar.color,
                    backgroundColor: this.avatar.bgColor,
                    backgroundImage: this.avatar.image ? 'url(' + this.avatar.image + ')' : null,
                };
            },

            classes() {
                return {
                    __default: !this.avatar.image,
                    __clickable: this.clickable,
                };
            },

            caption() {
                return this.avatar.image ? '' : this.avatar.text;
            },
        },

    };

</script>
