<template>

    <div class="UnknownFile">
        <div class="UnknownFile__preview">
            <svg width="24" height="24" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" clip-rule="evenodd">
                <path d="M22 24h-20v-24h14l6 6v18zm-7-23h-12v22h18v-16h-6v-6zm3 15v1h-12v-1h12zm0-3v1h-12v-1h12zm0-3v1h-12v-1h12zm-2-4h4.586l-4.586-4.586v4.586z"/>
            </svg>
        </div>
        <div class="UnknownFile__description">
            <a :href="file.url" target="_blank">{{ file.meta.name }}</a>
        </div>
    </div>

</template>

<style>

    .UnknownFile {
        display: flex;
        align-items: center;
    }

    .UnknownFile__preview {
        width: 30px;
        height: 30px;

        display: flex;
        align-items: center;
    }

    .UnknownFile__description {
        word-break: break-all;
    }

    .UnknownFile__description a {
        color: #2988ff;
    }

</style>

<script>

    export default {

        props: {
            file: {
                type: Object,
                required: true,
            },
        },

        data() {
            return {
                fullscreen: false,
            };
        },

        methods: {
            keydown(event) {
                if (event.key === 'Escape') {
                    event.preventDefault();
                    this.fullscreen = false;
                }
            },
        },

        mounted() {
            window.addEventListener('keydown', this.keydown);
        },

        beforeDestroy() {
            window.removeEventListener('keydown', this.keydown);
        },

    };

</script>
