<template>
    <div class="InlineVideoBlock" @click.stop="click" @contextmenu="$emit('contextmenu', $event)">
        <div :style="frameStyle">
            <video :id='playerElementId' disablePictureInPicture />
        </div>
        <div class="PlayButton" v-if="paused"></div>
        <div class="Waiting" v-if="waiting"></div>
        <div class="Rewind" v-if="rewindVisible" @click.stop="rewind"><div class="RewindClick"></div></div>
        <div class="FastForward" v-if="fastForwardVisible" @click.stop="fastForward"><div class="FastForwardClick"></div></div>
        <div class="BufferProgress" :style='{ width: bufferProgress }'></div>
        <div class="PlayProgress" :style='{ width: playProgress }'></div>
    </div>
</template>

