      height: 100px;
      margin: 50px;
    }

    .InlineVideoBlock video {
        width: 100%;

        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
    }

    .BufferProgress {
        position: absolute;
        top: 0px;
        height: 5px;
        background-color: rgba(128, 128, 128, 0.75);
    }

    .PlayProgress {
        position: absolute;
        top: 0px;
        height: 5px;
        background-color: rgba(200, 200, 200, 0.75);
    }
</style>

<script>
    const rewindStep = 10;

    export default {
        data: function() {
            return {
              paused: null,
              waiting: true,
              bufferProgress: '0%',
              playProgress: '0%',
              bufferPosition: null,
              playPosition: null,
              duration: null
            };
        },

        props: {
            block: {
                type: Object,
                required: true,
            },
        },

        mounted() {
            const hlsjscdn = document.createElement("script");
            hlsjscdn.setAttribute("src", "https://cdn.jsdelivr.net/npm/hls.js@latest");
            document.head.appendChild(hlsjscdn);

            this.setWindowOnResize();

            this.loadHls();
        },

        computed: {
            playerElementId() {
                return this.block.key
                    ? "video-player-" + this.block.key.replace(" ", "-")
                    : `video–player–${Math.random().toString(36).substring(2, 15) + Math.random().toString(36).substring(2, 15)}`
            },

            frameStyle() {
                if (this.block.videoAspectRatio) {
                    return {
                        paddingBottom: `${this.block.videoAspectRatio[1] / this.block.videoAspectRatio[0] * 100}%`,
                        position: 'relative',
                        background: 'black',
                    }
                }
            },

            rewindVisible() {
                return (
                    this.paused === true &&
                    this.duration !== null &&
                    this.playPosition !== null &&
                    this.playPosition > 0
                );
            },

            fastForwardVisible() {
                return (
                    this.paused === true &&
                    this.bufferPosition !== null &&
                    this.playPosition !== null &&
                    this.playPosition + rewindStep < this.bufferPosition
                );
            },
        },

        methods: {
            click() {
                if (null === this.paused) {
                    return;
                }

                const videoNode = document.getElementById(this.playerElementId);
                if (videoNode.paused) {
                  videoNode.play();
                } else {
                  videoNode.pause();
                }
            },

            rewind() {
                const videoNode = document.getElementById(this.playerElementId);
                if (videoNode.currentTime > rewindStep) {
                    videoNode.currentTime -= rewindStep;
                } else {
                    videoNode.currentTime = 0;
                }
                this.updateProgress();
            },

            fastForward() {
                const videoNode = document.getElementById(this.playerElementId);
                if (videoNode.currentTime + rewindStep < this.bufferPosition) {
                    videoNode.currentTime += rewindStep;
                } else {
                    videoNode.currentTime = this.bufferPosition;
                }
                this.updateProgress();
            },

            loadHls() {
                // Have to wait for the Hls JS player resource to load into
                // the page before can display the player
                if (undefined === window.Hls) {
                    window.setTimeout(() => this.loadHls(), 100);
                    return;
                }

                const videoNode = document.getElementById(this.playerElementId);
                this.adjustDisposition(videoNode);

                videoNode.disablePictureInPicture = true;

                videoNode.addEventListener('ended', () => {
                    if (this.block.onVideoEnd) {
                        this.$emit("navigate", this.block.onVideoEnd);
                    }
                });

                videoNode.addEventListener('play', () => {
                    this.paused = false;
                });

                videoNode.addEventListener('pause', () => {
                    this.waiting = false;
                    this.paused = true;
                });

                videoNode.addEventListener('waiting', () => {
                    this.waiting = true;
                });

                videoNode.addEventListener('playing', () => {
                    this.waiting = false;
                });

                videoNode.addEventListener('canplay', () => {
                    this.updateProgress();
                    if (null === this.paused) {
                        this.waiting = false;
                        this.paused = true;
                    }
                });

                // Unreliable event - not always executed
                videoNode.addEventListener('progress', (event) => {
                    this.updateProgress();
                });

                videoNode.addEventListener('timeupdate', (event) => {
                    this.updateProgress();
                })

                const hls = new Hls();
                hls.loadSource(this.block.hlsUrl);
                hls.attachMedia(videoNode);
                videoNode.hls = hls;

                // Freeze the quality at the lowest level (360 on Integros)
                hls.currentLevel = 0;
            },

            updateProgress() {
                const videoNode = document.getElementById(this.playerElementId);
                const duration = this.duration = videoNode.duration;

                this.playProgress = (100 * videoNode.currentTime / duration).toFixed(2) + '%';
                this.playPosition = videoNode.currentTime;

                if (videoNode.buffered.length) {
                    const bufferLastChunk = videoNode.buffered.end(videoNode.buffered.length - 1);
                    const bufferEnd = this.bufferPosition = bufferLastChunk;
                    this.bufferProgress = (100 * bufferEnd / duration).toFixed(2) + '%';
                }
            },

            adjustDisposition(videoNode) {
                const frameNode = videoNode.parentNode;
                const blockNode = videoNode.parentNode.parentNode;
                blockNode.style.maxHeight = window.innerHeight + 'px';
                const offset = (frameNode.clientHeight - blockNode.clientHeight) * 0.33;
                frameNode.style.marginTop = '-' + offset + 'px';
            },

            setWindowOnResize() {
                if (undefined === window.inlineVideoList) {
                    window.inlineVideoList = [];
                    // Resize all inline video blocks
                    window.addEventListener('resize', () => {
                        const videoList =  document.querySelectorAll('.InlineVideoBlock video');
                        videoList.forEach(video => this.adjustDisposition(video));
                    });
                }
            }
        }
    }
</script>
