<template>

    <div class="Logo" :class="classes" :style="styles" @click="click">
        <template v-if="! ( params.image && params.image !== '' )">
            <template v-if="params.iconCssClass && params.iconCssClass != ''">
                <i :class="params.iconCssClass" />
            </template>
            <template v-else-if="params.appIcon">
                <i class="fas" :class="{ ['fa-' + params.appIcon]: true }" />
            </template>
            <template v-else>
                {{ caption }}
            </template>
        </template>
    </div>

</template>

<style>

    .Logo {
        height: 50px;
        width: 50px;
        border-radius: 50px;

        background-size: cover;

        display: flex;
        font-size: 23px;
        align-items: center;
        justify-content: center;

        position: relative;
    }

    .Logo.__medium {
        height: 30px;
        width: 30px;
    }

    .Logo.__large {
        height: 72px;
        width: 72px;
    }

    .Logo.__small {
        height: 24px;
        width: 24px;
    }

    .Logo.__mini {
        height: 15px;
        width: 15px;
    }

    .Logo.__default:after {
        content: " ";
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;

        background: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0));

        border-radius: 50px;
    }


</style>

<script>

    export default {
        props: {
            params: {
                type: Object,
                required: true,
            },

            size: {
                type: String,
                default: 'normal',
            },

            disableStylish: {
                type: Boolean,
                required: false,
                default: false
            },
        },

        methods: {
            click() {
                this.$emit('click')
            },

            navigate(block) {
                this.$emit('navigate', block )
            },
        },

        computed: {
            caption() {
                return (this.params.image ? '' : (this.params.text ? this.params.text.toString() : null)) || '';
            },

            styles() {
                let fontSize = 23;

                if (this.size === 'medium') {
                    fontSize = 16;
                } else if (this.size === 'small') {
                    fontSize = 12;
                } else if (this.size === 'mini') {
                    fontSize = 8;
                } else if (this.size === 'large') {
                    fontSize = 36;
                }

                return {
                    color: this.params.color,
                    backgroundColor: this.params.bgColor,
                    backgroundImage: this.params.image ? `url(${this.params.image})` : null,
                    fontSize: (this.caption.length > 2 ? fontSize * (2.5 / this.caption.length) : fontSize) + 'px',
                };
            },

            classes() {
                return {
                    __default: !this.params.image && !!this.params.bgColor && ! this.disableStylish,
                    __mini: this.size === 'mini',
                    __small: this.size === 'small',
                    __medium: this.size === 'medium',
                    __large: this.size === 'large',
                }
            },
        },

    };

</script>
