<template>

    <div class="SectionBlock" @contextmenu="$emit('contextmenu', $event)">
        <div class="SectionBlock__title">{{ block.title }}</div>
        <block-list :blocks="block.blocks" @navigate="navigate" @refresh="refresh" />
    </div>

</template>

<style>

    .SectionBlock__title {
        color: gray;
        -webkit-font-smoothing: antialiased;
        padding: 15px 10px 5px 10px;
        text-transform: uppercase;
        letter-spacing: 1px;
        font-size: 12px;
    }

</style>

<script>

    export default {

        name: 'section-block',

        props: {
            block: {
                type: Object,
                required: true,
            },
        },

        methods: {
            navigate(block) {
                this.$emit('navigate', block);
            },

            refresh() {
                this.$emit('refresh');
            },
        },

    };

</script>
