<template>

    <modal v-if="files.length > 0" super-top>
        <content-block class="ModalFileUploader">
            <div class="ModalFileUploader__queue">
                <div v-for="file in files" class="ModalFileUploader__queue-file" :class="{ __canceled: file.canceled, __uploaded: file.uploaded }">
                    <div class="ModalFileUploader__queue-file-left">
                        <div class="ModalFileUploader__queue-file-info">
                            <div class="ModalFileUploader__queue-file-name">{{ file.file.name }}</div>
                            <div class="ModalFileUploader__queue-file-size">{{ file.sizeLabel }}</div>
                        </div>
                        <div class="ModalFileUploader__queue-file-progress">
                            <div class="ModalFileUploader__queue-file-progress-bar" :style="{ width: `${file.progress}%` }"></div>
                        </div>
                    </div>
                    <div class="ModalFileUploader__queue-file-right" @click="cancelUpload(file)">
                        <svg v-if="file.uploaded" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="currentColor">
                            <path d="M12 2c5.514 0 10 4.486 10 10s-4.486 10-10 10-10-4.486-10-10 4.486-10 10-10zm0-2c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm6.25 8.891l-1.421-1.409-6.105 6.218-3.078-2.937-1.396 1.436 4.5 4.319 7.5-7.627z"/>
                        </svg>
                        <svg v-else-if="file.canceled" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="currentColor">
                            <path d="M12 2c5.514 0 10 4.486 10 10s-4.486 10-10 10-10-4.486-10-10 4.486-10 10-10zm0-2c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm-1 6h2v8h-2v-8zm1 12.25c-.69 0-1.25-.56-1.25-1.25s.56-1.25 1.25-1.25 1.25.56 1.25 1.25-.56 1.25-1.25 1.25z"/>
                        </svg>
                        <svg v-else xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="currentColor">
                            <path d="M12 2c5.514 0 10 4.486 10 10s-4.486 10-10 10-10-4.486-10-10 4.486-10 10-10zm0-2c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm6 16.538l-4.592-4.548 4.546-4.587-1.416-1.403-4.545 4.589-4.588-4.543-1.405 1.405 4.593 4.552-4.547 4.592 1.405 1.405 4.555-4.596 4.591 4.55 1.403-1.416z"/>
                        </svg>
                    </div>
                </div>
            </div>

            <secondary-button @click="close()" :disabled="uploading">Закрыть</secondary-button>
        </content-block>
    </modal>

</template>

<style>

    .ModalFileUploader {
        width: 400px;
        max-width: 400px;

        z-index: 30000;
    }

    .ModalFileUploader__queue {
        margin-bottom: 20px;
    }

    .ModalFileUploader__queue-file {
        padding: 10px 0;

        display: flex;
    }

    .ModalFileUploader__queue-file-left {
        flex-grow: 2;
        overflow: hidden;
    }

    .ModalFileUploader__queue-file-right {
        flex-shrink: 0;
        width: 30px;

        display: flex;
        align-items: center;
        justify-content: flex-end;

        color: #cfcfcf;
        cursor: pointer;
    }

    .ModalFileUploader__queue-file.__canceled .ModalFileUploader__queue-file-right {
        color: #a21e1e;
    }

    .ModalFileUploader__queue-file.__uploaded .ModalFileUploader__queue-file-right {
        color: #51a21e;
    }

    .ModalFileUploader__queue-file-info {
        display: flex;
        align-items: baseline;
    }

    .ModalFileUploader__queue-file-name {
        font-weight: bold;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;

        flex-grow: 2;
    }

    .ModalFileUploader__queue-file-size {
        white-space: nowrap;
        flex-shrink: 0;
        font-size: 12px;
        font-weight: 100;
        color: gray;

        padding-left: 10px;
    }

    .ModalFileUploader__queue-file-progress {
        margin-top: 10px;
        width: 100%;
        height: 10px;
        background-color: #e0e0cc;
        border-radius: 3px;
    }

    .ModalFileUploader__queue-file-progress-bar {
        height: 10px;
        background-color: #3b73db;
        border-radius: 3px;
    }

    .ModalFileUploader__queue-file.__canceled .ModalFileUploader__queue-file-name {
        color: #a21e1e;
    }

    .ModalFileUploader__queue-file.__canceled .ModalFileUploader__queue-file-progress-bar {
        background-color: #a21e1e;
    }

    .ModalFileUploader__queue-file.__canceled .ModalFileUploader__queue-file-progress {
        background-color: #f1dbdb;
    }

    .ModalFileUploader__queue-file.__uploaded .ModalFileUploader__queue-file-progress-bar {
        background-color: #51a21e;
    }

    .ModalFileUploader__queue-file-progress {
        background-image: repeating-linear-gradient(-45deg, #d7d7d1, #d7d7d1 5px, #e0e0cc 5px, #e0e0cc 10px);
        animation: progress 2s linear infinite;
        background-size: 150% 100%;
    }

    .ModalFileUploader__queue-file.__canceled .ModalFileUploader__queue-file-progress {
        animation: none;
        background-image: none;
    }

    @keyframes progress{
        0% { background-position: 0 0; }
        100% { background-position: -70px 0; }
    }

</style>

<script>

    import Vue from 'vue';

    import builder from "@/modules/common/components/presenter/blocks/helper/builder";
    import SecondaryButton from "@/modules/common/components/ui/SecondaryButton";
    import store from "@/store";

    import ContentBlock from "@/modules/common/components/ui/ContentBlock";
    import Modal from "@/modules/common/components/ui/Modal";
    import PrimaryButton from "@/modules/common/components/ui/PrimaryButton";

    export default {

        components: {
            SecondaryButton,
            ContentBlock,
            Modal,
            PrimaryButton,
        },

        props: {
            action: {
                type: Object,
                required: true,
            },

            navigate: {
                type: Function,
                required: true,
            },
        },

        computed: {
            files() {
                return store.uploadingFiles
            },

            uploading() {
                return !!this.files.find(i => !i.uploaded && !i.canceled)
            },
        },

        methods: {
            close() {
                store.uploadingFiles = []
                this.$emit('close')
            },

            fileCatch(files) {
                files.forEach(file => {
                    builder.addUploadFile(store.accountId, file, async (hash, uploaderFile) => {
                        try {
                            await this.navigate({
                                type: "apiCall",
                                url: this.action.submitUrl,
                                apiParams: {
                                    file: {
                                        hash: hash,
                                        name: file.name
                                    }
                                },
                            })

                            if (!this.files.find(i => !i.uploaded || i.canceled)) {
                                this.close()
                            }
                        } catch (_) {
                            uploaderFile.canceled = true
                            uploaderFile.uploaded = false
                        }
                    })
                })
            },

            select() {
                this.$root.$emit('file-select')
            },

            cancelUpload(file) {
                Vue.set(file, 'canceled', true)
                file.cancelToken.cancel()
            },
        },

        mounted() {
            this.$root.$on('file-catch', this.fileCatch)

            this.select()
        },

        beforeDestroy() {
            this.$root.$off('file-catch', this.fileCatch)
        },

    }

</script>
