<template>

    <div class="AudioBlock" :style="styles"  @contextmenu="$emit('contextmenu', $event)">
        <audio class="AudioBlock__audio" preload="metadata" controls style="width: 100%">
            <source :src="downloadUrl" />
        </audio>
        <div class="AudioBlock__labels" v-if="block.title || block.description">
        <span class="AudioBlock__title">{{block.title}}</span>
        <span class="AudioBlock__description">{{block.description}}</span>
        </div>
    </div>

</template>

<style>

    .AudioBlock {
        margin-bottom: 10px;
    }

    .AudioBlock__audio {
        max-width: 100%;
        outline: none;
    }

    .AudioBlock__labels {
        padding-top: 15px;
    }

    .AudioBlock__title {


    }
    .AudioBlock__description {
        color: #999;

    }

</style>

<script>

    import style from './helper/style';
    import fileStore from "@/fileStore";

    export default {

        props: {
            block: {
                type: Object,
                required: true,
            },
        },

        computed: {
            styles() {
                return style.container(this.block);
            },
            downloadUrl() {
                return this.block.file ? fileStore.getDownloadUrl( this.block.file.hash ) : this.block.downloadUrl;
            },
        },

    };

</script>
