<template>
    <div class="Navigator">
        <slot :navigate="navigate" />
    </div>
</template>

<style>

    .YandexKassaPayFormOverlay {
        position: fixed;
        z-index: 1000000;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        display: flex;
        justify-content: center;
        background-color: rgba(0,0,0,0.8);
        padding-top: 100px;
    }

</style>

<script>

    import store from "@/store";
    import screenStore from "@/store/screen";

    export default {
        methods: {
            receiveWindowMessage(event) {
                try {
                    const action = JSON.parse(event.data);
                    if (action && action.type === "appHandleAction" && action.data) {
                        this.processAction(action.data, true);
                    }
                } catch (e) {
                    // Молчание дороже золота (с)
                }
            },

            async processAction(action, ignoreAppHandleAction = false) {
                // Если страница загружена в мобильном приложении - прокидываем действие в него
                if (!ignoreAppHandleAction && typeof window.appHandleAction === "function") {
                    return window.appHandleAction(action);
                }

                if ( action.confirm ) {
                    if (! confirm(action.confirm) ){
                        return ;
                    }
                }

                if (action.type === "navigate") {
                    if (action.url.startsWith("http")) {
                        const urlComponents = new URL(action.url);

                        // Переход на страницу оплаты
                        if (['/pay/yandex.html', '/pay/cloudpayment.html'].includes(urlComponents.pathname)) {
                            location.href = action.url
                            return
                        }

                        if (urlComponents.hostname !== store.host) {
                            if (action.openInCurrentScreen === true) {
                                let queryParams = new URLSearchParams(window.location.search)
                                queryParams.set('url', action.url)

                                this.$emit("url", { ...action, url: '?' + queryParams.toString() });
                                return;
                            }

                            if (store.accessToken && store.refreshToken) {
                                try {
                                    await this.$api.post("/auth/migrate/credentials", {
                                        access_token: store.accessToken,
                                        refresh_token: store.refreshToken
                                    });
                                } catch (_) {
                                    // Ну не получилось...
                                    console.error("Migrate credentials failed =(");
                                }
                            }

                            location.href = action.url;
                            return;
                        }
                    }

                    this.$emit("url", action);
                } else if (action.type === "goBack") {
                    this.$emit("back", action);
                } else if (action.type === "confirmPhone") {
                    store.needAuth = true;
                } else if (action.type === "refresh") {
                    this.$emit("refresh");
                } else if (action.type === "resetSearch") {
                    this.$emit("resetSearch");
                } else if (action.type === "copyToClipboard") {
                    await navigator.clipboard.writeText(action.value);
                    this.$emit("toast", action.message ? action.message : "Скопировано в буфер обмена");
                } else if (action.type === "showDialog") {
                    this.$emit("showDialog", action);
                } else if (action.type === "attachMedia") {
                    this.$emit("attachMedia", action);
                } else if (action.type === "showContextMenu") {
                    this.$emit("showContextMenu", action);
                } else if (action.type === "showToast") {
                    this.$emit("toast", action.toast);
                } else if (action.type === "signOut") {
                    window.localStorage.clear();
                    location.href = location.href;
                } else if (action.type === "apiCall") {
                    const response = await this.$api.post(action.url, action.apiParams);

                    if (action.callback) {
                        await action.callback(response);
                    }

                    if (response.appAction) {
                        const actions = response.appAction instanceof Array ? response.appAction : [response.appAction];

                        for (let action of actions) {
                            await this.processAction(action);
                        }
                    }

                    if (response.appScreens) {
                        Object.keys(response.appScreens).map(url => screenStore.set(url, response.appScreens[url]));
                    }
                } else if (action.type === "requestPayment") {
                    if (action.integration === 'yandexkassa') {
                        const response = await this.$api.post('/api/payment/yandexkassa/prepare', {
                            amount: action.amount,
                            description: action.description,
                            token: action.token,
                        })

                        const script = document.createElement("script");
                        script.src = "https://kassa.yandex.ru/checkout-ui/v2.js";
                        script.onload = () => {
                            const div = document.createElement("div")
                            div.id = "payment-form"
                            div.className = "YandexKassaPayFormOverlay"
                            document.body.appendChild(div)

                            const checkout = new window.YandexCheckout({
                                confirmation_token: response.confirmation_token,
                                return_url: window.location.href,
                                error_callback: function () {

                                },
                            });

                            checkout.render('payment-form');
                        };
                        document.body.appendChild(script);
                    } else if (action.integration === 'cloudpayments') {
                        const script = document.createElement("script");
                        script.src = "https://widget.cloudpayments.ru/bundles/cloudpayments";
                        script.onload = () => {
                            const widget = new cp.CloudPayments();
                            widget.charge(
                                {
                                    publicId: action.payload.publicId,
                                    description: action.description,
                                    amount: action.amount,
                                    currency: "RUB", //валюта
                                    invoiceId: action.token,
                                    skin: "mini" //дизайн виджета
                                },
                            );
                        };
                        document.body.appendChild(script);
                    } else {
                        console.log(action)
                    }
                } else {
                    this.$emit("unknown", action);
                }
            },

            async navigate(block) {
                if (block.onClick) {
                    const actions = block.onClick instanceof Array ? block.onClick : [block.onClick];

                    for (let action of actions) {
                        await this.processAction(action);
                    }
                } else if (block.type === "apiCall") {
                    await this.processAction(block);
                }
            },
        },

        mounted() {
            window.document.addEventListener("message", this.receiveWindowMessage, true)
            window.addEventListener("message", this.receiveWindowMessage, true)
        },

        beforeDestroy() {
            window.document.removeEventListener("message", this.receiveWindowMessage, true)
            window.removeEventListener("message", this.receiveWindowMessage, true)
        },
    }

</script>
