<template>

    <div class="ChatMessages">
        <div v-for="(part, index) in preparedMessages" :key="index">
            <div class="ChatMessage__day">
                <div class="ChatMessage__day-text">{{ part.dateFormated }}</div>
            </div>
            <chat-message v-for="message in part.messages" :key="message.id" ref="messages"
                          :message="message"
                          :can-pin="canPin"
                          :last-read="lastReadId === message.id && lastReadId !== lastMessageId"
                          :user="user"
                          @navigate="navigate"
                          @reply="$emit('reply', $event)"
                          @edit="$emit('edit', $event)"
                          @delete="$emit('delete', $event)"
                          @pin="$emit('pin', $event)"
                          @react="$emit('react', $event)"
                          @scroll-to="scrollTo"
                          @contextMenu="$emit('contextMenu', $event, message)"
            />
        </div>
    </div>

</template>

<style>

    .ChatMessage__day {
        position: sticky;
        top: 0;
        height: 31px;

        display: flex;
        align-items: center;
        justify-content: center;

        z-index: 100;
    }

    .ChatMessage__day-text {
        background-color: white;
        padding: 5px 10px;
        border-radius: 30px;
        font-weight: bold;
        font-size: 13px;
        color: #999999;
    }

</style>

<script>

    import ChatMessage from '@/modules/chat/components/ChatMessage';
    import datetime from "@/services/datetime";

    export default {

        props: {
            messages: {
                type: Array,
                required: true,
            },

            user: {
                type: [Object, null],
            },

            lastRead: {
                type: Number,
                default: 0,
            },

            lastReadId: {
                type: String,
                default: null,
            },

            lastMessageId: {
                type: String,
                default: null,
            },

            canPin: {
                type: Boolean,
                default: false,
            },
        },

        components: {
            ChatMessage,
        },

        computed: {
            preparedMessages() {
                return [...this.messages]
                .sort((a, b) => {
                    return a.createdAt - b.createdAt;
                })
                .reduce((result, message) => {
                    let sameAuthor = false;
                    let sameDay = false;

                    if (result.length > 0) {
                        const lastMessage = result[result.length - 1];
                        sameAuthor = lastMessage.author && message.author && lastMessage.author.id === message.author.id;
                        sameDay = datetime.sameDate(lastMessage.createdAt, message.createdAt);

                        if (lastMessage.type === 'blocks') {
                            sameAuthor = false
                            sameDay = false
                        }

                        if (message.type === 'blocks') {
                            sameAuthor = true
                            sameDay = true
                        }
                    }

                    result.push({
                        ...message,
                        sameAuthor,
                        sameDay,
                        readed: message.createdAt <= this.lastRead,
                    });

                    return result;
                }, [])
                .reduce((result, message) => {
                    const date = datetime.truncTime(message.createdAt).getTime();
                    let messages = [];

                    if (result.length > 0 && result[result.length - 1].date === date) {
                        messages = result[result.length - 1].messages;
                    } else {
                        result.push({
                            date: date,
                            dateFormated: datetime.formatDate(date),
                            messages,
                        });
                    }

                    messages.push(message);

                    return result;
                }, []);
            },
        },

        methods: {
            navigate(block) {
                this.$emit('navigate', block);
            },

            scrollTo(message) {
                this.$emit('scroll-to', message);
            },
        },

    };

</script>
