<template>

    <div class="VectorImageEditor" v-if="width * height !== 0">
        <div class="VectorImageEditor__content">
            <div class="VectorImageEditor__content-tools">
                <div class="VectorImageEditor__tool" :class="{ __selected: type === types.ArrowDrawable }" @click="type = types.ArrowDrawable">
                    <svg width="18" height="18" viewBox="0 0 24 24" fill="currentColor">
                        <path d="M21.883 12l-7.527 6.235.644.765 9-7.521-9-7.479-.645.764 7.529 6.236h-21.884v1h21.883z"/>
                    </svg>
                </div>
                <div class="VectorImageEditor__tool" :class="{ __selected: type === types.FreePathDrawable }" @click="type = types.FreePathDrawable">
                    <svg width="18" height="18" viewBox="0 0 24 24" fill="currentColor">
                        <path d="M8.071 21.586l-7.071 1.414 1.414-7.071 14.929-14.929 5.657 5.657-14.929 14.929zm-.493-.921l-4.243-4.243-1.06 5.303 5.303-1.06zm9.765-18.251l-13.3 13.301 4.242 4.242 13.301-13.3-4.243-4.243z"/>
                    </svg>
                </div>
                <div class="VectorImageEditor__tool" :class="{ __selected: type === types.EllipseDrawable }" @click="type = types.EllipseDrawable">
                    <svg width="18" height="18" viewBox="0 0 24 24" fill="currentColor">
                        <path d="M12 1c6.065 0 11 4.935 11 11s-4.935 11-11 11-11-4.935-11-11 4.935-11 11-11zm0-1c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12z"/>
                    </svg>
                </div>
                <div class="VectorImageEditor__tool" :class="{ __selected: type === types.RectDrawable }" @click="type = types.RectDrawable">
                    <svg width="18" height="18" viewBox="0 0 24 24" stroke="currentColor">
                        <rect x="2" y="2" width="20" height="20" fill="none" stroke-width="1" />
                    </svg>
                </div>

                <div class="VectorImageEditor__tool-divider"></div>

                <div v-for="color in colors" class="VectorImageEditor__color" :class="{ __selected: color === currentColor }" :style="{ 'background-color': color }" @click="setColor(color)" />

                <div class="VectorImageEditor__tool-divider"></div>

                <div class="VectorImageEditor__tool" :class="{ __disabled: items.length === 0 }" @click="undo">
                    <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24" fill="currentColor">
                        <path d="M12 0c-3.31 0-6.291 1.353-8.459 3.522l-2.48-2.48-1.061 7.341 7.437-.966-2.489-2.488c1.808-1.808 4.299-2.929 7.052-2.929 5.514 0 10 4.486 10 10s-4.486 10-10 10c-3.872 0-7.229-2.216-8.89-5.443l-1.717 1.046c2.012 3.803 6.005 6.397 10.607 6.397 6.627 0 12-5.373 12-12s-5.373-12-12-12z"/>
                    </svg>
                </div>
            </div>
            <div class="VectorImageEditor__content-image">
                <v-stage class="VectorImageEditor__content-image-stage"
                         :config="configKonva"
                         @mousedown="mouseDown"
                         @mouseup="mouseUp"
                         @mousemove="mouseMove"
                         @mouseleave="mouseLeave"
                         @contenttouchstart="touchstart"
                         @touchmove="touchmove"
                         @touchend="touchend"
                         ref="stage"
                >
                    <v-layer>
                        <v-image :config="{ image: original, width, height }"/>
                        <component v-for="item, index in items" :key="index" :is="item.type" :config="item.config" />
                    </v-layer>
                </v-stage>
            </div>
        </div>
    </div>

</template>

<style>

    .VectorImageEditor {
        background-color: white;
        border-radius: 5px;
    }

    .VectorImageEditor__content-tools {
        display: flex;
        flex-wrap: wrap;

        border-bottom: 1px solid #eee;
        padding: 10px;
    }

    .VectorImageEditor__content-image {
        padding: 10px;
        background-color: #f4f4f4;
    }

    .VectorImageEditor__content-image-stage {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .VectorImageEditor__color {
        width: 24px;
        height: 24px;

        border-radius: 24px;

        box-shadow: inset 0 0 0 2px rgba(255, 255, 255, 0.3);
        border: 1px solid rgba(0, 0, 0, 0.1);
        display: inline-block;

        margin-right: 5px;

        cursor: pointer;
        transition: box-shadow 0.4s
    }

    .VectorImageEditor__color.__selected {
        box-shadow: inset 0 0 0 6px rgba(255, 255, 255, 0.7);
    }

    .VectorImageEditor__tool {
        width: 24px;
        height: 24px;

        margin-right: 5px;

        cursor: pointer;
        border-radius: 3px;

        display: inline-flex;
        align-items: center;
        justify-content: center;
    }

    .VectorImageEditor__tool.__selected {
        background-color: #63a3d0;
        color: white;
    }

    .VectorImageEditor__tool.__disabled {
        color: #ccc;
    }

    .VectorImageEditor__tool-divider {
        width: 25px;
        position: relative;
    }

    .VectorImageEditor__tool-divider:after {
        content: " ";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 10px;
        border-left: 1px solid #ccc;
    }

</style>

<script>

    class Drawable {
        constructor(startX, startY, color, lineWidth) {
            this.type = 'Unknown';

            this.startX = startX;
            this.startY = startY;
            this.lastX = startX;
            this.lastY = startY;

            this.color = color;
            this.lineWidth = lineWidth;
        }

        registerMovement(x, y) {
            this.lastX = x;
            this.lastY = y;
        }
    }

    class LineDrawable extends Drawable {
        constructor(startX, startY, color, lineWidth) {
            super(startX, startY, color, lineWidth);

            this.type = 'v-line';
        }

        config() {
            return {
                points: [
                    this.startX, this.startY,
                    Math.abs(this.lastX - this.startX) < 10 ? this.startX : this.lastX,
                    Math.abs(this.lastY - this.startY) < 10 ? this.startY : this.lastY
                ],
                stroke: this.color,
                fill: this.color,
                strokeWidth: this.lineWidth,
                draggable: false,
            };
        }
    }

    class ArrowDrawable extends Drawable {
        constructor(startX, startY, color, lineWidth) {
            super(startX, startY, color, lineWidth);

            this.type = 'v-arrow';
        }

        config() {
            let pointerLength = 20;
            let pointerWidth = 10;

            if (Math.abs(this.startX - this.lastX) < 30 || Math.abs(this.startX - this.lastX) < 30) {
                pointerLength = Math.min(20, Math.round(Math.sqrt(Math.pow(Math.abs(this.startX - this.lastX), 2) + Math.pow(Math.abs(this.startY - this.lastY), 2))));
                pointerWidth = 10;
            }

            return {
                points: [this.startX, this.startY, this.lastX, this.lastY],
                stroke: this.color,
                fill: this.color,
                strokeWidth: this.lineWidth,
                pointerLength : pointerLength,
                pointerWidth : pointerWidth,
                draggable: false,
            };
        }
    }

    class FreePathDrawable extends Drawable {
        constructor(startX, startY, color, lineWidth) {
            super(startX, startY, color, lineWidth);

            this.type = 'v-line';
            this.points = [startX, startY];
        }

        registerMovement(x, y) {
            super.registerMovement(x, y);

            this.points = [ ...this.points, x, y ];
        }

        config() {
            return {
                points: this.points,
                stroke: this.color,
                fill: this.color,
                strokeWidth: this.lineWidth,
                draggable: false,
            };
        }
    }

    class RectDrawable extends Drawable {
        constructor(startX, startY, color, lineWidth) {
            super(startX, startY, color, lineWidth);

            this.type = 'v-rect';
        }

        config() {
            return {
                x: this.startX,
                y: this.startY,
                width: this.lastX - this.startX,
                height: this.lastY - this.startY,
                stroke: this.color,
                strokeWidth: this.lineWidth,
                draggable: false,
            };
        }
    }

    class EllipseDrawable extends Drawable {
        constructor(startX, startY, color, lineWidth) {
            super(startX, startY, color, lineWidth);

            this.type = 'v-ellipse';
        }

        config() {
            return {
                x: this.startX + (this.lastX - this.startX) / 2,
                y: this.startY + (this.lastY - this.startY) / 2,
                radiusX: Math.abs((this.lastX - this.startX) / 2),
                radiusY: Math.abs((this.lastY - this.startY) / 2),
                stroke: this.color,
                strokeWidth: this.lineWidth,
                draggable: false,
            };
        }
    }

    export default {

        props: {
            image: {
                type: String,
                required: true,
            },
        },

        data() {
            return {
                down: false,
                startX: 0,
                startY: 0,

                original: null,
                height: 0,
                width: 0,

                colors: [
                    '#001f3f', '#0074d9', '#7fdbff', '#3d9970', '#01ff70', '#ffdc00',
                    '#ff851b', '#ff4136', '#85144b', '#f012be', '#b10dc9', '#111111', '#aaaaaa', '#ffffff',
                ],

                types: {
                    LineDrawable,
                    ArrowDrawable,
                    FreePathDrawable,
                    RectDrawable,
                    EllipseDrawable,
                },

                type: ArrowDrawable,
                currentColor: '#ff4136',
                currentLineWidth: 4,

                items: [],
                currentItem: null,
            };
        },

        computed: {
            configKonva() {
                return {
                    width: this.width,
                    height: this.height,
                };
            },
        },

        methods: {
            touchstart(event) {
                this.pointerDown(
                    event.evt.touches[0].clientX - event.currentTarget.content.offsetLeft,
                    event.evt.touches[0].clientY - event.currentTarget.content.offsetTop
                );
            },

            touchmove(event) {
                if (this.down) {
                    this.pointerMove(
                        event.evt.touches[0].clientX - event.currentTarget.content.offsetLeft,
                        event.evt.touches[0].clientY - event.currentTarget.content.offsetTop
                    );
                }
            },

            touchend(event) {
                this.pointerUp();
            },

            mouseDown(event) {
                this.pointerDown(event.evt.layerX, event.evt.layerY);
            },

            mouseUp() {
                this.pointerUp();
            },

            mouseMove(event) {
                if (this.down) {
                    this.pointerMove(event.evt.layerX, event.evt.layerY);
                }
            },

            mouseLeave() {
                this.down = false;
            },

            pointerDown(x, y) {
                this.down = true;

                this.startX = x;
                this.startY = y;

                const item = new this.type(x, y, this.currentColor, 4);
                this.currentItem = {
                    item: item,
                    type: item.type,
                    config: item.config(),
                };

                this.items.push(this.currentItem);
            },

            pointerUp() {
                this.down = false;
            },

            pointerMove(x, y) {
                this.currentItem.item.registerMovement(x, y);
                this.currentItem.config = this.currentItem.item.config();
            },

            keyDown(event) {
                if (this.items.length > 0 && event.code === 'KeyZ' && (event.metaKey || event.ctrlKey)) {
                    event.preventDefault();
                    event.stopPropagation();

                    this.undo();
                }

                if (event.code === 'Enter') {
                    event.preventDefault();
                    event.stopPropagation();

                    this.submit();
                }

                if (event.code === 'Escape') {
                    event.preventDefault();
                    event.stopPropagation();

                    if (this.down) {
                        this.undo();
                    } else {
                        this.cancel();
                    }
                }
            },

            setColor(color) {
                this.currentColor = color;
            },

            submit() {
                function dataURItoBlob(dataURI) {
                    // convert base64 to raw binary data held in a string
                    // doesn't handle URLEncoded DataURIs - see SO answer #6850276 for code that does this
                    let byteString = atob(dataURI.split(',')[1]);
                    // separate out the mime component
                    let mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];
                    // write the bytes of the string to an ArrayBuffer
                    let ab = new ArrayBuffer(byteString.length);
                    let dw = new DataView(ab);
                    for(let i = 0; i < byteString.length; i++) {
                        dw.setUint8(i, byteString.charCodeAt(i));
                    }
                    // write the ArrayBuffer to a blob, and you're done
                    return new Blob([ab], {type: mimeString});
                }

                // Если мы ничего не рисовали, то шлем оригинальную картинку
                if (this.items.length > 0) {
                    this.$emit('submit', dataURItoBlob(this.$refs.stage.getStage().toDataURL()));
                } else {
                    this.$emit('submit', dataURItoBlob(this.image));
                }
            },

            cancel() {
                this.$emit('cancel');
            },

            undo() {
                this.items.pop();
            },
        },

        mounted() {
            this.original = new Image();

            const maxHeight = window.innerHeight - 150;
            const maxWidth = window.innerWidth - 60;

            this.original.onload = () => {
                let height = this.original.height / (window.devicePixelRatio || 1);
                let width = this.original.width / (window.devicePixelRatio || 1);

                let coeff = 1;
                let coeffHeight = 1;
                let coeffWidth = 1;
                if (height > maxHeight) {
                    coeffHeight = maxHeight / height;
                }

                if (width > maxWidth) {
                    coeffWidth = maxWidth / width;
                }

                if (coeffHeight <= coeffWidth) {
                    coeff = coeffHeight;
                } else {
                    coeff = coeffWidth;
                }

                this.height = height * coeff;
                this.width = width * coeff;
            };

            this.original.src = this.image;

            document.addEventListener('keydown', this.keyDown);
        },

        destroyed() {
            document.removeEventListener('keydown', this.keyDown);
        },

    };

</script>
