<template>

    <textarea class="TextareaAutosize" :class="{ __error: error }"
              :style="computedStyles"
              v-model="currentValue"
              @change="change"
              @focus="resize"
              @input="input"
              ref="input"
    />

</template>

<style>

    .TextareaAutosize {
        padding: 8px 15px;
        font: inherit;
        outline: none;
        border-radius: 3px;
        border: 1px solid silver;
        width: 100%;
    }

    .TextareaAutosize::placeholder {
        color: silver;
        font-weight: 100;
    }

    .TextareaAutosize:focus {
        border-color: rgba(2, 121, 254, 0.38);
        box-shadow: 0 0 0 2px rgba(2, 121, 254, 0.38);
    }

    .TextareaAutosize.__error {
        background-color: #fcf4f4;
        border: 1px solid #fe7676;
    }

    .TextareaAutosize.__error:focus {
        border-color: rgba(254, 2, 57, 0.38);
        box-shadow: 0 0 0 2px rgba(254, 2, 57, 0.38);
    }

</style>

<script>

    export default {
        name: 'TextareaAutosize',

        props: {
            value: {
                type: [String, Number],
                default: ''
            },
            error: {
                type: Boolean,
                default: false,
            },
            autosize: {
                type: Boolean,
                default: true
            },
            minHeight: {
                type: [Number],
                'default': null
            },
            maxHeight: {
                type: [Number],
                'default': null
            },
            /*
             * Force !important for style properties
             */
            important: {
                type: [Boolean, Array],
                default: false
            },
            autofocus: {
                type: Boolean,
                default: false,
            },
        },
        data() {
            return {
                // data property for v-model binding with real textarea tag
                currentValue: this.value,
                // works when content height becomes more then value of the maxHeight property
                maxHeightScroll: false,
                height: 'auto'
            }
        },
        computed: {
            computedStyles() {
                if (!this.autosize) return {}
                return {
                    resize: !this.isResizeImportant ? 'none' : 'none !important',
                    height: this.height,
                    overflow: this.maxHeightScroll ? 'auto' : (!this.isOverflowImportant ? 'hidden' : 'hidden !important')
                }
            },
            isResizeImportant() {
                const imp = this.important
                return imp === true || (Array.isArray(imp) && imp.includes('resize'))
            },
            isOverflowImportant() {
                const imp = this.important
                return imp === true || (Array.isArray(imp) && imp.includes('overflow'))
            },
            isHeightImportant() {
                const imp = this.important
                return imp === true || (Array.isArray(imp) && imp.includes('height'))
            }
        },
        watch: {
            value(val) {
                this.currentValue = val
            },
            currentValue(val) {
                this.$nextTick(this.resize)
            },
            minHeight() {
                this.$nextTick(this.resize)
            },
            maxHeight() {
                this.$nextTick(this.resize)
            },
            autosize(val) {
                if (val) this.resize()
            }
        },
        methods: {
            input(value) {
                this.$emit('input', this.currentValue);
            },
            change(value) {
                this.$emit('change', this.currentValue);
            },
            resize() {
                const important = this.isHeightImportant ? 'important' : ''
                this.height = `auto${important ? ' !important' : ''}`
                this.$nextTick(() => {
                    let contentHeight = this.$el.scrollHeight + 1

                    if (this.minHeight) {
                        contentHeight = contentHeight < this.minHeight ? this.minHeight : contentHeight
                    }

                    if (this.maxHeight) {
                        if (contentHeight > this.maxHeight) {
                            contentHeight = this.maxHeight
                            this.maxHeightScroll = true
                        } else {
                            this.maxHeightScroll = false
                        }
                    }

                    const heightVal = contentHeight + 'px'
                    this.height = `${heightVal}${important ? ' !important' : ''}`
                })

                return this
            },
            focus() {
                this.$refs.input.focus()
            }
        },
        mounted() {
            this.resize()

            if (this.autofocus) {
                this.$nextTick(() => this.focus())
            }
        }
    }
</script>
