<template>

    <div class="FooterBlock">
        <block-list :blocks="block.blocks"
                    @navigate="navigate"
                    @refresh="refresh"
                    @back="back"
        />
    </div>

</template>

<style>

    .FooterBlock {
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 100;

        width: 100%;

        border-top: 1px solid #eee;
        background-color: white;
    }

    .FooterBlock > div:first-child {
        margin-top: 0 !important;
    }

    .FooterBlock > div:last-child {
        margin-bottom: 0 !important;
    }

    @media screen and (max-width: 900px) {

        .FooterBlock {
            position: fixed;
        }

    }

</style>

<script>

    import BlockList from "./BlockList";
    import style from "./helper/style";

    export default {
        components: {
            BlockList
        },

        props: {
            block: {
                type: Object,
                required: true,
            },
        },

        computed: {
            containerStyles() {
                return style.container(this.block);
            },
        },

        methods: {
            navigate(block) {
                this.$emit('navigate', block);
            },

            refresh() {
                this.$emit('refresh');
            },
            back() {
                this.$emit('back');
            },
        }
    }

</script>
