<template>

    <div class="ContextMenu" @click="$emit('close')" @contextmenu.prevent="$emit('close')">
        <div class="ContextMenu__items" :style="styles" @click.stop ref="items">
            <div v-for="action in items" class="ContextMenu__item" @click="$emit('navigate', action)">
                <div class="ContextMenu__item-name">{{ action.name || action.title }}</div>
                <div v-if="false" class="ContextMenu__item-loader">
                    <loader :width="16" :height="16" />
                </div>
            </div>
        </div>
    </div>

</template>

<style>

    .ContextMenu {
        position: fixed;

        left: 0;
        top: 0;
        height: 100%;
        width: 100%;

        z-index: 10001;
    }

    .ContextMenu__items {
        position: absolute;
        padding: 5px 0;

        background-color: #f2f2f2;
        border: 1px solid #c0c0c038;
        border-radius: 5px;
        box-shadow: 0 0 10px -2px rgba(0,0,0,0.2);
        overflow: hidden auto;
    }

    .ContextMenu__item {
        display: flex;
        padding: 10px 20px;
        cursor: pointer;
    }

    .ContextMenu__item:hover {
        background-color: #2888cc;
        color: white;
    }

    .ContextMenu__item-loader {
        width: 30px;
        flex-shrink: 0;

        display: flex;
        align-items: baseline;
        justify-content: flex-end;
    }

    .ContextMenu__item-name {
        flex-grow: 2;

        max-width: 300px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }

</style>

<script>

    import Loader from "@/modules/common/components/ui/Loader";

    export default {
        components: {
            Loader
        },

        props: {
            items: {
                type: Array,
                default: () => [],
            },

            left: {
                type: Number,
                default: 0,
            },

            top: {
                type: Number,
                default: 0,
            },

            width: {
                type: Number,
                default: 0,
            },

            height: {
                type: Number,
                default: 0,
            },
        },

        data() {
            return {
                itemsHeight: 0,
                resultLeft: this.left,
            }
        },

        computed: {
            styles() {
                const top = Math.max(0, Math.round(this.top + this.height - this.itemsHeight))

                return {
                    left: Math.round(this.resultLeft) + 'px',
                    top: top + 'px',
                    width: this.width && this.width > 300 ? Math.round(this.width) + 'px' : 'auto',
                    maxHeight: '100vh',
                }
            },
        },

        watch: {
            items() {
                this.$nextTick(() => this.adjustPosition())
            },
        },

        methods: {
            keydown(event) {
                if (event.key === 'Escape') {
                    event.preventDefault()
                    this.$emit('close')
                }
            },

            adjustPosition() {
                if (this.top + this.height + this.$refs.items.clientHeight > window.innerHeight) {
                    this.itemsHeight = this.$refs.items.clientHeight + this.height
                }

                if (this.resultLeft + this.$refs.items.clientWidth > window.innerWidth) {
                    this.resultLeft = Math.max(0, this.left + this.width - this.$refs.items.clientWidth)
                }
            },
        },

        mounted() {
            this.adjustPosition()

            document.addEventListener('keydown', this.keydown)
        },

        beforeDestroy() {
            document.removeEventListener('keydown', this.keydown)
        },

    }

</script>
