<template>

    <div class="ImageEditor">
        <div>
            <div>
                <div class="ImageEditor__color" :style="{ 'background-color': 'rgba(200,0,0,1)' }" @click="setColor('rgba(200,0,0,0.1)')"></div>
                <div class="ImageEditor__color" :style="{ 'background-color': 'rgba(100,0,200,1)' }" @click="setColor('rgba(100,0,200,0.1)')"></div>
                <div class="ImageEditor__color" :style="{ 'background-color': 'rgba(0,200,0,1)' }" @click="setColor('rgba(0,200,0,0.1)')"></div>
                <div class="ImageEditor__color" :style="{ 'background-color': 'rgba(0,0,200,1)' }" @click="setColor('rgba(0,0,200,0.1)')"></div>
                <div class="ImageEditor__color" :style="{ 'background-color': 'rgba(0,100,200,1)' }" @click="setColor('rgba(0,100,200,0.1)')"></div>
                <div class="ImageEditor__color" :style="{ 'background-color': 'rgba(200,0,200,1)' }" @click="setColor('rgba(200,0,200,0.1)')"></div>
                <div class="ImageEditor__color" :style="{ 'background-color': 'rgba(250,200,100,1)' }" @click="setColor('rgba(250,200,100,0.1)')"></div>
            </div>
            <canvas ref="canvas" @mousedown="mouseDown" @mouseup="mouseUp" @mousemove="mouseMove" @mouseleave="mouseLeave"></canvas>
        </div>
    </div>

</template>

<style>

    .ImageEditor {
        position: fixed;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        z-index: 100;
        background-color: rgba(0, 0, 0, 0.5);

        display: flex;
        align-items: center;
        justify-content: center;
    }

    .ImageEditor__color {
        width: 32px;
        height: 32px;

        border-radius: 32px;

        box-shadow: inset 0 0 0 2px rgba(255, 255, 255, 0.5);
        display: inline-block;

        margin-right: 5px;

        cursor: pointer;
    }

</style>

<script>

    export default {

        props: {
            image: {
                type: String,
                required: true,
            },
        },

        data() {
            return {
                down: false,
                lastX: 0,
                lastY: 0,

                original: null,
            };
        },

        methods: {
            dot(x, y) {
                const context = this.$refs.canvas.getContext('2d');
                context.beginPath();
                context.arc(x, y, 1, 0, Math.PI * 2, true);
                // context.fill();
                context.stroke();
                // context.closePath();
            },

            line(x1, y1, x2, y2) {
                const context = this.$refs.canvas.getContext('2d');

                context.beginPath();
                context.moveTo(x1, y1);
                context.lineTo(x2, y2);
                context.stroke();
                context.closePath();
            },

            mouseDown(event) {
                this.down = true;

                this.lastX = event.offsetX;
                this.lastY = event.offsetY;

                this.dot(this.lastX, this.lastY);
            },

            mouseUp() {
                this.down = false;

                // const context = this.$refs.canvas.getContext('2d');
                // context.closePath();

                const context = this.$refs.canvas.getContext('2d');
                context.closePath();

            },

            mouseMove(event) {
                if (this.down) {
                    const context = this.$refs.canvas.getContext('2d');

                    context.lineTo(event.offsetX, event.offsetY);
                    context.stroke();

                    // this.line(this.lastX, this.lastY, event.offsetX, event.offsetY);

                    this.lastX = event.offsetX;
                    this.lastY = event.offsetY;
                }
            },

            mouseLeave() {
                this.down = false;

                const context = this.$refs.canvas.getContext('2d');
                context.closePath();
            },

            keyDown(event) {
                if (event.code === 'Enter') {
                    event.preventDefault();
                    event.stopPropagation();
                    this.$refs.canvas.toBlob((blob) => {
                        this.$emit('submit', blob);
                    });
                }

                if (event.code === 'Escape') {
                    this.$emit('cancel');
                }
            },

            setColor(color) {
                const context = this.$refs.canvas.getContext('2d');
                context.strokeStyle = color;
                context.fillStyle = color;
            }
        },

        mounted() {
            this.original = new Image();

            this.original.onload = () => {
                this.$refs.canvas.height = this.original.height;
                this.$refs.canvas.width = this.original.width;

                const context = this.$refs.canvas.getContext('2d');
                context.strokeStyle = "#000000";
                context.lineCap = 'round';
                context.lineJoin = 'round';
                context.lineWidth = 5;
                context.strokeStyle = "rgba(0,0,200,0.1)";
                context.fillStyle = "rgba(0,0,200,0.1)";
                context.drawImage(this.original, 0, 0);
            };

            this.original.src = this.image;

            document.addEventListener('keydown', this.keyDown);
        },

        destroyed() {
            document.removeEventListener('keydown', this.keyDown);
        },

    };

</script>
