<template>

    <div class="FileCatcher">
        <input class="FileCatcher__file" type="file" multiple @change="onFileChange" ref="file">
        <div v-if="visible" class="FileCatcher__overlay" :class="{ __visible: visible }">
            Бросьте файлы для отправки...
        </div>
    </div>

</template>

<style>

    .FileCatcher__file {
        visibility: hidden;
        display: block;
        width: 0;
        height: 0;
        overflow: hidden;
    }

    .FileCatcher__overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 100;

        background-color: rgba(255, 255, 255, 0.9);
        visibility: hidden;
        opacity: 0;
        transition: visibility 200ms, opacity 200ms;

        display: flex;
        align-items: center;
        justify-content: center;

        font-size: 32px;
        color: #777;
    }

    .FileCatcher__overlay.__visible {
        opacity: 1;
        visibility: visible;
    }

</style>

<script>

    export default {

        data() {
            return {
                visible: false,
                target: null,
            };
        },

        methods: {
            onDragEnter(event) {
                this.target = event.target;
                this.visible = true;
            },

            onDragLeave(event) {
                if (event.target === this.target) {
                    this.visible = false;
                }
            },

            onDragOver(event) {
                event.preventDefault();
            },

            onDrop(event) {
                event.preventDefault();

                this.visible = false;
                this.$emit('catch', Array.prototype.map.call(event.dataTransfer.files, file => file));
            },

            onPaste(event) {
                let clipboardData = event.clipboardData || window.clipboardData || event.originalEvent.clipboardData;
                if (clipboardData && clipboardData.files && clipboardData.files.length > 0) {
                    this.$emit('catch', Array.prototype.map.call(clipboardData.files, file => file));
                }
            },

            onFileChange(event) {
                event.preventDefault();

                this.$emit('catch', Array.prototype.map.call(event.target.files, file => file));
                if (this.$refs.file) {
                    this.$refs.file.value = '';
                }
            },

            selectFiles() {
                this.$refs.file.click();
            }
        },

        mounted() {
            document.addEventListener('paste', this.onPaste);

            window.addEventListener('dragenter', this.onDragEnter);
            window.addEventListener('dragleave', this.onDragLeave);
            window.addEventListener('dragover', this.onDragOver);
            window.addEventListener('drop', this.onDrop);
        },

        beforeDestroy() {
            document.removeEventListener('paste', this.onPaste);

            window.removeEventListener('dragenter', this.onDragEnter);
            window.removeEventListener('dragleave', this.onDragLeave);
            window.removeEventListener('dragover', this.onDragOver);
            window.removeEventListener('drop', this.onDrop);
        }

    };

</script>
