<template>

    <div class="ImageFile">

        <div class="ImageFile__preview-wrapper" @click="fullscreen = true" :style="style">
        </div>

        <portal v-if="fullscreen" to="presenter-stack">
            <div class="ImageFile__fullscreen-preview" @click="fullscreen = false">
                <img class="ImageFile__fillsize" :src="file.url"  width="auto" height="auto">
            </div>
        </portal>
    </div>

</template>

<style>

    .ImageFile {
        display: inline-block;
    }

    .ImageFile__preview-wrapper {
        width: 200px;
        height: 200px;

        position: relative;

        display: flex;
        align-items: center;
        justify-content: center;

        border-radius: 5px;
        box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1);

        background-size: cover;
        background-repeat: no-repeat;
        background-position: 50% 50%;
    }

    .ImageFile__fullscreen-preview {
        position: fixed;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 10000;

        display: flex;
        align-items: center;
        justify-content: center;

        background-color: rgba(0, 0, 0, 0.9);
    }

    .ImageFile__fillsize {
        max-width: 100vw;
        max-height: 100vh;
    }

</style>

<script>

    export default {

        props: {
            file: {
                type: Object,
                required: true,
            },
        },

        data() {
            return {
                fullscreen: false,
            };
        },

        computed: {
            width() {
                const matches = this.file.url.match(/\.([\d]+)x([\d]+)\./)
                if (matches) {
                    return parseInt(matches[1])
                } else {
                    return 400
                }
            },

            height() {
                const matches = this.file.url.match(/\.([\d]+)x([\d]+)\./)
                if (matches) {
                    return parseInt(matches[2])
                } else {
                    return 400
                }
            },

            style() {
                const retinaCoeff = 2

                const maxWidth = 300 * retinaCoeff
                const maxHeight = 300 * retinaCoeff

                const coeffWidth = this.width / maxWidth
                const coeffHeight = this.height / maxHeight

                const coeff = Math.max(coeffWidth, coeffHeight)

                if (coeff === 0) {
                    return {
                        backgroundImage: 'url(' + this.file.url + ')',
                        width: '200px',
                        height: '200px',
                    }
                }

                return {
                    backgroundImage: 'url(' + this.file.url + ')',
                    width: Math.round(this.width / coeff / retinaCoeff) + 'px',
                    height: Math.round(this.height / coeff / retinaCoeff) + 'px',
                }
            },
        },

        methods: {
            keydown(event) {
                if (event.key === 'Escape') {
                    event.preventDefault();
                    this.fullscreen = false;
                }
            },
        },

        mounted() {
            document.addEventListener('keydown', this.keydown);
        },

        beforeDestroy() {
            document.removeEventListener('keydown', this.keydown);
        },

        // 902x470



    };

</script>
